import React, { useState, useRef, useEffect, useCallback } from "react";
import styles from "./object_keyframe_vertical_scroll.module.css";

export default function ObjectKeyFrameVerticalScroll({
  parentref = null,
  keyframes=[],
  // exitframes=[],
  currentScroll = 0,
  onchange,
  min = 0,
  max = 100,
  onKeyFrameClicked,
  tag,
  tooltip_text,
  elementIndex, // Add this prop to identify which element's keyframes these are
  property, // Add this prop to identify which property (x, y, height, width, rotation, opacity)
  onDeleteKeyframe, // Add this prop for the delete callback
  onKeyframeSelectionChange, // Add this prop to notify parent of selection changes
  onDeleteExitframe, // Add this prop for deleting exitframe
  exit_scroll, // Add this prop to receive exitframes
}) {
  const containerRef = useRef();
  const progressBarRef = useRef();
  const knobRef = useRef();
  const [progress, setProgress] = useState(currentScroll);
  const [containerHeight, setContainerHeight] = useState(0);
  const [progressBarHeight, setProgressBarHeight] = useState(0);
  const [knobHeight, setKnobHeight] = useState(0);
  const [isDragging, setIsDragging] = useState(false);
  const [selectedKeyframe, setSelectedKeyframe] = useState(null); // Track selected keyframe
  const [editingKeyframe, setEditingKeyframe] = useState(null); // Track keyframe being edited
  const [editorValues, setEditorValues] = useState({ relative: '', absolute: '' });
  const dragOffset = useRef(0);
  
  // Sync internal progress with external currentScroll prop
  useEffect(() => {
    setProgress(currentScroll);
  }, [currentScroll]);

  // Notify parent when keyframe selection changes
  useEffect(() => {
    if (onKeyframeSelectionChange) {
      onKeyframeSelectionChange(selectedKeyframe !== null);
    }
    
    // Close editor when keyframe is deselected
    if (selectedKeyframe === null) {
      setEditingKeyframe(null);
      setEditorValues({ relative: '', absolute: '' });
    }
  }, [selectedKeyframe, onKeyframeSelectionChange]);

  // Measure container height and calculate knob height
  useEffect(() => {
    const updateSizes = () => {
      if (containerRef.current && progressBarRef.current) {
        const containerRect = containerRef.current.getBoundingClientRect();
        const progressBarRect = progressBarRef.current.getBoundingClientRect();
        
        setContainerHeight(containerRect.height);
        setProgressBarHeight(progressBarRect.height);
        // Calculate knob height as 0.5% of container height
        setKnobHeight(containerRect.height * 0.005);
      }
    };

    updateSizes();
    
    // Add resize observer to handle dynamic size changes
    const resizeObserver = new ResizeObserver(updateSizes);
    if (containerRef.current) {
      resizeObserver.observe(containerRef.current);
    }
    if (progressBarRef.current) {
      resizeObserver.observe(progressBarRef.current);
    }

    return () => resizeObserver.disconnect();
  }, []);

  // Handle keyframe/exitframe deletion with Delete key
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.key === 'Delete' && selectedKeyframe !== null) {
        event.preventDefault();
        event.stopPropagation();
        
        // Check if it's a keyframe
        const keyframeIndex = keyframes.findIndex(keyframe => keyframe === selectedKeyframe);
        if (keyframeIndex !== -1 && onDeleteKeyframe) {
          onDeleteKeyframe(elementIndex, property, keyframeIndex);
          setSelectedKeyframe(null);
          return;
        }
        
        // Check if it's an exitframe
        if (onDeleteExitframe) {
          onDeleteExitframe(elementIndex);
          setSelectedKeyframe(null);
        }
      }
    };
    document.addEventListener('keydown', handleKeyDown);
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [selectedKeyframe, keyframes, exit_scroll, elementIndex, property, onDeleteKeyframe, onDeleteExitframe]);

  // Handle clicks outside to deselect keyframes
  useEffect(() => {
    const handleClickOutside = (event) => {
      // Check if click is outside this component
      if (containerRef.current && !containerRef.current.contains(event.target)) {
        setSelectedKeyframe(null);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  // Compute knob position from progress - center the knob on the progress value
  const getKnobTop = useCallback(() => {
    if (progressBarHeight === 0 || knobHeight === 0) return 0;
    const range = max - min;
    const normalizedProgress = (progress - min) / range;
    // Center the knob on the progress value within the progress bar area
    const progressBarTop = (containerHeight - progressBarHeight) / 2; // Center vertically in container
    return progressBarTop + normalizedProgress * progressBarHeight - (knobHeight / 2);
  }, [progress, min, max, containerHeight, progressBarHeight, knobHeight]);

  // Compute keyframe position from value - center the keyframe on the keyframe value
  const getKeyframeTop = useCallback((keyframeValue) => {
    if (progressBarHeight === 0) return 0;
    const range = max - min;
    const normalizedValue = (keyframeValue - min) / range;
    // Position keyframe within the progress bar area
    const progressBarTop = (containerHeight - progressBarHeight) / 2; // Center vertically in container
    return progressBarTop + normalizedValue * progressBarHeight;
  }, [min, max, containerHeight, progressBarHeight]);

  // Check if scroll position is near a keyframe (within 10 units)
  const isKeyframeActive = useCallback((keyframeValue) => {
    return Math.abs(progress - keyframeValue) <= 10;
  }, [progress]);

  // Check if knob is on any keyframe
  const isKnobOnKeyframe = useCallback(() => {
    return keyframes.some(keyframe => Math.abs(progress - keyframe) <= 10) ||
          [exit_scroll].some(exitframe => Math.abs(progress - exitframe) <= 10);
  }, [progress, keyframes, exit_scroll]);

  // Update progress from knob position
  const updateProgressFromY = useCallback(
    (y) => {
      if (progressBarHeight === 0 || knobHeight === 0) return;
      
      const range = max - min;
      const progressBarTop = (containerHeight - progressBarHeight) / 2;
      
      // Convert y position to position within progress bar
      const relativeY = y - progressBarTop + (knobHeight / 2);
      const clampedY = Math.max(0, Math.min(progressBarHeight, relativeY));
      
      let newProgress;
      if (progressBarHeight === 0) {
        newProgress = min;
      } else {
        newProgress = min + (clampedY / progressBarHeight) * range;
      }
      
      // Apply stepping of 1 for mouse drag
      newProgress = Math.round(newProgress);
      newProgress = Math.max(min, Math.min(max, newProgress));
      
      setProgress(newProgress);
      if (onchange) onchange(newProgress);
    },
    [min, max, containerHeight, progressBarHeight, knobHeight, onchange]
  );

  // Handle keyframe click
  const handleKeyframeClick = useCallback((e, keyframeValue, keyframeIndex) => {
    e.stopPropagation(); // Prevent container click
    
    // Select the keyframe
    setSelectedKeyframe(keyframeValue);
    
    // Set up editing state
    setEditingKeyframe({ value: keyframeValue, index: keyframeIndex, type: 'keyframe' });
    setEditorValues({ relative: '', absolute: keyframeValue.toString() });
     
    if (onKeyFrameClicked) {
      onKeyFrameClicked(keyframeValue);
    }
  }, [onKeyFrameClicked]);
  // Handle exitframe click
  const handleExitframeClick = useCallback((e, exitframeValue) => {
    e.stopPropagation(); // Prevent container click
    
    // Select the exitframe (reuse keyframe selection state)
    setSelectedKeyframe(exitframeValue);
    
    // Set up editing state for exitframe
    setEditingKeyframe({ value: exitframeValue, index: null, type: 'exitframe' });
    setEditorValues({ relative: '', absolute: exitframeValue.toString() });
         
    if (onKeyFrameClicked) {
      onKeyFrameClicked(exitframeValue);
    }
  }, [onKeyFrameClicked]);
  // Handle container click (click-to-position) - disabled when hovering keyframes
  const handleContainerClick = useCallback((e) => {
    if (isDragging || knobHeight === 0) return; // Don't interfere with dragging
    
    // Check if click is on a keyframe element
    const clickedElement = e.target;
    if (clickedElement.classList.contains(styles.keyframe) || 
        clickedElement.closest(`.${styles.keyframe}`)) {
      return; // Don't handle container click if clicking on keyframe
    }
    
    // Check if click is on the keyframe editor
    if (clickedElement.closest(`.${styles.keyframe_editor}`)) {
      return; // Don't handle container click if clicking on editor
    }    
    // Deselect keyframe when clicking on container
    setSelectedKeyframe(null);
    
    const containerRect = containerRef.current.getBoundingClientRect();
    const clickY = e.clientY - containerRect.top;
    
    // Center the knob on the click position
    const targetY = clickY - knobHeight / 2;
    updateProgressFromY(targetY);
  }, [isDragging, knobHeight, updateProgressFromY]);

  // Mouse drag handlers
  const handleMouseDown = useCallback((e) => {
    setIsDragging(true);
    const knobRect = knobRef.current.getBoundingClientRect();
    dragOffset.current = e.clientY - knobRect.top;
    e.preventDefault();
    e.stopPropagation(); // Prevent container click
  }, []);

  const handleMouseMove = useCallback((e) => {
    if (!isDragging || !containerRef.current) return;
    
    const containerRect = containerRef.current.getBoundingClientRect();
    const targetY = e.clientY - containerRect.top - dragOffset.current;
    updateProgressFromY(targetY);
  }, [isDragging, updateProgressFromY]);

  const handleMouseUp = useCallback(() => {
    setIsDragging(false);
  }, []);

  // Wheel scroll handler
  const handleWheel = useCallback((e) => {
    e.preventDefault();
    const delta = e.deltaY > 0 ? 1 : -1;
    
    // Use step of 1 when Ctrl is held, step of 30 otherwise
    const step = e.ctrlKey ? 1 : 50;
    
    setProgress((prev) => {
      const newProgress = Math.max(min, Math.min(max, prev + delta * step));
      if (onchange) onchange(newProgress);
      return newProgress;
    });
  }, [min, max, onchange]);
  // Handle keyframe editor changes
  const handleEditorChange = useCallback((field, value) => {
    setEditorValues(prev => ({ ...prev, [field]: value }));
  }, []);

  // Handle relative adjustment (+50 or -50)
  const handleRelativeAdjustment = useCallback((delta) => {
    if (!editingKeyframe || !onDeleteKeyframe) return;
    
    const currentValue = editingKeyframe.value;
    const newValue = Math.max(min, Math.min(max, currentValue + delta));
    const actualDelta = newValue - currentValue;
    
    if (actualDelta === 0) return;
    
    // Update this keyframe and all subsequent ones
    updateKeyframeAndSubsequent(editingKeyframe, newValue, actualDelta);
  }, [editingKeyframe, min, max, onDeleteKeyframe]);

  // Handle absolute value change
  const handleAbsoluteChange = useCallback(() => {
    if (!editingKeyframe || !editorValues.absolute || !onDeleteKeyframe) return;
    
    const newValue = parseFloat(editorValues.absolute);
    if (isNaN(newValue)) return;
    
    const clampedValue = Math.max(min, Math.min(max, newValue));
    const delta = clampedValue - editingKeyframe.value;
    
    if (delta === 0) return;
    
    // Update this keyframe and all subsequent ones
    updateKeyframeAndSubsequent(editingKeyframe, clampedValue, delta);
  }, [editingKeyframe, editorValues.absolute, min, max, onDeleteKeyframe]);

  // Helper function to update keyframe and subsequent keyframes
  const updateKeyframeAndSubsequent = useCallback((keyframeInfo, newValue, delta) => {
    if (keyframeInfo.type === 'exitframe') {
      // For exitframes, just update the exit scroll
      if (onDeleteExitframe) {
        // We need a way to update exitframe value - for now, just update it
        // This would need to be handled by the parent component
        const event = new CustomEvent('updateExitframe', { 
          detail: { elementIndex, newValue: newValue } 
        });
        document.dispatchEvent(event);
      }
      return;
    }
    
    // Get all keyframes for this property type
    let allKeyframes = [];
    
    if (property === 'position') {
      const xScrollY = keyframes.filter(kf => kf !== undefined);
      allKeyframes = [...new Set(xScrollY)].sort((a, b) => a - b);
    } else {
      allKeyframes = [...keyframes].sort((a, b) => a - b);
    }
    
    const currentIndex = allKeyframes.indexOf(keyframeInfo.value);
    if (currentIndex === -1) return;
    
    // Update current keyframe
    setEditorValues(prev => ({ ...prev, absolute: newValue.toString() }));
    setEditingKeyframe(prev => ({ ...prev, value: newValue }));
    
    // Emit custom event for parent to handle the updates
    const event = new CustomEvent('updateKeyframeAndSubsequent', {
      detail: {
        elementIndex,
        property,
        currentKeyframe: keyframeInfo.value,
        newValue,
        delta,
        subsequentKeyframes: allKeyframes.slice(currentIndex + 1)
      }
    });
    document.dispatchEvent(event);
    
  }, [elementIndex, property, keyframes, onDeleteExitframe]);

  // Handle input key press
  const handleInputKeyPress = useCallback((e) => {
    if (e.key === 'Enter') {
      handleAbsoluteChange();
    }
  }, [handleAbsoluteChange]);
  // Attach global mouse listeners
  useEffect(() => {
    if (isDragging) {
      document.addEventListener("mousemove", handleMouseMove);
      document.addEventListener("mouseup", handleMouseUp);
      
      return () => {
        document.removeEventListener("mousemove", handleMouseMove);
        document.removeEventListener("mouseup", handleMouseUp);
      };
    }
  }, [isDragging, handleMouseMove, handleMouseUp]);

  // Attach wheel listener
  useEffect(() => {
    const container = containerRef.current;
    if (container) {
      container.addEventListener("wheel", handleWheel, { passive: false });
      
      return () => {
        container.removeEventListener("wheel", handleWheel);
      };
    }
  }, [handleWheel]);

  return (
    <div style={{height: '100%', width:'100%', display:'flex', flexDirection: 'column'}}>
      {/* Tag container at the top */}
      {tag && (
        <div className={styles.tag_container}>
          {tag}
          <div className={styles.tag_tooltip}>
            {tooltip_text}
          </div>
        </div>
      )}
      
      {/* Main scroll container */}
      <div style={{height: '100%', width:'100%', display:'flex'}}>
        <div
          ref={containerRef}
          className={styles.container}
          style={{ userSelect: "none" }}
          onClick={handleContainerClick}
        >
          <div ref={progressBarRef} className={styles.progress_bar} style={{ width: "30%" }} />
          
          {/* Render keyframes */}
          {keyframes.map((keyframe, index) => (
            <div
              key={`keyframe-${index}`}
              className={`${styles.keyframe} ${isKeyframeActive(keyframe) ? styles.active : ''} ${selectedKeyframe === keyframe ? styles.selected : ''}`}
              style={{ 
                top: `${getKeyframeTop(keyframe)}px`,
                backgroundColor: selectedKeyframe === keyframe ? '#ff6b35' : (isKeyframeActive(keyframe) ? '#0056b3' : '#007bff'),
                transform: selectedKeyframe === keyframe ? 'translateX(-50%) translateY(-50%) scale(1.3)' : 'translateX(-50%) translateY(-50%)',
                boxShadow: selectedKeyframe === keyframe ? '0 0 12px rgba(255, 107, 53, 0.8)' : undefined
              }}
              onClick={(e) => handleKeyframeClick(e, keyframe, index)}
            >
              <div className={styles.keyframe_tooltip}>
                {keyframe} {selectedKeyframe === keyframe ? '(Selected - Press Delete to remove)' : ''}
              </div>
              
              {/* Keyframe Editor */}
              {editingKeyframe && editingKeyframe.value === keyframe && editingKeyframe.type === 'keyframe' && (
                <div className={styles.keyframe_editor}>
                  <div className={styles.keyframe_editor_header}>Edit Keyframe</div>
                  
                  <div className={styles.keyframe_editor_row}>
                    <button 
                      className={styles.keyframe_editor_button}
                      onClick={() => handleRelativeAdjustment(-50)}
                    >
                      -50
                    </button>
                    <button 
                      className={styles.keyframe_editor_button}
                      onClick={() => handleRelativeAdjustment(50)}
                    >
                      +50
                    </button>
                  </div>
                  
                  <div className={styles.keyframe_editor_row}>
                    <span className={styles.keyframe_editor_label}>Rel:</span>
                    <input
                      type="number"
                      className={styles.keyframe_editor_input}
                      value={editorValues.relative}
                      onChange={(e) => handleEditorChange('relative', e.target.value)}
                      placeholder="+/-"
                    />
                  </div>
                  
                  <div className={styles.keyframe_editor_row}>
                    <span className={styles.keyframe_editor_label}>Abs:</span>
                    <input
                      type="number"
                      className={styles.keyframe_editor_input}
                      value={editorValues.absolute}
                      onChange={(e) => handleEditorChange('absolute', e.target.value)}
                      onKeyPress={handleInputKeyPress}
                    />
                  </div>
                </div>
              )}
            </div>
          ))}
          {/* Render exitframes */}
            {()=>console.log("Rendering exitframe:", exit_scroll)}
           {(exit_scroll&&exit_scroll>0?( <div
              key={`exitframe-exit`}
              className={`${styles.keyframe} ${isKeyframeActive(exit_scroll) ? styles.active : ''} ${selectedKeyframe === exit_scroll ? styles.selected : ''}`}
              style={{ 
                top: `${getKeyframeTop(exit_scroll)}px`,
                backgroundColor: selectedKeyframe === exit_scroll ? '#ff6b35' : (isKeyframeActive(exit_scroll) ? '#b30000' : '#ff0000'),
                transform: selectedKeyframe === exit_scroll ? 'translateX(-50%) translateY(-50%) scale(1.3)' : 'translateX(-50%) translateY(-50%)',
                boxShadow: selectedKeyframe === exit_scroll ? '0 0 12px rgba(255, 107, 53, 0.8)' : undefined
              }}
              onClick={(e) => handleExitframeClick(e, exit_scroll, null)}
            >
              <div className={styles.keyframe_tooltip}>
                Exit: {exit_scroll} {selectedKeyframe === exit_scroll ? '(Selected - Press Delete to remove)' : ''}
              </div>
{/* Exitframe Editor */}
              {editingKeyframe && editingKeyframe.value === exit_scroll && editingKeyframe.type === 'exitframe' && (
                <div className={styles.keyframe_editor}>
                  <div className={styles.keyframe_editor_header}>Edit Exit Frame</div>
                  
                  <div className={styles.keyframe_editor_row}>
                    <button 
                      className={styles.keyframe_editor_button}
                      onClick={() => handleRelativeAdjustment(-50)}
                    >
                      -50
                    </button>
                    <button 
                      className={styles.keyframe_editor_button}
                      onClick={() => handleRelativeAdjustment(50)}
                    >
                      +50
                    </button>
                  </div>
                  
                  <div className={styles.keyframe_editor_row}>
                    <span className={styles.keyframe_editor_label}>Abs:</span>
                    <input
                      type="number"
                      className={styles.keyframe_editor_input}
                      value={editorValues.absolute}
                      onChange={(e) => handleEditorChange('absolute', e.target.value)}
                      onKeyPress={handleInputKeyPress}
                    />
                  </div>
                </div>
              )}
            </div>):null)}

          <div
            ref={knobRef}
            className={`${styles.knob} ${isKnobOnKeyframe() ? styles.on_keyframe : ''}`}
            style={{ top: `${getKnobTop()}px` }}
            onMouseDown={handleMouseDown}
          />
        </div>
      </div>
    </div>
  );
}