import React from 'react';

function Text({ text, style = {} }) {
  // Helper function to parse numeric values from strings
  const parseNumeric = (value, defaultValue = 0) => {
    if (typeof value === 'number') return value;
    if (typeof value === 'string') {
      const parsed = parseFloat(value.replace(/px|em|rem|%/, ''));
      return isNaN(parsed) ? defaultValue : parsed;
    }
    return defaultValue;
  };

  // Helper function to convert canvas textAlign to CSS
  const getTextAlign = (canvasAlign) => {
    switch (canvasAlign) {
      case 'start':
      case 'left':
        return 'left';
      case 'center':
        return 'center';
      case 'end':
      case 'right':
        return 'right';
      default:
        return 'left';
    }
  };

  // Helper function to convert canvas textBaseline to CSS vertical alignment
  const getVerticalAlign = (baseline) => {
    switch (baseline) {
      case 'top':
      case 'hanging':
        return 'flex-start';
      case 'middle':
        return 'center';
      case 'bottom':
      case 'alphabetic':
      case 'ideographic':
        return 'flex-end';
      default:
        return 'flex-start';
    }
  };

  // Helper function to handle text wrapping similar to canvas
  const getWordWrap = (whiteSpace, wordWrap) => {
    if (whiteSpace === 'nowrap') return 'nowrap';
    if (wordWrap === 'break-word') return 'break-word';
    return 'normal';
  };

  // Helper function to create CSS filter for canvas-like shadow
  const getTextShadow = (style) => {
    if (!style.shadow) return 'none';
    
    const shadowColor = style.shadowColor || 'rgba(0, 0, 0, 0.5)';
    const shadowBlur = parseNumeric(style.shadowBlur, 4);
    const shadowOffsetX = parseNumeric(style.shadowOffsetX, 2);
    const shadowOffsetY = parseNumeric(style.shadowOffsetY, 2);
    
    return `${shadowOffsetX}px ${shadowOffsetY}px ${shadowBlur}px ${shadowColor}`;
  };

  // Helper function to create CSS stroke effect
  const getTextStroke = (style) => {
    const strokeWidth = parseNumeric(style.strokeWidth, 0);
    if (strokeWidth <= 0) return {};
    
    const strokeColor = style.strokeColor || '#000000';
    return {
      WebkitTextStroke: `${strokeWidth}px ${strokeColor}`,
      textStroke: `${strokeWidth}px ${strokeColor}`,
    };
  };

  // Helper function to create gradient text effect
  const getGradientStyle = (style) => {
    if (!style.gradientEnabled) return {};
    
    const color1 = style.color || '#000000';
    const color2 = style.gradientColor2 || '#ff0000';
    const direction = parseNumeric(style.gradientDirection, 0);
    
    return {
      background: `linear-gradient(${direction}deg, ${color1}, ${color2})`,
      WebkitBackgroundClip: 'text',
      WebkitTextFillColor: 'transparent',
      backgroundClip: 'text',
      color: 'transparent',
    };
  };

  // Helper function to handle line height like canvas
  const getLineHeight = (lineHeight, fontSize) => {
    if (typeof lineHeight === 'number') {
      if (lineHeight <= 2) {
        // Relative line height (multiplier)
        return lineHeight;
      } else {
        // Absolute line height in pixels
        return `${lineHeight}px`;
      }
    }
    return lineHeight || 'normal';
  };

  // Helper function to handle text overflow with ellipsis
  const getTextOverflow = (style) => {
    if (style.ellipsis && style.maxLines === 1) {
      return {
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap',
      };
    } else if (style.maxLines && style.maxLines > 1) {
      return {
        display: '-webkit-box',
        WebkitLineClamp: style.maxLines,
        WebkitBoxOrient: 'vertical',
        overflow: 'hidden',
        textOverflow: style.ellipsis ? 'ellipsis' : 'clip',
      };
    }
    return {};
  };

  // Build the complete style object
  const canvasTextStyle = {
    // Font properties (exact canvas mapping)
    fontFamily: style.fontFamily || 'Arial, sans-serif',
    fontSize: `${parseNumeric(style.fontSize, 24)}px`,
    fontWeight: style.fontWeight || 'normal',
    fontStyle: style.fontStyle || 'normal',
    
    // Text styling
    color: style.gradientEnabled ? 'transparent' : (style.color || '#000000'),
    backgroundColor: style.backgroundColor !== 'transparent' ? style.backgroundColor : 'transparent',
    textDecoration: style.textDecoration || 'none',
    textTransform: style.textTransform || 'none',
    
    // Text layout and alignment
    textAlign: getTextAlign(style.textAlign || 'left'),
    lineHeight: getLineHeight(style.lineHeight, parseNumeric(style.fontSize, 24)),
    letterSpacing: typeof style.letterSpacing === 'number' ? `${style.letterSpacing}px` : (style.letterSpacing || 'normal'),
    wordSpacing: typeof style.wordSpacing === 'number' ? `${style.wordSpacing}px` : (style.wordSpacing || 'normal'),
    
    // Text wrapping (canvas-like behavior)
    whiteSpace: style.whiteSpace || 'normal',
    wordWrap: getWordWrap(style.whiteSpace, style.wordWrap),
    overflowWrap: style.wordWrap === 'break-word' ? 'break-word' : 'normal',
    
    // Canvas-specific effects
    textShadow: getTextShadow(style),
    ...getTextStroke(style),
    ...getGradientStyle(style),
    ...getTextOverflow(style),
    
    // Box model
    padding: `${parseNumeric(style.padding, 10)}px`,
    border: style.borderWidth > 0 ? `${parseNumeric(style.borderWidth)}px solid ${style.borderColor || '#000000'}` : 'none',
    borderRadius: `${parseNumeric(style.borderRadius, 0)}px`,
    
    // Layout properties
    width: '100%',
    height: '100%',
    position: 'static',
    boxSizing: 'border-box',
    
    // Flex properties for vertical alignment (canvas textBaseline equivalent)
    display: 'flex',
    flexDirection: 'column',
    justifyContent: getVerticalAlign(style.textBaseline || 'top'),
    alignItems: getTextAlign(style.textAlign || 'left') === 'center' ? 'center' : 
               getTextAlign(style.textAlign || 'left') === 'right' ? 'flex-end' : 'flex-start',
  };

  // Handle text content with proper line breaks
  const renderText = () => {
    const textContent = text || '';
    
    // Handle maxLines limitation
    const lines = textContent.split('\n');
    const maxLines = style.maxLines;
    const limitedLines = maxLines && maxLines > 0 ? lines.slice(0, maxLines) : lines;
    
    // Add ellipsis to last line if needed
    if (maxLines && lines.length > maxLines && style.ellipsis) {
      const lastIndex = limitedLines.length - 1;
      if (lastIndex >= 0) {
        limitedLines[lastIndex] = limitedLines[lastIndex] + '...';
      }
    }
    
    return limitedLines.map((line, index) => (
      <div key={index} style={{ margin: 0, padding: 0 }}>
        {line || '\u00A0'} {/* Non-breaking space for empty lines */}
      </div>
    ));
  };

  return (
    <div style={canvasTextStyle}>
      {renderText()}
    </div>
  );
}

export default Text;