import { useState, useEffect } from 'react'
import './App.css'
import HostedPage from './pages/hosted_page/hosted_page.jsx'
import store, { hostedPageActions } from './store/hosted_page.js'
import { useDispatch, Provider } from 'react-redux'
import sample_data from './store/sample_state.js'
import './animations/global_animation.css'; // Import global animations if needed
import { createBrowserRouter, RouterProvider } from 'react-router-dom';
import RouterMain from './routers/router_main.jsx'; // Import the main router component
import AnimationDashboard from './pages/animation_dashboard/animation_dashboard.jsx'; // Import the animation dashboard
import SandBox from './pages/sandbox/hosted_page.jsx'; // Import the scrollable parent component
import EditPage from './pages/edit_page/edit_page.jsx'
import CanvasHostedPage from './pages/canvasHostedPage/canvas_hosted_page.jsx'
// Define routes using createBrowserRouter
const router = createBrowserRouter([
  {
    path: "/",
    element: <RouterMain />,
    children: [
      {
        path: "/hostedpage",
        element: <HostedPage />
      },
      {
        path: "/ad",
        element: <AnimationDashboard />
      },
      {
        path: "/sandbox",
        element: <SandBox />
      },
      {
        path: "/edit",
        element: <EditPage />
      },
       {
        path: "/canvas",
        element: <CanvasHostedPage />
      },
    ],
  },
]);

function App() {
  const dispatch = useDispatch();
  
  // useEffect(() => {
  //   // // console.log('print dispatching ', sample_data)
  //   dispatch(hostedPageActions.setReducer(sample_data))
  // }, [dispatch])
  
  return (
    <>
      <RouterProvider router={router} />
    </>
  )
}

export default App;
