const hostedPageSlice = createSlice({
  name: "hosted_page",
  initialState,
  reducers: {
    setReducer(state, action) {
      return { ...action.payload };
    },

    addElement(state, action) {
      // console.log("Adding element:", action.payload);
      if (!state.page_content) {
        state.page_content = [];
      }
      state.page_content.push(action.payload);
    },

    moveUpElementLayer(state, action) {
      // console.log("moveUpElementLayer element:", action.payload);
      if (!state.page_content) {
        state.page_content = [];
      }
      state.page_content[action.payload.index].layer += action.payload.level;
    },

    moveDownElementLayer(state, action) {
      // console.log("moveDownElementLayer element:", action.payload);
      if (!state.page_content) {
        state.page_content = [];
      }
      state.page_content[action.payload.index].layer -= action.payload.level;
    },

    deleteElement(state, action) {
      // console.log("Deleting element at index:", action.payload);
      if (!state.page_content) {
        state.page_content = [];
      }
      state.page_content.splice(action.payload.index, 1);
    },

    deleteKeyframe(state, action) {
      const { index, property, keyframeIndex } = action.payload;
      // console.log("Deleting keyframe:", action.payload);
      if (
        // (state.page_content[index].animation.keyframes[property].value.length <= 2)||
        keyframeIndex === 0 ||
        (keyframeIndex ===
          state.page_content[index].animation.keyframes[property].value.length -
            1 &&
          property === "y")
      ) {
        // console.log(
        //   "Cannot delete keyframe: At least two keyframes required.",
        //   action.payload,
        //   JSON.parse(
        //     JSON.stringify(
        //       state.page_content[index].animation.keyframes[property]
        //     )
        //   )
        // );
        return; // Exit if there are only two keyframes left
      }
      if (!state.page_content) {
        state.page_content = [];
      }

      if (
        state.page_content[index] &&
        state.page_content[index].animation &&
        state.page_content[index].animation.keyframes &&
        state.page_content[index].animation.keyframes[property]
      ) {
        state.page_content[index].animation.keyframes[property].value.splice(
          keyframeIndex,
          1
        );
        state.page_content[index].animation.keyframes[property].scrollY.splice(
          keyframeIndex,
          1
        );
      }
      if (property === "y") {
        var element = state.page_content[index];
        const yKeyframes = element.animation.keyframes.y;
        const heightKeyframes = element.animation.keyframes.height;

        if (
          yKeyframes?.value?.length >= 2 &&
          heightKeyframes?.value?.length >= 1
        ) {
          const yValues = yKeyframes.value;

          // Update the last Y keyframe
          const lastIndex = yValues.length - 1;
          if (lastIndex >= 0) {
            yKeyframes.scrollY[lastIndex] = state.page_scroll_height;

            if (lastIndex >= 1) {
              const scrollDiff =
                yKeyframes.scrollY[lastIndex] -
                yKeyframes.scrollY[lastIndex - 1];
              yKeyframes.value[lastIndex] =
                yKeyframes.value[lastIndex - 1] + scrollDiff;
            }

            console.log(
              "keyframeY.value calculation = ",
              yKeyframes.value[lastIndex],
              "\n\nkeyframeY.scrollY[-1] = ",
              yKeyframes.scrollY[lastIndex]
            );
          }
        }
      }
    },

    updateElementAnimation(state, action) {
      console.log("Updating element animation:", action.payload);

      const { index, values, currentScrollY } = action.payload;
      const element = state.page_content[index];

      // Process each value type and update keyframes
      Object.entries(values).forEach(([valueType, value]) => {
        // Initialize keyframe structure if it doesn't exist
        if (!element.animation.keyframes[valueType]) {
          element.animation.keyframes[valueType] = {
            scrollY: [],
            value: [],
          };
        }

        const keyframe = element.animation.keyframes[valueType];
        const existingIndex = keyframe.scrollY.indexOf(currentScrollY);
        if (existingIndex !== -1) {
          // Update existing keyframe
          if (keyframe.value[existingIndex] === value) {
            console.log(" No change needed"); // No change needed
          } else if (keyframe.value.length === 2) {
            keyframe.value = [value, value];
          } else {
            keyframe.value[existingIndex] = value;
          }
        } else {
          // Find insertion point for new keyframe
          const insertIndex = keyframe.scrollY.findIndex(
            (scrollY) => scrollY > currentScrollY
          );

          if (insertIndex === -1) {
            // Append to end
            if (keyframe.value[keyframe.value.length - 1] == value) {
              console.log(" No change needed"); // No change needed
            } else {
              keyframe.scrollY.push(currentScrollY);
              keyframe.value.push(value);
            }
          } else {
            // Insert at specific position
            if (keyframe.value[insertIndex - 1] == value) {
              console.log(" No change needed"); // No change needed
            } else {
              keyframe.scrollY.splice(insertIndex, 0, currentScrollY);
              keyframe.value.splice(insertIndex, 0, value);
            }
          }
        }
      });

      // console.log(
      //   "state.page_content[index].animation.keyframes",
      //   JSON.stringify(state.page_content[index].animation.keyframes, null, 2)
      // );

      // Calculate max height based on 'y' and 'height' keyframes
      const yKeyframes = element.animation.keyframes.y;
      const heightKeyframes = element.animation.keyframes.height;

      if (
        yKeyframes?.value?.length >= 2 &&
        heightKeyframes?.value?.length >= 1
      ) {
        const yValues = yKeyframes.value;
        const heightValues = heightKeyframes.value;

        const maxHeightElement =
          yValues[yValues.length - 2] + heightValues[heightValues.length - 1];
        const maxHeight = Math.max(maxHeightElement, state.page_scroll_height);

        console.log("max_height", maxHeight, state.page_scroll_height);

        // Update the last Y keyframe
        const lastIndex = yValues.length - 1;
        if (lastIndex >= 0) {
          yKeyframes.scrollY[lastIndex] = maxHeight;

          if (lastIndex >= 1) {
            const scrollDiff =
              yKeyframes.scrollY[lastIndex] - yKeyframes.scrollY[lastIndex - 1];
            yKeyframes.value[lastIndex] =
              yKeyframes.value[lastIndex - 1] + scrollDiff;
          }

          console.log(
            "keyframeY.value calculation = ",
            yKeyframes.value[lastIndex],
            "\n\nkeyframeY.scrollY[-1] = ",
            yKeyframes.scrollY[lastIndex]
          );
        }

        // console.log(
        //   "state.page_content[index].animation.keyframes",
        //   JSON.stringify(state.page_content[index].animation.keyframes, null, 2)
        // );

        state.page_scroll_height = maxHeight;
      }
    },

    toggelLockElement(state, action) {
      const { index } = action.payload; // lock is boolean
      if (state.page_content[index]) {
        state.page_content[index].islocked =
          !state.page_content[index].islocked;
      } else {
        console.log("Element not found at index:", index);
      }
    },
    addAnimationProperty(state, action) {
      console.log("Adding animation property:", action.payload);
      const { index, property, typeStr } = action.payload;
      console.log(
        "index =",
        index,
        "property =",
        property,
        "typeStr =",
        typeStr
      );
      if (!state.page_content) {
        state.page_content = [];
      }

      state.page_content[index].animation["type"] = action.payload.type;
      state.page_content[index].animation["properties"] =
        action.payload.properties;

      console.log(JSON.parse(JSON.stringify(state.page_content[index])));
    },
    toggleElementAnimation(state, action) {
      const { index, type } = action.payload; // lock is boolean
      if (state.page_content[index].animation.properties) {
        state.page_content[index].animation.type = type;
      } else {
        console.log("Element animation properties not found at index:", index);
      }
    },
    updateExitScroll(state, action) {
      console.log("Adding exit scroll:", action.payload);
      const { index, exit_scroll } = action.payload;
      state.page_content[index].exit_scroll = exit_scroll;
    },
    updateKeyframeScrollY(state, action) {
      const { index, property, keyframeIndex, newScrollY } = action.payload;
      // console.log(action,"index, property, keyframeIndex, newScrollY",index, property, keyframeIndex, newScrollY)
      // return
      // Access the scrollY array safely
      const keyframes = state.page_content[index].animation.keyframes[property];
      const keys = keyframes.scrollY;

      // Calculate the difference (delta)
      const diff = keys[keyframeIndex] - newScrollY;

      // Update the selected keyframe and all subsequent keyframes
      for (let i = keyframeIndex; i < keys.length; i++) {
        keys[i] = keys[i] - diff; // Subtract to move all subsequent values by delta
      }

      // Update the targeted keyframe with the new value
      keys[keyframeIndex] = newScrollY;
    },
  },
});

// const store = createStore(hostedPageReducer);
const store = configureStore({
  reducer: { hosted_page: hostedPageSlice.reducer },
});
export const hostedPageActions = hostedPageSlice.actions;
export default store;
