// preset_animations.js
export class PresetAnimationManager {
  constructor(metadata) {
    this.metadata = metadata;
    this.animationStartTime = Date.now();
    this.originalProps = null;
    this.enabled = false;
    this.type = null;
    this.properties = {};

    this.initialize();
  }

  /**
   * Initialize preset animation system
   */
  initialize() {
    // Check if this element has preset animation
    const animation = this.metadata.animation;
    if (animation && animation.type === "preset") {
      this.enabled = true;
      this.properties = animation.properties || {};

      // Determine animation type based on properties
      this.type=this.properties.type
    }
  }

  /**
   * Check if preset animation affects a specific property
   */
  affectsProperty(property) {
    if (!this.enabled) return false;

    const affectedProps = this.getAffectedProperties();
    return affectedProps.includes(property);
  }

  /**
   * Get properties affected by current preset animation
   */
  getAffectedProperties() {
    switch (this.type) {
      case "infiniteRotation":
        return ["rotation"];
      case "pulse":
        return ["width", "height", "opacity"];
      case "bounce":
        return ["y"];
      case "shake":
        return ["x"];
      case "float":
        return ["y"];
      default:
        return [];
    }
  }

  /**
   * Apply preset animation to current properties
   * @param {Object} currentProps - Current element properties
   * @param {Object} textProps - Text properties (for text elements)
   * @returns {Object} Modified properties
   */
  apply(currentProps, textProps = null) {
    if (!this.enabled) return currentProps;

    const currentTime = Date.now();
    const elapsed = (currentTime - this.animationStartTime) / 1000; // Convert to seconds

    // Create a copy to avoid mutating the original
    const modifiedProps = { ...currentProps };

    switch (this.type) {
      case "infiniteRotation":
        const rotationSpeed = this.properties.speed || 1; // rotations per second
        modifiedProps.rotation =
          (elapsed * rotationSpeed * 2 * Math.PI) % (2 * Math.PI);
        break;

      case "pulse":
        const pulseSpeed = this.properties.speed || 1;
        const pulseIntensity = this.properties.intensity || 0.2;
        const pulseValue =
          Math.sin(elapsed * pulseSpeed * 2 * Math.PI) * pulseIntensity + 1;

        // Store original values for restoration
        if (!this.originalProps) {
          this.originalProps = {
            width: currentProps.width,
            height: currentProps.height,
            opacity: currentProps.opacity,
          };
        }

        modifiedProps.width = this.originalProps.width * pulseValue;
        modifiedProps.height = this.originalProps.height * pulseValue;
        modifiedProps.opacity = Math.min(
          1,
          this.originalProps.opacity * pulseValue
        );
        break;

      case "bounce":
        const bounceHeight = this.properties.height || 20;
        const bounceSpeed = this.properties.speed || 1;
        const bounceOffset =
          Math.abs(Math.sin(elapsed * bounceSpeed * Math.PI)) * bounceHeight;
        modifiedProps.y = currentProps.y - bounceOffset;
        break;

      case "shake":
        const shakeIntensity = this.properties.intensity || 5;
        const shakeSpeed = this.properties.speed || 10;
        const shakeOffset =
          Math.sin(elapsed * shakeSpeed * 2 * Math.PI) * shakeIntensity;
        modifiedProps.x = currentProps.x + shakeOffset;
        break;

      case "float":
        const floatHeight = this.properties.height || 10;
        const floatSpeed = this.properties.speed || 0.5;
        const floatOffset =
          Math.sin(elapsed * floatSpeed * 2 * Math.PI) * floatHeight;
        modifiedProps.y = currentProps.y + floatOffset;
        break;

    }

    return modifiedProps;
  }

  /**
   * Reset animation start time (useful for restarting animations)
   */
  reset() {
    this.animationStartTime = Date.now();
    this.originalProps = null;
  }

  /**
   * Get animation info for debugging
   */
  getInfo() {
    return {
      enabled: this.enabled,
      type: this.type,
      properties: this.properties,
      affectedProperties: this.getAffectedProperties(),
    };
  }
}

/**
 * Static utility functions for preset animations
 */
export const PresetAnimationUtils = {
  /**
   * Create animation manager instance
   */
  createManager(metadata) {
    return new PresetAnimationManager(metadata);
  },

  /**
   * Check if metadata has preset animation
   */
  hasPresetAnimation(metadata) {
    return metadata?.animation?.type === "preset";
  },

  /**
   * Get supported animation types
   */
  getSupportedTypes() {
    return [
      "infiniteRotation",
      "pulse",
      "bounce",
      "shake",
      "float",
    ];
  },

  /**
   * Validate preset animation properties
   */
  validateProperties(properties, type) {
    const validations = {
      infiniteRotation: ["speed"],
      pulse: ["speed", "intensity"],
      bounce: ["height", "speed"],
      shake: ["intensity", "speed"],
      float: ["height", "speed"],
    };

    const validProps = validations[type] || [];
    const errors = [];

    Object.keys(properties).forEach((prop) => {
      if (
        !validProps.includes(prop) &&
        ![
          "infiniteRotation",
          "pulse",
          "bounce",
          "shake",
          "float",
        ].includes(prop)
      ) {
        errors.push(`Invalid property: ${prop} for animation type: ${type}`);
      }
    });

    return errors;
  },
};
