import React, { useState } from "react";
import {
  Search,
  Image as Images,
  Square,
  Type,
  Shapes,
  Circle,
  Triangle,
  Layout,
  Layers,
  Grid,
  Calendar,
  BarChart,
  Video,
  Music,
  FileText,
  Star,
  Heart,
  Users,
  Settings,
} from "lucide-react";
import styles from "./left_options_bar.module.css";
import ElementEditorSidebar from "../../components/element_property/element_property.jsx";
import store, { hostedPageActions } from "../../store/hosted_page.js";
import { useDispatch, Provider } from "react-redux";
import { useCallback } from "react";
import elementData from "./elementData.js";
export default function PageLayout({
  tab_width,
  updateElementAnimation,
  currentSelectedElement,
  setCurrentSelectedElement,
  // updateElementObjectProperties,
  scaledScrollY,
}) {
  const dispatch = useDispatch();
  const [activeTab, setActiveTab] = useState("elements");
  const [searchQuery, setSearchQuery] = useState("");
  const baseUrl = process.env.REACT_APP_ASSET_BASE_URL;
  const [size, setSize] = useState({ width: 0, height: 0 });
  const designElement = elementData;
  const appendImageElement = (image_url, scrollY) => {
    // console.log("Adding image element:", image_url, scrollY);

    // Prepend base URL if needed
    const fullUrl = image_url.startsWith("http")
      ? image_url
      : `${baseUrl}${image_url}`;

    const img = new Image();
    img.src = fullUrl;

    img.onload = () => {
      const width = img.naturalWidth;
      const height = img.naturalHeight;
      const max_scroll = 10000;
      const default_object_property = {
        exit_scroll: null, // Scroll position to exit the page
        object_type: "image",
        layer: 0,
        islocked: false,
        object_property: {
          file: image_url,
        },
        animation: {
          type: "scroll",
          properties: {},
          keyframes: {
            x: { value: [0, 0], scrollY: [scrollY, scrollY ] },
            y: {
              value: [scrollY, scrollY + max_scroll],
              scrollY: [scrollY, scrollY + max_scroll],
            },
            height: {
              value: [height, height],
              scrollY: [scrollY, scrollY ],
            },
            width: {
              value: [width, width],
              scrollY: [scrollY, scrollY ],
            },
            rotation: {
              value: [0, 0],
              scrollY: [scrollY, scrollY ],
            },
            opacity: {
              value: [1, 1],
              scrollY: [scrollY, scrollY ],
            },
          },
        },
      };

      //  Dispatch only after image is fully loaded
      dispatch(hostedPageActions.addElement(default_object_property));
    };

    img.onerror = (err) => {
      console.error("Image failed to load:", fullUrl, err);
    };
  };// appendImageElement

  const appendTextElement = (scrollY) => {
    // console.log("Adding image element:", image_url, scrollY);
      const max_scroll = 10000;
      const default_object_property = {
        exit_scroll: null, // Scroll position to exit the page
        object_type: "text",
        layer: 0,
        islocked: false,
        object_property: {
          text: `Your Text Here.`,
          style: {
            fontFamily: '"Ranchers", sans-serif;', // Elegant script fonts
            fontSize: "40px", // Larger for a formal look
            fontWeight: "400", // Light and elegant
            fontStyle: "italic", // Gives a soft, romantic touch
            textDecoration: "none",
            textTransform: "capitalize", // Capitalizes each word nicely
            color: "#6b4c3b", // Soft brown, elegant tone
            lineHeight: "1.6", // Good vertical spacing
            letterSpacing: "0.5px", // Slight spacing for readability
            wordSpacing: "2px", // Adds grace
            textAlign: "center", // Typically centered for cards
            textShadow: "1px 1px 2px #ccc", // Subtle shadow for depth
            verticalAlign: "middle", // Center alignment within elements
            whiteSpace: "normal",
            wordBreak: "break-word", // Ensures no overflow
            overflowWrap: "break-word",
            textOverflow: "ellipsis", // Clean cut if overflowed
            overflow: "hidden", // Hide overflowing content
            display: "block", // Block for centered text
            backgroundClip: "padding-box",
            backgroundColor: "transparent", // Assume it's placed over a styled card background
          },
        },
        animation: {
          type: "scroll",
          properties: {},
          keyframes: {
            x: { value: [0, 0], scrollY: [scrollY, scrollY ] },
            y: {
              value: [scrollY, scrollY + max_scroll],
              scrollY: [scrollY, scrollY + max_scroll],
            },
            height: {
              value: [200, 200],
              scrollY: [scrollY, scrollY ],
            },
            width: {
              value: [500, 500],
              scrollY: [scrollY, scrollY ],
            },
            rotation: {
              value: [0, 0],
              scrollY: [scrollY, scrollY ],
            },
            opacity: {
              value: [1, 1],
              scrollY: [scrollY, scrollY ],
            },
          },
        },
      };

      //  Dispatch only after image is fully loaded
      dispatch(hostedPageActions.addElement(default_object_property));
  };// appendTextElement
 
  const appendShapeElement = ( scrollY) => {
     const max_scroll = 10000;
      const default_object_property = {
        exit_scroll: null, // Scroll position to exit the page
        object_type: "shape",
        layer: 0,
        islocked: false,
        object_property: {
          shape: "circle",
          color: "#050505ff",
          gradient_enabled: false,
          color2: "#ef4444",
          direction: "45deg",
          shadow: true,
          shadowColor: "#f4ede2ff",
          shadowBlur: 0,
          shadowOffsetX: 0,
          shadowOffsetY: 0,
        },
        animation: {
          type: "scroll",
          properties: {},
          keyframes: {
            x: { value: [0, 0], scrollY: [scrollY, scrollY ] },
            y: {
              value: [scrollY, scrollY + max_scroll],
              scrollY: [scrollY, scrollY + max_scroll],
            },
            height: {
              value: [300, 300],
              scrollY: [scrollY, scrollY ],
            },
            width: {
              value: [300, 300],
              scrollY: [scrollY, scrollY ],
            },
            rotation: {
              value: [0, 0],
              scrollY: [scrollY, scrollY ],
            },
            opacity: {
              value: [1, 1],
              scrollY: [scrollY, scrollY ],
            },
          },
        },
      };
      //  Dispatch only after image is fully loaded
      dispatch(hostedPageActions.addElement(default_object_property));
  };
  
  // Dummy data for components
  const componentsShapes = [
    {
      name: "Text Block",
      description: "Formatted text content",
      icon: <Type size={20} />,
      category: "Content",
      onClick: () => appendTextElement(scaledScrollY)
    },
    {
      name: "Shapes",
      description: "Shaps",
      icon: <Shapes size={20} />,
      category: "Data",
      onClick: () => appendShapeElement( scaledScrollY)
    },
    {
      name: "Video Player",
      description: "Custom video player controls",
      icon: <Video size={20} />,
      category: "Media",
    },
    {
      name: "Button Component",
      description: "Interactive button with hover effects",
      icon: <Square size={20} />,
      category: "Interactive",
    },
    {
      name: "Card Layout",
      description: "Responsive card container",
      icon: <Layout size={20} />,
      category: "Layout",
    },
    {
      name: "Image Gallery",
      description: "Responsive image grid",
      icon: <Grid size={20} />,
      category: "Media",
    },
    {
      name: "Navigation Bar",
      description: "Header navigation component",
      icon: <Layers size={20} />,
      category: "Navigation",
    },
    {
      name: "Form Builder",
      description: "Dynamic form generator",
      icon: <FileText size={20} />,
      category: "Interactive",
    },
  ];

  // Filter function for search
  const filterItems = (items, query) => {
    if (!query) return items;
    return items.filter(
      (item) =>
        item.name.toLowerCase().includes(query.toLowerCase()) ||
        (item.description &&
          item.description.toLowerCase().includes(query.toLowerCase())) ||
        (item.type && item.type.toLowerCase().includes(query.toLowerCase()))
    );
  };

  const renderElementsTab = () => {
    const filteredElements = filterItems(designElement, searchQuery);

    return (
      <div>
        <h3 className={styles.sectionTitle}>
          <Images size={16} style={{ marginRight: "8px", display: "inline" }} />
          Design Elements
        </h3>

        {filteredElements.length > 0 ? (
          <div className={styles.elementsGrid}>
            {filteredElements.map((element, index) => (
              <div
                key={index}
                className={styles.elementCard}
                onClick={() => {
                  appendImageElement(element.url, scaledScrollY);
                }}
              >
                <img
                  src={`${baseUrl}${element.url}`}
                  alt={element.name}
                  className={styles.elementImage}
                />
                <div className={styles.elementInfo}>
                  <h4 className={styles.elementName}>{element.name}</h4>
                  <p className={styles.elementType}>{element.type}</p>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className={styles.emptyState}>
            <Search className={styles.emptyStateIcon} />
            <p className={styles.emptyStateText}>
              No elements found matching "{searchQuery}"
            </p>
          </div>
        )}
      </div>
    );
  };

  const renderComponentsTab = () => {
    const filteredComponents = filterItems(componentsShapes, searchQuery);

    return (
      <div>
        <h3 className={styles.sectionTitle}>
          <Layers size={16} style={{ marginRight: "8px", display: "inline" }} />
          UI Components
        </h3>

        {filteredComponents.length > 0 ? (
          <div className={styles.componentList}>
            {filteredComponents.map((component) => (
              <div 
              key={component.id} 
              className={styles.componentItem}
              onClick={component.onClick}
              >
                <div className={styles.componentIcon}>{component.icon}</div>
                <div className={styles.componentDetails}>
                  <h4 className={styles.componentName}>{component.name}</h4>
                  <p className={styles.componentDescription}>
                    {component.description}
                  </p>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className={styles.emptyState}>
            <Search className={styles.emptyStateIcon} />
            <p className={styles.emptyStateText}>
              No components found matching "{searchQuery}"
            </p>
          </div>
        )}
      </div>
    );
  };

  const renderLibraryContent = () => {
    switch (activeTab) {
      case "elements":
        return renderElementsTab();
      case "components":
        return renderComponentsTab();
      default:
        return renderElementsTab();
    }
  };

  return (
    <div className={styles.sidebar}>
      {currentSelectedElement.index == null ? (
        <>
          {/* Header */}
          <div className={styles.header}>
            <h2 className={styles.headerTitle}>Design Library</h2>
          </div>

          {/* Search */}
          <div className={styles.searchSection}>
            <div style={{ position: "relative", width: "100%" }}>
              <Search
                size={16}
                style={{
                  position: "absolute",
                  left: "12px",
                  top: "50%",
                  transform: "translateY(-50%)",
                  color: "#9ca3af",
                  zIndex: 1,
                }}
              />
              <input
                type="text"
                placeholder="Search library..."
                className={styles.searchInput}
                style={{ paddingLeft: "36px" }}
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
          </div>

          {/* Tabs */}
          <div className={styles.tabBar}>
            <button
              onClick={() => setActiveTab("elements")}
              className={`${styles.tabButton} ${
                activeTab === "elements" ? styles.tabButtonActive : ""
              }`}
            >
              <Images size={16} />
              Elements
            </button>
            <button
              onClick={() => setActiveTab("components")}
              className={`${styles.tabButton} ${
                activeTab === "components" ? styles.tabButtonActive : ""
              }`}
            >
              <Layers size={16} />
              Components
            </button>
          </div>

          {/* Content */}
          <div className={styles.content}>{renderLibraryContent()}</div>
        </>
      ) : (
        <ElementEditorSidebar
          selectedElement={currentSelectedElement}
          setSelectedElement={setCurrentSelectedElement}
          updateElementAnimation={updateElementAnimation}
          // updateElementObjectProperties={updateElementObjectProperties}
          scaledScrollY={scaledScrollY}
        />
      )}
    </div>
  );
}
