import React, {
  useState,
  useRef,
  useEffect,
  useMemo,
  useCallback,
} from "react";
import styles from "./edit_page.module.css";
import { useSelector } from "react-redux";
import LeftOptionBar from "../../components/left_options_bar/left_options_bar.jsx";
import { useDispatch, Provider } from "react-redux";
import store, { hostedPageActions } from "../../store/hosted_page.js";
import Image from "../../components/image/image.jsx";
import Text from "../../components/text_component/text_component.jsx";
import ShapeGenerator from "../../components/shape_genrator/shape_genrator.jsx";
import Position from "../../components/position/position.jsx";
import ResizeElement from "../../components/resize_element/resize_element.jsx";
import CountDown from "../../components/countdown/countdown.jsx";
// import CustomScroll from "../../components/vertical_scroll/vertical_scroll.jsx";
import ObjectKeyFrameVerticalScroll from "../../components/object_keyframe_vertical_scroll/object_keyframe_vertical_scroll.jsx";

function saveStoreToFile(store, fileName = "store.json") {
  const state = store.getState();
  const blob = new Blob([JSON.stringify(state, null, 2)], {
    type: "application/json",
  });

  const link = document.createElement("a");
  link.href = URL.createObjectURL(blob);
  link.download = fileName;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
}

const EditPage = () => {
  const pageState = useSelector((state) => state.hosted_page);
  const canvasRef = useRef(null);
  const dispatch = useDispatch();
  const [scale, setScale] = useState(1);
  const [canvasSize, setCanvasSize] = useState({ width: 800, height: 600 });
  const [panOffset, setPanOffset] = useState({ x: 0, y: 0 });

  const [isPanning, setIsPanning] = useState(false);
  const [startMouse, setStartMouse] = useState({ x: 0, y: 0 });
  const [startPan, setStartPan] = useState({ x: 0, y: 0 });
  const baseUrl = process.env.REACT_APP_ASSET_BASE_URL;
  const [scaledScrollY, setScaledScrollY] = useState(0);
  const [currentSelectedElement, setCurrentSelectedElement] = useState({
    index: null,
    object: null,
  });
  const [scrollInputValue, setScrollInputValue] = useState("");
  const [isEditingScroll, setIsEditingScroll] = useState(false);
  // Add state to track if any keyframe is selected
  const [hasSelectedKeyframe, setHasSelectedKeyframe] = useState(false);
  const [isMainAreaFocused, setIsMainAreaFocused] = useState(false);
  
  // Handle custom events for keyframe updates
  useEffect(() => {
    const handleKeyframeUpdate = (event) => {
      const { elementIndex, property, currentKeyframe, newValue, delta, subsequentKeyframes } = event.detail;
      
      if (elementIndex === null || elementIndex === undefined) return;
      
      // Update the current keyframe
      const element = pageState?.page_content?.[elementIndex];
      if (!element?.animation?.keyframes) return;
      
      // Find and update the current keyframe based on property type
      if (property === 'position') {
        // Handle position keyframes (x and y)
        ['x', 'y'].forEach(prop => {
          const scrollYArray = element.animation.keyframes[prop]?.scrollY || [];
          const keyframeIndex = scrollYArray.indexOf(currentKeyframe);
          if (keyframeIndex !== -1) {
            dispatch(hostedPageActions.updateKeyframeScrollY({
              index: elementIndex,
              property: prop,
              keyframeIndex: keyframeIndex,
              newScrollY: newValue
            }));
          }
        });
      } else if (property === 'scale') {
        // Handle scale keyframes (height and width)
        ['height', 'width'].forEach(prop => {
          const scrollYArray = element.animation.keyframes[prop]?.scrollY || [];
          const keyframeIndex = scrollYArray.indexOf(currentKeyframe);
          if (keyframeIndex !== -1) {
            dispatch(hostedPageActions.updateKeyframeScrollY({
              index: elementIndex,
              property: prop,
              keyframeIndex: keyframeIndex,
              newScrollY: newValue
            }));
          }
        });
      } else {
        // Handle single property keyframes (rotation, opacity)
        const scrollYArray = element.animation.keyframes[property]?.scrollY || [];
        const keyframeIndex = scrollYArray.indexOf(currentKeyframe);
        if (keyframeIndex !== -1) {
          dispatch(hostedPageActions.updateKeyframeScrollY({
            index: elementIndex,
            property: property,
            keyframeIndex: keyframeIndex,
            newScrollY: newValue
          }));
        }
      }
      
      // Update subsequent keyframes
      subsequentKeyframes.forEach(subsequentKeyframe => {
        const newSubsequentValue = subsequentKeyframe + delta;
        
        if (property === 'position') {
          ['x', 'y'].forEach(prop => {
            const scrollYArray = element.animation.keyframes[prop]?.scrollY || [];
            const subKeyframeIndex = scrollYArray.indexOf(subsequentKeyframe);
            if (subKeyframeIndex !== -1) {
              dispatch(hostedPageActions.updateKeyframeScrollY({
                index: elementIndex,
                property: prop,
                keyframeIndex: subKeyframeIndex,
                newScrollY: Math.max(0, Math.min(pageState?.page_scroll_height || 1000, newSubsequentValue))
              }));
            }
          });
        } else if (property === 'scale') {
          ['height', 'width'].forEach(prop => {
            const scrollYArray = element.animation.keyframes[prop]?.scrollY || [];
            const subKeyframeIndex = scrollYArray.indexOf(subsequentKeyframe);
            if (subKeyframeIndex !== -1) {
              dispatch(hostedPageActions.updateKeyframeScrollY({
                index: elementIndex,
                property: prop,
                keyframeIndex: subKeyframeIndex,
                newScrollY: Math.max(0, Math.min(pageState?.page_scroll_height || 1000, newSubsequentValue))
              }));
            }
          });
        } else {
          const scrollYArray = element.animation.keyframes[property]?.scrollY || [];
          const subKeyframeIndex = scrollYArray.indexOf(subsequentKeyframe);
          if (subKeyframeIndex !== -1) {
            dispatch(hostedPageActions.updateKeyframeScrollY({
              index: elementIndex,
              property: property,
              keyframeIndex: subKeyframeIndex,
              newScrollY: Math.max(0, Math.min(pageState?.page_scroll_height || 1000, newSubsequentValue))
            }));
          }
        }
      });
    };
    
    const handleExitframeUpdate = (event) => {
      const { elementIndex, newValue } = event.detail;
      dispatch(hostedPageActions.updateExitScroll({
        index: elementIndex,
        exit_scroll: Math.max(0, Math.min(pageState?.page_scroll_height || 1000, newValue))
      }));
    };
    
    document.addEventListener('updateKeyframeAndSubsequent', handleKeyframeUpdate);
    document.addEventListener('updateExitframe', handleExitframeUpdate);
    
    return () => {
      document.removeEventListener('updateKeyframeAndSubsequent', handleKeyframeUpdate);
      document.removeEventListener('updateExitframe', handleExitframeUpdate);
    };
  }, [dispatch, pageState?.page_scroll_height, pageState?.page_content]);
  const selectElement = (index) => {
    if (currentSelectedElement != index) {
      setCurrentSelectedElement(index);
    }
  };

  // Use a ref to queue state updates and prevent render-time updates
  const pendingScrollUpdate = useRef(null);

  const deleteKeyframe = (elementIndex, propertyType, keyframeIndex) => {
    if (elementIndex === null || elementIndex === undefined) return;

    const element = pageState?.page_content?.[elementIndex];
    if (!element?.animation?.keyframes) return;
    if (element?.islocked) return;
    // Handle different property types
    switch (propertyType) {
      case "position":
        // For position, we need to find which specific property (x or y) has this keyframe
        const posKeyframes = [
          ...new Set([
            ...(element.animation.keyframes.x?.scrollY || []),
            ...(element.animation.keyframes.y?.scrollY || []),
          ]),
        ].sort((a, b) => a - b);

        const scrollYToDelete = posKeyframes[keyframeIndex];

        // Delete from both x and y if they exist at this scroll position
        if (element.animation.keyframes.x?.scrollY?.includes(scrollYToDelete)) {
          const xIndex =
            element.animation.keyframes.x.scrollY.indexOf(scrollYToDelete);
          dispatch(
            hostedPageActions.deleteKeyframe({
              index: elementIndex,
              property: "x",
              keyframeIndex: xIndex,
            })
          );
        }
        if (element.animation.keyframes.y?.scrollY?.includes(scrollYToDelete)) {
          const yIndex =
            element.animation.keyframes.y.scrollY.indexOf(scrollYToDelete);
          dispatch(
            hostedPageActions.deleteKeyframe({
              index: elementIndex,
              property: "y",
              keyframeIndex: yIndex,
            })
          );
        }
        break;

      case "scale":
        // For scale, handle height and width
        const scaleKeyframes = [
          ...new Set([
            ...(element.animation.keyframes.height?.scrollY || []),
            ...(element.animation.keyframes.width?.scrollY || []),
          ]),
        ].sort((a, b) => a - b);

        const scaleScrollYToDelete = scaleKeyframes[keyframeIndex];

        // Delete from both height and width if they exist at this scroll position
        if (
          element.animation.keyframes.height?.scrollY?.includes(
            scaleScrollYToDelete
          )
        ) {
          const heightIndex =
            element.animation.keyframes.height.scrollY.indexOf(
              scaleScrollYToDelete
            );
          dispatch(
            hostedPageActions.deleteKeyframe({
              index: elementIndex,
              property: "height",
              keyframeIndex: heightIndex,
            })
          );
        }
        if (
          element.animation.keyframes.width?.scrollY?.includes(
            scaleScrollYToDelete
          )
        ) {
          const widthIndex =
            element.animation.keyframes.width.scrollY.indexOf(
              scaleScrollYToDelete
            );
          dispatch(
            hostedPageActions.deleteKeyframe({
              index: elementIndex,
              property: "width",
              keyframeIndex: widthIndex,
            })
          );
        }
        break;

      case "rotation":
        // For rotation, it's straightforward
        dispatch(
          hostedPageActions.deleteKeyframe({
            index: elementIndex,
            property: "rotation",
            keyframeIndex: keyframeIndex,
          })
        );
        break;

      case "opacity":
        // For opacity, it's straightforward
        dispatch(
          hostedPageActions.deleteKeyframe({
            index: elementIndex,
            property: "opacity",
            keyframeIndex: keyframeIndex,
          })
        );
        break;

      default:
        // Fallback for direct property names
        dispatch(
          hostedPageActions.deleteKeyframe({
            index: elementIndex,
            property: propertyType,
            keyframeIndex: keyframeIndex,
          })
        );
        break;
    }
  };
  const deleteExitframe = (elementIndex) => {
    console.log("Deleting exitframe for element index:", elementIndex);
    if (elementIndex === null || elementIndex === undefined) return;
    const element = pageState?.hosted_page?.page_content?.[elementIndex];
    if (!element) return;
    if (element?.islocked) return;
    dispatch(
      hostedPageActions.updateExitScroll({
        index: elementIndex,
        exit_scroll: null,
      })
    );
  };
  // Delete element function
  const deleteSelectedElement = useCallback(() => {
    if (currentSelectedElement.index !== null) {
      dispatch(
        hostedPageActions.deleteElement({ index: currentSelectedElement.index })
      );
      setCurrentSelectedElement({ index: null, object: null });
    }
  }, [currentSelectedElement.index, dispatch]);

  // Keyboard event handler for delete key - only delete element if no keyframe is selected
  useEffect(() => {
    const handleKeyDown = (event) => {
      //  Delete selected element
      if (
        event.key === "Delete" &&
        currentSelectedElement.index !== null &&
        !hasSelectedKeyframe &&
        isMainAreaFocused
      ) {
        event.preventDefault();
        deleteSelectedElement();
      }

      //  Ctrl+S / Cmd+S for Save
      if ((event.ctrlKey || event.metaKey) && event.key.toLowerCase() === "s") {
        event.preventDefault();
        saveStoreToFile(store); // <-- call your save function here
      }
    };

    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  }, [
    deleteSelectedElement,
    currentSelectedElement.index,
    hasSelectedKeyframe,
    isMainAreaFocused,
  ]);

  // Memoize the scroll change handler with queued state update
  const handleScrollChange = useCallback((newScrollY) => {
    pendingScrollUpdate.current = newScrollY;
    // Use requestAnimationFrame to defer the update
    requestAnimationFrame(() => {
      if (pendingScrollUpdate.current !== null) {
        setScaledScrollY(pendingScrollUpdate.current);
        pendingScrollUpdate.current = null;
      }
    });
  }, []);
  // Handle scroll input changes
  const handleScrollInputChange = (e) => {
    setScrollInputValue(e.target.value);
  };

  const handleScrollInputKeyDown = (e) => {
    if (e.key === 'Enter') {
      const newScrollValue = parseFloat(scrollInputValue) || 0;
      const maxScroll = pageState?.page_scroll_height || 1000;
      const clampedValue = Math.max(0, Math.min(maxScroll, newScrollValue));
      setScaledScrollY(clampedValue);
      setIsEditingScroll(false);
    }
    if (e.key === 'Escape') {
      setIsEditingScroll(false);
      setScrollInputValue("");
    }
  };

  const handleScrollInputFocus = () => {
    setIsEditingScroll(true);
    setScrollInputValue(scaledScrollY.toString());
  };

  const handleScrollInputBlur = () => {
    setIsEditingScroll(false);
    setScrollInputValue("");
  };

  // Memoize the keyframe click handler with queued state update
  const handleKeyFrameClick = useCallback((scrollY) => {
    pendingScrollUpdate.current = scrollY;
    // Use requestAnimationFrame to defer the update
    requestAnimationFrame(() => {
      if (pendingScrollUpdate.current !== null) {
        setScaledScrollY(pendingScrollUpdate.current);
        pendingScrollUpdate.current = null;
      }
    });
  }, []);

  // Callback to handle keyframe selection state changes
  const handleKeyframeSelectionChange = useCallback((hasSelection) => {
    setHasSelectedKeyframe(hasSelection);
  }, []);

  // Fit to screen with proper centering and scaling
  const fitToScreen = useCallback(() => {
    // console.log("fit to screen");
    const container = canvasRef.current;
    if (!container) return;

    const containerRect = container.getBoundingClientRect();
    const padding = 20; // Add some padding

    const usableWidth = containerRect.width - padding;
    const usableHeight = containerRect.height - padding;

    const scaleX = usableWidth / canvasSize.width;
    const scaleY = usableHeight / canvasSize.height;

    const newScale = Math.min(scaleX, scaleY);

    // Center the canvas
    const offsetX = (containerRect.width - canvasSize.width * newScale) / 2;
    const offsetY = (containerRect.height - canvasSize.height * newScale) / 2;

    setScale(newScale);
    setPanOffset({ x: offsetX, y: offsetY });
  }, [canvasSize]);

  // Enhanced animation keyframe update function
  const updateElementAnimation = useCallback(
    (index, values) => {
      dispatch(
        hostedPageActions.updateElementAnimation({
          index: index,
          values: values,
          currentScrollY: scaledScrollY,
        })
      );
    },
    [pageState, scaledScrollY, dispatch]
  );

  // Zooming with Ctrl + Scroll
  useEffect(() => {
    const handleWheel = (event) => {
      if (event.ctrlKey) {
        event.preventDefault();
        const delta = event.deltaY;
        const zoomFactor = delta > 0 ? 0.9 : 1.1;

        setScale((prevScale) => {
          const newScale = Math.max(0.1, Math.min(5, prevScale * zoomFactor));
          return newScale;
        });
      }
    };

    const container = canvasRef.current;
    if (container) {
      container.addEventListener("wheel", handleWheel, { passive: false });
      return () => container.removeEventListener("wheel", handleWheel);
    }
  }, []);

  const pageSize = useMemo(() => {
    // Memoize the page size value so it only updates when the actual content changes
    // Using JSON.stringify here creates a string version of the object,
    // which allows us to detect deep value changes (not just reference changes)
    return pageState?.page_size;
  }, [JSON.stringify(pageState?.page_size)]);

  useEffect(() => {
    // Run this effect only when the memoized `pageSize` changes
    // Prevents unnecessary calls to setCanvasSize when Redux returns a new object
    // with the same values (which would normally trigger useEffect)
    if (pageSize) {
      // Update canvas size only when actual size values have changed
      setCanvasSize({ ...pageSize });
    }
  }, [pageSize]);

  // Fit to screen when canvas size changes
  useEffect(() => {
    fitToScreen();
  }, [canvasSize, fitToScreen]);

  // Mouse event handlers for panning
  const handleMouseDown = useCallback(
    (event) => {
      if (event.button === 1 || (event.button === 0 && event.ctrlKey)) {
        event.preventDefault();
        setIsPanning(true);
        setStartMouse({ x: event.clientX, y: event.clientY });
        setStartPan({ ...panOffset });
      }
    },
    [panOffset]
  );

  const handleMouseMove = useCallback(
    (event) => {
      if (!isPanning) return;

      const dx = event.clientX - startMouse.x;
      const dy = event.clientY - startMouse.y;

      setPanOffset({
        x: startPan.x + dx,
        y: startPan.y + dy,
      });
    },
    [isPanning, startMouse, startPan]
  );

  const handleMouseUp = useCallback(() => {
    setIsPanning(false);
  }, []);

  // Global mouse event listeners
  useEffect(() => {
    if (isPanning) {
      window.addEventListener("mousemove", handleMouseMove);
      window.addEventListener("mouseup", handleMouseUp);

      return () => {
        window.removeEventListener("mousemove", handleMouseMove);
        window.removeEventListener("mouseup", handleMouseUp);
      };
    }
  }, [isPanning, handleMouseMove, handleMouseUp]);

  // Render images with proper memoization
  const renderElement = useMemo(() => {
    if (!pageState?.page_content) {
      return null;
    }

    return pageState.page_content.map((elementMetaData, index) => {
      // Create a more specific key that includes object_property changes
      const objectPropertyKey = JSON.stringify(
        elementMetaData.object_property || {}
      );
      const positionKey = `${elementMetaData.position?.x || 0}-${
        elementMetaData.position?.y || 0
      }-${elementMetaData.position?.width || 0}-${
        elementMetaData.position?.height || 0
      }-${elementMetaData.position?.rotation || 0}`;

      const render = (type, object_property) => {
        // console.log("Rendering element:", type, object_property);
        switch (type) {
          case "image":
            return (
              <Image
                key={objectPropertyKey} // Force re-render when properties change
                file={`${baseUrl}${object_property?.file || ""}`}
              />
            );

          case "text":
            return (
              <Text
                key={objectPropertyKey} // Force re-render when properties change
                text={object_property?.text || ""}
                style={object_property?.style || {}}
              />
            );

          case "shape":
            return (
              <ShapeGenerator
                key={objectPropertyKey} // Force re-render when properties change
                selectedShape={object_property?.shape || "circle"}
                color={object_property?.color || "#FCEFDA"}
                gradientEnabled={object_property?.gradient_enabled || false}
                gradientColor2={object_property?.color2 || "#ef4444"}
                gradientDirection={object_property?.direction || "45deg"}
                gradient={object_property?.shadow || false}
                shadow={object_property?.shadow || false}
                shadowColor={object_property?.shadowColor || "#f4ede2ff"}
                shadowBlur={object_property?.shadowBlur || 0}
                shadowOffsetX={object_property?.shadowOffsetX || 0}
                shadowOffsetY={object_property?.shadowOffsetY || 0}
              />
            );
          case "countdown":
            return (
              <CountDown
                key={objectPropertyKey} // Force re-render when properties change
                targetDate={object_property?.target || ""}
                style={object_property?.style || {}}
              />
            );

          default:
            return null; // or some fallback UI
        }
      };

      // Safely access keyframes with fallbacks
      const keyframes = elementMetaData.animation?.keyframes || {};
      const xKeyframes = keyframes.x || [0];
      const yKeyframes = keyframes.y || [0];
      const heightKeyframes = keyframes.height || [100];
      const widthKeyframes = keyframes.width || [100];
      const rotationKeyframes = keyframes.rotation || [0];
      const opacityKeyframes = keyframes.opacity || [1];
      // console.log(
      //   "EEEEEEEE-",
      //   elementMetaData.exit_scroll,
      //   scaledScrollY,
      //   elementMetaData.exit_scroll >= scaledScrollY
      // );
      return (elementMetaData.exit_scroll === null ||
        elementMetaData.exit_scroll >= scaledScrollY) &&
        elementMetaData.animation.keyframes.y.scrollY[0] <= scaledScrollY ? (
        <Position
          key={`element-${index}-${positionKey}-${objectPropertyKey}`}
          x={`${xKeyframes[0]}px`}
          y={`${yKeyframes[0]}px`}
          height={`${heightKeyframes[0]}px`}
          width={`${widthKeyframes[0]}px`}
          rotation={rotationKeyframes[0]}
          opacity={opacityKeyframes[0]}
          animation={elementMetaData.animation}
          layer={elementMetaData.layer}
          scale={scale}
          scaledScrollY={scaledScrollY}
          style={
            {
              // border: '10px dashed #5734d4ff',
              // boxSizing:'border-box'
            }
          }
          passStyleFlag={true}
          onClick={() => {
            // console.log(`Element clicked: index = ${index}`);
            // selectElement({index:index, object:elementMetaData});
          }}
        >
          <ResizeElement
            resizeHandler={(val) => {
              console.table(
                ("images",
                index,
                {
                  x: val.x,
                  y: val.y,
                  height: val.height,
                  width: val.width,
                  rotation: val.rotation,
                  opacity: val.opacity,
                })
              );
              updateElementAnimation(index, {
                x: val.x,
                y: val.y,
                height: val.height,
                width: val.width,
                rotation: val.rotation,
                opacity: val.opacity,
              });
            }}
            index={index}
            scale={scale}
            setSelectCurrent={setCurrentSelectedElement}
            islocked={elementMetaData.islocked}
          >
            {render(
              elementMetaData.object_type,
              elementMetaData.object_property
            )}
          </ResizeElement>
        </Position>
      ) : null;
    });
  }, [
    pageState?.hosted_page?.page_content,
    baseUrl,
    scale,
    scaledScrollY,
    updateElementAnimation,
    canvasSize,
    selectElement,
  ]);

  return (
    <div className={styles.container}>
      <div className={styles.sidebar}>
        <LeftOptionBar
          updateElementAnimation={updateElementAnimation}
          currentSelectedElement={currentSelectedElement}
          setCurrentSelectedElement={setCurrentSelectedElement}
          // updateElementObjectProperties={updateElementObjectProperties}
          scaledScrollY={scaledScrollY} // Add this line
        />
      </div>

      <div className={styles.mainArea}>
        <div className={styles.canvasContainer}>
          <div
            ref={canvasRef}
            className={styles.canvasWrapper}
            data-main-canvas="true" // ADD THIS LINE
            style={{ cursor: isPanning ? "grabbing" : "default" }}
            onMouseDown={handleMouseDown}
            onFocus={() => setIsMainAreaFocused(true)}
            onBlur={() => setIsMainAreaFocused(false)}
            onMouseEnter={() => setIsMainAreaFocused(true)}
            onMouseLeave={() => setIsMainAreaFocused(false)}
            tabIndex={0} // Make the div focusable
          >
            <div
              className={styles.canvasContent}
              style={{
                width: canvasSize.width,
                height: canvasSize.height,
                transform: `translate(${panOffset.x}px, ${panOffset.y}px) scale(${scale})`,
                transformOrigin: "0 0",
                backgroundColor: pageState?.page_background?.color || "#ffffff",
              }}
            >
              {renderElement}
            </div>

            {/* Zoom instructions
            <div className={styles.zoomInstructions}>
              Ctrl + Scroll: Zoom | Ctrl + Drag or Middle Click: Pan | Delete: Remove Selected Element
            </div> */}
          </div>
        </div>
      </div>

      <div className={styles.right_sidebar}>
        <div style={{ padding: "5px 8px", fontSize: "12px", textAlign: "center", display: "flex", alignItems: "center", justifyContent: "center", gap: "6px" }}>
          <span>Scroll:</span>
          <style>
            {`
              input[type="number"]::-webkit-outer-spin-button,
              input[type="number"]::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
              }
              
              input[type="number"] {
                -moz-appearance: textfield;
                appearance: textfield;
              }
              
              input[type="number"]:focus {
                outline: none;
              }
            `}
          </style>
          {isEditingScroll ? (
            <input
              type="number"
              min="0"
              max={pageState?.page_scroll_height || 1000}
              value={scrollInputValue}
              onChange={handleScrollInputChange}
              onKeyDown={handleScrollInputKeyDown}
              onBlur={handleScrollInputBlur}
              autoFocus
              style={{
                width: "60px",
                padding: "3px 0px",
                fontSize: "12px",
                border: "1px solid #d1d5db",
                backgroundColor: "#ffffff",
                boxShadow: "0 0 0 1px rgba(59, 130, 246, 0.3)",
                borderRadius: "2px",
                textAlign: "center",
                outline: "none"
              }}
            />
          ) : (
            <span 
              onClick={handleScrollInputFocus}
              style={{ 
                cursor: "pointer", 
                textDecoration: "none",
                padding: "3px 6px",
                width: "65px",
                display: "inline-block",
                textAlign: "center",
                border: "1px solid #e5e7eb",
                backgroundColor: "#f9fafb",
                borderRadius: "2px",
                transition: "all 0.15s ease",
                fontSize: "12px",
                boxSizing: "border-box"
              }}
              onMouseEnter={(e) => {
                e.target.style.backgroundColor = "#f3f4f6";
                e.target.style.borderColor = "#d1d5db";
              }}
              onMouseLeave={(e) => {
                e.target.style.backgroundColor = "#f9fafb";
                e.target.style.borderColor = "#e5e7eb";
              }}
            >
              {scaledScrollY}
            </span>
          )}
        </div>
        
        <div
          style={{
            height: "100%",
            width: "100%",
            display: "flex",
            justifyContent: "space-evenly",
          }}
        >
          <div
            style={{
              height: "100%",
              width: "25%",
            }}
          >
            <ObjectKeyFrameVerticalScroll
              initialProgress={0}
              onchange={handleScrollChange}
              keyframes={[
                ...new Set([
                  ...(pageState?.page_content?.[
                    currentSelectedElement?.index
                  ]?.animation?.keyframes?.x?.scrollY || []),
                  ...(pageState?.page_content?.[
                    currentSelectedElement?.index
                  ]?.animation?.keyframes?.y?.scrollY || []),
                ]),
              ].sort((a, b) => a - b)}
              onKeyFrameClicked={handleKeyFrameClick}
              currentScroll={scaledScrollY}
              min={0}
              max={pageState?.page_scroll_height || 1000}
              tag={"P"}
              tooltip_text={"Position"}
              elementIndex={currentSelectedElement?.index}
              property="position"
              onDeleteKeyframe={deleteKeyframe}
              onKeyframeSelectionChange={handleKeyframeSelectionChange}
              onDeleteExitframe={deleteExitframe}
              exit_scroll={[
                pageState?.page_content?.[
                  currentSelectedElement?.index
                ]?.exit_scroll,
              ]}
            />
          </div>
          <div
            style={{
              height: "100%",
              width: "25%",
            }}
          >
            <ObjectKeyFrameVerticalScroll
              initialProgress={0}
              onchange={handleScrollChange}
              keyframes={[
                ...new Set([
                  ...(pageState?.page_content?.[
                    currentSelectedElement?.index
                  ]?.animation?.keyframes?.height?.scrollY || []),
                  ...(pageState?.page_content?.[
                    currentSelectedElement?.index
                  ]?.animation?.keyframes?.width?.scrollY || []),
                ]),
              ].sort((a, b) => a - b)}
              onKeyFrameClicked={handleKeyFrameClick}
              currentScroll={scaledScrollY}
              min={0}
              max={pageState?.page_scroll_height || 1000}
              tag={"S"}
              tooltip_text={"Scale"}
              elementIndex={currentSelectedElement?.index}
              property="scale"
              onDeleteKeyframe={deleteKeyframe}
              onKeyframeSelectionChange={handleKeyframeSelectionChange}
              // onDeleteExitframe={deleteExitframe}
            />
          </div>
          <div
            style={{
              height: "100%",
              width: "25%",
            }}
          >
            <ObjectKeyFrameVerticalScroll
              initialProgress={0}
              onchange={handleScrollChange}
              keyframes={[
                ...new Set([
                  ...(pageState?.page_content?.[
                    currentSelectedElement?.index
                  ]?.animation?.keyframes?.rotation?.scrollY || []),
                ]),
              ].sort((a, b) => a - b)}
              onKeyFrameClicked={handleKeyFrameClick}
              currentScroll={scaledScrollY}
              min={0}
              max={pageState?.page_scroll_height || 1000}
              tag={"R"}
              tooltip_text={"Rotation"}
              elementIndex={currentSelectedElement?.index}
              property="rotation"
              onDeleteKeyframe={deleteKeyframe}
              onKeyframeSelectionChange={handleKeyframeSelectionChange}
            />
          </div>
          <div
            style={{
              height: "100%",
              width: "25%",
            }}
          >
            <ObjectKeyFrameVerticalScroll
              initialProgress={0}
              onchange={handleScrollChange}
              keyframes={[
                ...new Set([
                   ...(pageState?.page_content?.[
                    currentSelectedElement?.index
                  ]?.animation?.keyframes?.opacity?.scrollY || []),
                ]),
              ].sort((a, b) => a - b)}
              onKeyFrameClicked={handleKeyFrameClick}
              currentScroll={scaledScrollY}
              min={0}
              max={pageState?.page_scroll_height || 1000}
              
              tag={"O"}
              tooltip_text={"Opacity"}
              elementIndex={currentSelectedElement?.index}
              property="opacity"
              onDeleteKeyframe={deleteKeyframe}
              onKeyframeSelectionChange={handleKeyframeSelectionChange}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default EditPage;
