import React, { useState, useCallback, useEffect } from "react";
import { Play, HelpCircle, Trash2 } from "lucide-react";

const AnimationPresets = ({ selectedElement, applyAnimationPreset }) => {
  const [hoveredAnimation, setHoveredAnimation] = useState(null);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  // console.log(selectedElement.object.animation.properties.type)
  // Define preset animations based on preset_animation.js
  const presetAnimations = {
    infiniteRotation: {
      name: "Infinite Rotation",
      icon: "",
      parameters: {
        speed: {
          default: 1,
          min: 0.1,
          max: 5,
          step: 0.1,
          description: "Rotations per second",
        },
      },
    },
    pulse: {
      name: "Pulse",
      icon: "",
      parameters: {
        speed: {
          default: 1,
          min: 0.1,
          max: 3,
          step: 0.1,
          description: "Pulse frequency per second",
        },
        intensity: {
          default: 0.2,
          min: 0.1,
          max: 1,
          step: 0.1,
          description: "How much the element grows/shrinks",
        },
      },
    },
    bounce: {
      name: "Bounce",
      icon: "",
      parameters: {
        height: {
          default: 30,
          min: 5,
          max: 100,
          step: 5,
          description: "Maximum bounce height in pixels",
        },
        speed: {
          default: 1,
          min: 0.1,
          max: 3,
          step: 0.1,
          description: "Bounce frequency per second",
        },
      },
    },
    shake: {
      name: "Shake",
      icon: "",
      parameters: {
        intensity: {
          default: 10,
          min: 1,
          max: 20,
          step: 1,
          description: "Maximum shake distance in pixels",
        },
        speed: {
          default: 1,
          min: 0.1,
          max: 3,
          step: 0.1,
          description: "Shake frequency per second",
        },
      },
    },
    float: {
      name: "Float",
      icon: "",
      parameters: {
        height: {
          default: 20,
          min: 2,
          max: 50,
          step: 2,
          description: "Maximum float distance in pixels",
        },
        speed: {
          default: 1,
          min: 0.1,
          max: 2,
          step: 0.1,
          description: "Float frequency per second",
        },
      },
    },
  };

  // State for animation parameters
  const [animationParams, setAnimationParams] = useState(() => {
    const params = {};
    Object.keys(presetAnimations).forEach((type) => {
      params[type] = {};
      Object.keys(presetAnimations[type].parameters).forEach((param) => {
        params[type][param] = presetAnimations[type].parameters[param].default;
      });
    });
    return params;
  });

  const [paramTooltip, setParamTooltip] = useState(null);

  // Set default values from selected element when element changes
  useEffect(() => {
    if (selectedElement?.object?.animation?.properties) {
      const animationProps = selectedElement.object.animation.properties;
      const animationType = animationProps.type;

      // Check if the animation type exists in our presets
      if (animationType && presetAnimations[animationType]) {
        const currentParams = animationProps.parameters || {};

        // Update the animation parameters with values from the selected element
        setAnimationParams((prev) => ({
          ...prev,
          [animationType]: {
            ...prev[animationType],
            ...currentParams, // Merge current parameters from the selected element
          },
        }));
      }
    }
  }, [selectedElement?.object?.animation?.properties]);

  // Handle mouse events for animation hover
  const handleAnimationMouseMove = useCallback((e, key) => {
    setMousePosition({ x: e.clientX, y: e.clientY });
    setHoveredAnimation(key);
  }, []);

  const handleAnimationMouseLeave = useCallback(() => {
    setHoveredAnimation(null);
  }, []);

  // Handle parameter tooltip
  const handleParamTooltip = useCallback((e, animationType, paramName) => {
    setMousePosition({ x: e.clientX, y: e.clientY });
    setParamTooltip({ animationType, paramName });
  }, []);

  const handleParamTooltipLeave = useCallback(() => setParamTooltip(null), []);

  // Get animation styles for preview
  const getAnimationStyles = useCallback((animation) => {
    return {
      animationName: animation.name,
      animationTimingFunction: animation["timing-function"],
      animationIterationCount: animation["iteration-count"],
      animationDuration: animation.duration || "1s",
      animationFillMode: animation["fill-mode"] || "none",
    };
  }, []);

  // Format animation info for tooltip
  const formatAnimationValue = useCallback((animation) => {
    return `Duration: ${animation.duration || "1s"}
Timing: ${animation["timing-function"]}
Iterations: ${animation["iteration-count"]}
Fill Mode: ${animation["fill-mode"] || "none"}`;
  }, []);

  // Apply preset animation with parameters
  const applyPresetAnimation = useCallback(
    (animationType) => {
      if (!selectedElement?.object) return;

      const params = animationParams[animationType];
      applyAnimationPreset(animationType, params);
    },
    [selectedElement, animationParams, applyAnimationPreset]
  );

  // Update parameter value
  const updateParam = useCallback((animationType, paramName, value) => {
    setAnimationParams((prev) => ({
      ...prev,
      [animationType]: {
        ...prev[animationType],
        [paramName]: value,
      },
    }));
  }, []);

  // Remove animation from selected element
  const removeAnimation = useCallback(() => {
    if (!selectedElement?.object) return;

    applyAnimationPreset(null, null); // Pass null to remove animation
  }, [selectedElement, applyAnimationPreset]);

  return (
    <div>
      <h3
        style={{
          fontWeight: "500",
          color: "#111827",
          marginBottom: "16px",
          marginTop: 0,
        }}
      >
        Preset Animations
      </h3>

      <div
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))",
          gap: "20px",
          marginBottom: "24px",
        }}
      >
        {Object.keys(presetAnimations).map((animationType) => {
          const animation = presetAnimations[animationType];
          const params = animationParams[animationType];
          const isCurrentAnimation =
            selectedElement?.object?.animation?.properties?.type ===
            animationType;

          return (
            <div
              key={animationType}
              style={{
                position: "relative",
                backgroundColor: isCurrentAnimation
                  ? "rgba(239, 246, 255, 0.9)"
                  : "rgba(255, 255, 255, 0.8)",
                border: isCurrentAnimation
                  ? "2px solid #60a5fa"
                  : "1px solid #e5e7eb",
                borderRadius: "8px",
                padding: "20px",
                transition: "all 0.2s ease",
              }}
              onMouseOver={(e) => {
                if (!isCurrentAnimation) {
                  e.currentTarget.style.borderColor = "#93c5fd";
                  e.currentTarget.style.backgroundColor = "#eff6ff";
                }
                e.currentTarget.style.transform = "translateY(-1px)";
                e.currentTarget.style.boxShadow =
                  "0 4px 12px rgba(0, 0, 0, 0.1)";
              }}
              onMouseOut={(e) => {
                if (!isCurrentAnimation) {
                  e.currentTarget.style.borderColor = "#e5e7eb";
                  e.currentTarget.style.backgroundColor =
                    "rgba(255, 255, 255, 0.8)";
                } else {
                  e.currentTarget.style.borderColor = "#60a5fa";
                  e.currentTarget.style.backgroundColor =
                    "rgba(239, 246, 255, 0.9)";
                }
                e.currentTarget.style.transform = "translateY(0)";
                e.currentTarget.style.boxShadow = "none";
              }}
            >
              {/* Current Animation Indicator */}
              {isCurrentAnimation && (
                <div
                  style={{
                    position: "absolute",
                    top: "8px",
                    right: "8px",
                    backgroundColor: "#10b981",
                    color: "white",
                    fontSize: "10px",
                    fontWeight: "600",
                    padding: "2px 6px",
                    borderRadius: "4px",
                    textTransform: "uppercase",
                    letterSpacing: "0.5px",
                  }}
                >
                  Active
                </div>
              )}

              {/* Animation Preview */}
              <div
                onClick={() => applyPresetAnimation(animationType)}
                onMouseMove={(e) => handleAnimationMouseMove(e, animationType)}
                onMouseLeave={handleAnimationMouseLeave}
                style={{
                  cursor: "pointer",
                  marginBottom: "12px",
                  textAlign: "center",
                }}
              >
                <div
                  style={{
                    width: "40px",
                    height: "40px",
                    background: isCurrentAnimation
                      ? "linear-gradient(45deg, #10b981, #059669)"
                      : "linear-gradient(45deg, #60a5fa, #a855f7)",
                    borderRadius: "6px",
                    margin: "0 auto 8px auto",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    color: "white",
                    fontSize: "18px",
                    fontWeight: "bold",
                    boxShadow: "0 2px 8px rgba(0, 0, 0, 0.2)",
                  }}
                >
                  {animation.icon}
                </div>

                <h4
                  style={{
                    fontSize: "14px",
                    fontWeight: "600",
                    color: "#374151",
                    marginBottom: "4px",
                    marginTop: 0,
                    lineHeight: "1.2",
                  }}
                >
                  {animation.name}
                </h4>
              </div>

              {/* Animation Parameters */}
              <div style={{ marginBottom: "16px" }}>
                {Object.keys(animation.parameters).map((paramName) => {
                  const paramConfig = animation.parameters[paramName];
                  const currentValue = params[paramName];

                  return (
                    <div key={paramName} style={{ marginBottom: "12px" }}>
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          gap: "6px",
                          marginBottom: "4px",
                        }}
                      >
                        <label
                          style={{
                            fontSize: "11px",
                            fontWeight: "500",
                            color: "#6b7280",
                            textTransform: "capitalize",
                          }}
                        >
                          {paramName}
                        </label>
                        <HelpCircle
                          size={12}
                          style={{
                            color: "#9ca3af",
                            cursor: "help",
                          }}
                          onMouseEnter={(e) =>
                            handleParamTooltip(e, animationType, paramName)
                          }
                          onMouseLeave={handleParamTooltipLeave}
                        />
                      </div>
                      <input
                        type="number"
                        min={paramConfig.min}
                        max={paramConfig.max}
                        step={paramConfig.step}
                        value={currentValue}
                        onChange={(e) => {
                          const newValue =
                            parseFloat(e.target.value) || paramConfig.default;
                          updateParam(animationType, paramName, newValue);
                        }}
                        onClick={(e) => e.stopPropagation()}
                        style={{
                          width: "100%",
                          padding: "6px 8px",
                          border: isCurrentAnimation
                            ? "2px solid #60a5fa"
                            : "1px solid #d1d5db",
                          borderRadius: "4px",
                          fontSize: "12px",
                          backgroundColor: "white",
                        }}
                      />
                    </div>
                  );
                })}
              </div>

              {/* Action Buttons */}
              <div
                style={{
                  display: "flex",
                  gap: "8px",
                }}
              >
                <button
                  onClick={() => applyPresetAnimation(animationType)}
                  style={{
                    flex: 1,
                    padding: "8px 12px",
                    backgroundColor: isCurrentAnimation ? "#dcfce7" : "#eff6ff",
                    border: isCurrentAnimation
                      ? "1px solid #22c55e"
                      : "1px solid #93c5fd",
                    borderRadius: "4px",
                    fontSize: "12px",
                    fontWeight: "500",
                    color: isCurrentAnimation ? "#15803d" : "#1e40af",
                    cursor: "pointer",
                    transition: "all 0.2s",
                  }}
                  onMouseOver={(e) => {
                    if (isCurrentAnimation) {
                      e.target.style.backgroundColor = "#bbf7d0";
                      e.target.style.borderColor = "#16a34a";
                    } else {
                      e.target.style.backgroundColor = "#dbeafe";
                      e.target.style.borderColor = "#60a5fa";
                    }
                  }}
                  onMouseOut={(e) => {
                    if (isCurrentAnimation) {
                      e.target.style.backgroundColor = "#dcfce7";
                      e.target.style.borderColor = "#22c55e";
                    } else {
                      e.target.style.backgroundColor = "#eff6ff";
                      e.target.style.borderColor = "#93c5fd";
                    }
                  }}
                >
                  {isCurrentAnimation ? "Update Animation" : "Apply Animation"}
                </button>

                {isCurrentAnimation && (
                  <button
                    onClick={removeAnimation}
                    title="Remove animation from selected element"
                    style={{
                      padding: "8px",
                      backgroundColor: "#fee2e2",
                      border: "1px solid #fca5a5",
                      borderRadius: "4px",
                      fontSize: "12px",
                      fontWeight: "500",
                      color: "#dc2626",
                      cursor: "pointer",
                      transition: "all 0.2s",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                    }}
                    onMouseOver={(e) => {
                      e.target.style.backgroundColor = "#fecaca";
                      e.target.style.borderColor = "#f87171";
                    }}
                    onMouseOut={(e) => {
                      e.target.style.backgroundColor = "#fee2e2";
                      e.target.style.borderColor = "#fca5a5";
                    }}
                  >
                    <Trash2 size={14} />
                  </button>
                )}
              </div>
            </div>
          );
        })}
      </div>

      {/* Parameter Tooltip */}
      {paramTooltip && (
        <div
          style={{
            position: "fixed",
            left: mousePosition.x + 10,
            top: mousePosition.y - 10,
            backgroundColor: "rgba(0, 0, 0, 0.9)",
            color: "white",
            padding: "8px 12px",
            borderRadius: "6px",
            fontSize: "12px",
            boxShadow: "0 4px 15px rgba(0, 0, 0, 0.3)",
            backdropFilter: "blur(5px)",
            border: "1px solid rgba(255, 255, 255, 0.2)",
            pointerEvents: "none",
            zIndex: 51,
            opacity: 0.95,
            maxWidth: "200px",
            lineHeight: "1.3",
          }}
        >
          <div
            style={{
              fontWeight: "600",
              marginBottom: "4px",
              textTransform: "capitalize",
            }}
          >
            {paramTooltip.paramName}
          </div>
          <div style={{ fontSize: "11px", color: "#d1d5db" }}>
            {
              presetAnimations[paramTooltip.animationType].parameters[
                paramTooltip.paramName
              ].description
            }
          </div>
        </div>
      )}

      {/* Animation Hover Tooltip */}
      {hoveredAnimation && (
        <div
          style={{
            position: "fixed",
            left: mousePosition.x + 10,
            top: mousePosition.y - 10,
            backgroundColor: "rgba(0, 0, 0, 0.9)",
            color: "white",
            padding: "8px 12px",
            borderRadius: "6px",
            fontSize: "12px",
            boxShadow: "0 4px 15px rgba(0, 0, 0, 0.3)",
            backdropFilter: "blur(5px)",
            border: "1px solid rgba(255, 255, 255, 0.2)",
            pointerEvents: "none",
            zIndex: 50,
            opacity: 0.95,
            maxWidth: "200px",
            lineHeight: "1.3",
          }}
        >
          <div style={{ fontWeight: "600", marginBottom: "4px" }}>
            {presetAnimations[hoveredAnimation].name}
          </div>
          <div style={{ fontSize: "11px", color: "#d1d5db" }}>
            Click to apply this animation to the selected element
          </div>
        </div>
      )}

      {/* Info Messages */}
      {selectedElement?.object && (
        <div
          style={{
            marginTop: "16px",
            padding: "12px",
            backgroundColor: "#f0f9ff",
            borderRadius: "6px",
            border: "1px solid #0ea5e9",
          }}
        >
          <p
            style={{
              fontSize: "12px",
              color: "#0c4a6e",
              marginBottom: "6px",
              fontWeight: "500",
              margin: "0 0 6px 0",
            }}
          >
            Animation Ready
          </p>
          <p
            style={{
              fontSize: "11px",
              color: "#075985",
              margin: 0,
              lineHeight: "1.4",
            }}
          >
            Adjust parameters and click "Apply Animation" to add preset
            animations to the selected element. Hover over the ? icons for
            parameter details.
          </p>
        </div>
      )}

      {!selectedElement?.object && (
        <div
          style={{
            marginTop: "16px",
            padding: "12px",
            backgroundColor: "#fef3c7",
            borderRadius: "6px",
            border: "1px solid #f59e0b",
          }}
        >
          <p
            style={{
              fontSize: "12px",
              color: "#92400e",
              marginBottom: "6px",
              fontWeight: "500",
              margin: "0 0 6px 0",
            }}
          >
            Select an Element
          </p>
          <p
            style={{
              fontSize: "11px",
              color: "#b45309",
              margin: 0,
              lineHeight: "1.4",
            }}
          >
            Select an element from the canvas first, then choose a preset
            animation to apply to it.
          </p>
        </div>
      )}
    </div>
  );
};

export default AnimationPresets;
