import React, {
  useEffect,
  useState,
  useCallback,
  useMemo,
  useRef,
} from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { Settings, Play, LogOut } from "lucide-react";
import styles from "./element_property.module.css";
import store, { hostedPageActions } from "../../store/hosted_page.js";
import ANIMATION_PRESET from "../../animations/preset.js";

// Import separated components
import LayerControls from "./components/layer_controls.jsx";
import TransformProperties from "./components/transform_properties.jsx";
import ShapeProperties from "./components/shape_properties.jsx";
import TextProperties from "./components/text_properties.jsx";
import AnimationPresets from "./components/animation_presets.jsx";
import ExitAnimationPresets from "./components/exit_animation_presets.jsx";

const ElementEditorSidebar = ({
  selectedElement,
  setSelectedElement,
  updateElementAnimation,
  scaledScrollY = 0,
}) => {
  const dispatch = useDispatch();
  const pageState = useSelector((state) => state);
  const isElementLocked = selectedElement?.object?.islocked || false;
  
  // Update selected element object from state
  if (
    selectedElement &&
    typeof selectedElement.index === "number" &&
    Array.isArray(pageState?.hosted_page?.page_content)
  ) {
    selectedElement.object =
      pageState.hosted_page.page_content[selectedElement.index];
  }

  const [activeTab, setActiveTab] = useState("properties");
  const [animationDurations, setAnimationDurations] = useState(1);
  const [localProperties, setLocalProperties] = useState({});
  const updateTimeoutRef = useRef(null);
  const lastSelectedElementRef = useRef(null);

  // Initialize local properties when selectedElement changes
  useEffect(() => {
    if (
      selectedElement?.object &&
      selectedElement !== lastSelectedElementRef.current
    ) {
      setLocalProperties(selectedElement.object.object_property || {});
      lastSelectedElementRef.current = selectedElement;
      // setAnimationDurations(
      //   selectedElement?.object?.animation?.properties?.duration?.slice(0, -1) || 1
      // );
    }
  }, [selectedElement]);

  // Helper function to get current keyframe value at current scroll position
  const getCurrentKeyframeValue = useCallback(
    (keyframes, propertyName, defaultValue) => {
      if (!keyframes || !keyframes[propertyName]) {
        return defaultValue;
      }

      const { scrollY = [], value = [] } = keyframes[propertyName];

      if (scrollY.length === 0) {
        return defaultValue;
      }

      // Find the exact match first
      const exactIndex = scrollY.findIndex(
        (scroll) => scroll === scaledScrollY
      );
      if (exactIndex !== -1) {
        return value[exactIndex];
      }

      // If no exact match, interpolate or return closest value
      if (scaledScrollY <= scrollY[0]) {
        return value[0];
      }

      if (scaledScrollY >= scrollY[scrollY.length - 1]) {
        return value[value.length - 1];
      }

      // Find the two points to interpolate between
      for (let i = 0; i < scrollY.length - 1; i++) {
        if (scaledScrollY > scrollY[i] && scaledScrollY < scrollY[i + 1]) {
          const t =
            (scaledScrollY - scrollY[i]) / (scrollY[i + 1] - scrollY[i]);
          const startValue = parseFloat(value[i]) || 0;
          const endValue = parseFloat(value[i + 1]) || 0;
          return startValue + (endValue - startValue) * t;
        }
      }

      return defaultValue;
    },
    [scaledScrollY]
  );

  // Update element object properties function
  const updateElementObjectProperties = useCallback(
    (index, values) => {
      const content = pageState?.hosted_page?.page_content;
      if (!content || !content[index]) {
        console.warn(`Element not found:[${index}]`);
        return;
      }

      // Deep clone to avoid mutating state
      const updatedPageState = JSON.parse(JSON.stringify(pageState));
      const element = updatedPageState.hosted_page.page_content[index];

      // Ensure object_property structure exists
      if (!element.object_property) {
        element.object_property = {};
      }

      // Update object_property with the new values
      element.object_property = {
        ...element.object_property,
        ...values,
      };

      // Dispatch updated state
      dispatch(hostedPageActions.setReducer(updatedPageState.hosted_page));
    },
    [pageState, dispatch]
  );

  // Action functions
  const moveElementUp = (index, level = 1) => {
    dispatch(hostedPageActions.moveUpElementLayer({ index: index, level: level }));
  };

  const moveElementDown = (index, level = 1) => {
    dispatch(hostedPageActions.moveDownElementLayer({ index: index, level: level }));
  };

  const deleteElement = (index) => {
    dispatch(hostedPageActions.deleteElement({ index: index }));
  };

  const toggleElementVisibility = (index) => {
    dispatch(hostedPageActions.toggelLockElement({ index: index }));
  };

  const addExitScroll = (index, exit_scroll = 0) => {
    dispatch(hostedPageActions.updateExitScroll({ index: index, exit_scroll: exit_scroll }));
  };

  // Debounced update function to batch property changes
  const debouncedUpdate = useCallback(
    (elementIndex, updatedProperties) => {
      if (updateTimeoutRef.current) {
        clearTimeout(updateTimeoutRef.current);
      }

      updateTimeoutRef.current = setTimeout(() => {
        if (selectedElement?.index !== undefined) {
          updateElementObjectProperties(elementIndex, updatedProperties);
        }
      }, 150); // 150ms debounce
    },
    [selectedElement, updateElementObjectProperties]
  );

  // Optimized property update function
  const updateElementProperty = useCallback(
    (elementIndex, propertyPath, value) => {
      if (selectedElement?.index === undefined) {
        console.warn("No selected element or invalid index");
        return;
      }

      // Update local state immediately for instant UI feedback
      setLocalProperties((prevProperties) => {
        const updatedProperties = { ...prevProperties };
        const keys = propertyPath.replace("object_property.", "").split(".");
        let current = updatedProperties;

        // Navigate to the nested property
        for (let i = 0; i < keys.length - 1; i++) {
          const key = keys[i];
          if (typeof current[key] !== "object" || current[key] === null) {
            current[key] = {};
          } else {
            current[key] = { ...current[key] }; // Clone nested objects
          }
          current = current[key];
        }

        // Set the final value
        current[keys[keys.length - 1]] = value;

        // Debounce the actual Redux update
        debouncedUpdate(elementIndex, updatedProperties);

        return updatedProperties;
      });
    },
    [selectedElement, debouncedUpdate]
  );

  // Update animation keyframe function
  const updateAnimationKeyframe = useCallback(
    (propertyName, value) => {
      if (selectedElement?.index === undefined) {
        console.warn("No selected element or invalid index");
        return;
      }

      // Create update object for the specific property
      const updateValues = { [propertyName]: value };

      // Call the updateElementAnimation function
      updateElementAnimation(selectedElement.index, updateValues);
    },
    [selectedElement, scaledScrollY, updateElementAnimation]
  );

  // Apply animation preset to selected element
  const applyAnimationPreset = useCallback(
    (presetKey, params) => {
      console.log("applyAnimationPreset",presetKey,params)
      if (!selectedElement?.object) {
        console.warn("No selected element to apply animation to");
        return;
      }
      
      
      dispatch(hostedPageActions.addAnimationProperty({ 
        index: selectedElement.index,
        type: (params?"preset":"scroll"),
        properties: {...params,type:presetKey}
      }));
    },
    [selectedElement, animationDurations, dispatch]
  );

  // Reset states when selectedElement changes
  useEffect(() => {
    if (selectedElement?.object) {
      setActiveTab("properties");
    } else {
      setActiveTab("properties");
    }
  }, [selectedElement]);

  // Cleanup timeout on unmount
  useEffect(() => {
    return () => {
      if (updateTimeoutRef.current) {
        clearTimeout(updateTimeoutRef.current);
      }
    };
  }, []);

  return (
    <div className={styles.sidebar}>
      {/* Header */}
      <div className={styles.header}>
        <h2 className={styles.headerTitle}>Properties</h2>
      </div>

      {/* Tabs */}
      <div className={styles.tabBar}>
        <button
          onClick={() => setActiveTab("properties")}
          className={`${styles.tabButton} ${
            activeTab === "properties" ? styles.tabButtonActive : ""
          }`}
        >
          <Settings size={16} style={{ marginRight: "8px" }} />
          Properties
        </button>
        <button
          onClick={() => setActiveTab("animations")}
          className={`${styles.tabButton} ${
            activeTab === "animations" ? styles.tabButtonActive : ""
          }`}
        >
          <Play size={16} style={{ marginRight: "8px" }} />
          Animations
        </button>
        <button
          onClick={() => setActiveTab("exitAnimations")}
          className={`${styles.tabButton} ${
            activeTab === "exitAnimations" ? styles.tabButtonActive : ""
          }`}
        >
          <LogOut size={16} style={{ marginRight: "8px" }} />
          Exit Animations
        </button>
      </div>

      {/* Content */}
      <div className={styles.content}>
        {activeTab === "properties" && selectedElement?.object ? (
          <div style={{ display: "flex", flexDirection: "column", gap: "24px" }}>
            {/* Layer Controls */}
            <LayerControls
              selectedElement={selectedElement}
              setSelectedElement={setSelectedElement}
              moveElementUp={moveElementUp}
              moveElementDown={moveElementDown}
              deleteElement={deleteElement}
              toggleElementVisibility={toggleElementVisibility}
              isElementLocked={isElementLocked}
            />

            {/* Transform Properties */}
            {!isElementLocked && (
              <TransformProperties
                selectedElement={selectedElement}
                scaledScrollY={scaledScrollY}
                getCurrentKeyframeValue={getCurrentKeyframeValue}
                updateAnimationKeyframe={updateAnimationKeyframe}
              />
            )}

            {/* Element Properties */}
            {!isElementLocked && (
              <div>
                <ShapeProperties
                  selectedElement={selectedElement}
                  localProperties={localProperties}
                  updateElementProperty={updateElementProperty}
                />
                <TextProperties
                  selectedElement={selectedElement}
                  localProperties={localProperties}
                  updateElementProperty={updateElementProperty}
                />
              </div>
            )}
          </div>
        ) : activeTab === "properties" && !selectedElement?.object ? (
          <div
            style={{
              textAlign: "center",
              color: "#6b7280",
              marginTop: "32px",
            }}
          >
            <Settings
              size={48}
              style={{
                margin: "0 auto 12px auto",
                color: "#d1d5db",
                display: "block",
              }}
            />
            <p style={{ margin: 0 }}>
              Select an element to edit its properties
            </p>
          </div>
        ) : activeTab === "animations" ? (
          <AnimationPresets
            selectedElement={selectedElement}
            applyAnimationPreset={applyAnimationPreset}
          />
        ) : (
          <ExitAnimationPresets
            selectedElement={selectedElement}
            addExitScroll={addExitScroll}
            scaledScrollY={scaledScrollY}
            animationDurations={animationDurations}
          />
        )}
      </div>
    </div>
  );
};

export default ElementEditorSidebar;