import BaseElement from "./canvas_base_element";

export default class ImageElement extends BaseElement {
  constructor(metadata, scale = 1, canvas_width = 100, canvas_height = 100) {
    super(metadata, scale, canvas_width, canvas_height);
    const baseUrl = process.env.REACT_APP_ASSET_BASE_URL || "";

    this.img = new Image();
    this.img.crossOrigin = "anonymous";

    // Load image
    this.img.onload = () => {
      this.loaded = true;
      // console.log(`Image loaded successfully: ${this.img.src}`);
    };

    this.img.onerror = (error) => {
      this.loadError = true;
      console.error(`Failed to load image at ${this.img.src}`, error);
      // Create a fallback colored rectangle for visualization
      this.createFallbackCanvas();
    };

    // Fix the image source construction
    const imageUrl = `${baseUrl}${this.metadata.object_property.file}`;

    // console.log("Attempting to load image from:", imageUrl);
    this.img.src = imageUrl;
  } // constructor

  createFallbackCanvas() {
    // Create a fallback canvas with a colored rectangle
    const canvas = document.createElement("canvas");
    canvas.width = 200;
    canvas.height = 200;
    const ctx = canvas.getContext("2d");

    // Draw a gradient rectangle as fallback
    const gradient = ctx.createLinearGradient(0, 0, 200, 200);
    gradient.addColorStop(0, "#4CAF50");
    gradient.addColorStop(1, "#45a049");

    ctx.fillStyle = gradient;
    ctx.fillRect(0, 0, 200, 200);

    ctx.fillStyle = "white";
    ctx.font = "16px Arial";
    ctx.textAlign = "center";
    ctx.fillText("Fallback", 100, 90);
    ctx.fillText("Image", 100, 110);

    this.img = canvas;
    this.loaded = true;
    // console.log('Created fallback image');
  }

  /**
   * Renders the image element to the canvas
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   */
  draw(ctx) {
    // Check if image is ready (either loaded successfully or fallback created)
    if (!this.loaded) {
      return;
    }

    ctx.save();
    const { x, y, width, height, rotation, opacity } = this.currentProps;

    // Set opacity
    ctx.globalAlpha = Math.max(0, Math.min(1, opacity));

    // Move to image center and apply transformations
    const halfWidth = width / 2;
    const halfHeight = height / 2;
    ctx.translate(x + halfWidth, y + halfHeight);
    ctx.rotate(rotation);

    try {
      // Draw the image centered
      ctx.drawImage(this.img, -halfWidth, -halfHeight, width, height);

      // Debug: Draw a // border around the image to see its bounds
      // ctx.strokeStyle = "red";
      // ctx.lineWidth = 2;
      // ctx.strokeRect(-halfWidth, -halfHeight, width, height);
      // ----- debug -----

      // console.log(`Drew image at: x=${Math.round(x)}, y=${Math.round(y)}, w=${Math.round(width)}, h=${Math.round(height)}, scale=${this.scale}`);
    } catch (error) {
      console.error("Error drawing image:", error);
      // Draw a fallback rectangle
      ctx.fillStyle = `rgba(255, 0, 0, ${opacity})`;
      ctx.fillRect(-halfWidth, -halfHeight, width, height);
    }

    ctx.restore();
  }
}
