import { useSelector } from "react-redux";
import { useState, useRef, useEffect, useMemo, useCallback, memo } from "react";
import React from "react";

import Image from "../../components/image/image.jsx";
import Text from "../../components/text_component/text_component.jsx";
import ShapeGenerator from "../../components/shape_genrator/shape_genrator.jsx";
import Position from "../../components/position/position.jsx";
import CountDown from "../../components/countdown/countdown.jsx";

import styles from "./hosted_page.module.css";

// Mobile-optimized scroll function with better easing and frame rate
function scrollToPosition(element, target, duration = 2000) {
  const start = element.scrollTop;
  const change = target - start;
  const startTime = performance.now();

  function easeOutQuart(t) {
    return 1 - Math.pow(1 - t, 4);
  }

  function animateScroll(currentTime) {
    const elapsed = currentTime - startTime;
    const progress = Math.min(elapsed / duration, 1);

    const easedProgress = easeOutQuart(progress);
    const newScrollTop = start + change * easedProgress;

    element.scrollTop = Math.round(newScrollTop);

    if (progress < 1) {
      requestAnimationFrame(animateScroll);
    }
  }

  requestAnimationFrame(animateScroll);
}

// Highly optimized element renderer
const ElementRenderer = memo(
  ({ elementMetaData, index, baseUrl, scaledScrollY, scale }) => {
    // Pre-calculate values to avoid repeated property access
    const exitScroll = elementMetaData.exit_scroll;
    const entryScroll =
      elementMetaData.animation?.keyframes?.y?.scrollY?.[0] || 0;

    // Simple visibility check - avoid complex memoization
    if (
      exitScroll !== null &&
      exitScroll !== undefined &&
      scaledScrollY > exitScroll
    )
      return null;
    if (scaledScrollY < entryScroll) return null;

    const { object_type, object_property } = elementMetaData;
    const keyframes = elementMetaData.animation?.keyframes || {};

    // Direct component rendering without memoization overhead
    let renderedComponent;
    switch (object_type) {
      case "image":
        renderedComponent = (
          <Image file={`${baseUrl}${object_property?.file || ""}`} />
        );
        break;
      case "text":
        renderedComponent = (
          <Text
            text={object_property?.text || ""}
            style={{ ...(object_property?.style || {}), position: "static" }}
          />
        );
        break;
      case "shape":
        renderedComponent = (
          <ShapeGenerator
            selectedShape={object_property?.shape || "circle"}
            color={object_property?.color || "#FCEFDA"}
            gradientEnabled={object_property?.gradient_enabled || false}
            gradientColor2={object_property?.color2 || "#ef4444"}
            gradientDirection={object_property?.direction || "45deg"}
            gradient={object_property?.shadow || false}
            shadow={object_property?.shadow || false}
            shadowColor={object_property?.shadowColor || "#f4ede2ff"}
            shadowBlur={object_property?.shadowBlur || 0}
            shadowOffsetX={object_property?.shadowOffsetX || 0}
            shadowOffsetY={object_property?.shadowOffsetY || 0}
          />
        );
        break;
      case "countdown":
        renderedComponent = (
          <CountDown
            targetDate={object_property?.target || ""}
            style={object_property?.style || {}}
          />
        );
        break;
      default:
        return null;
    }

    return (
      <Position
        x={`${keyframes.x?.[0] || 0}px`}
        y={`${keyframes.y?.[0] || 0}px`}
        height={`${keyframes.height?.[0] || 100}px`}
        width={`${keyframes.width?.[0] || 100}px`}
        rotation={keyframes.rotation?.[0] || 0}
        opacity={keyframes.opacity?.[0] || 1}
        animation={elementMetaData.animation}
        layer={elementMetaData.layer}
        scale={scale}
        scaledScrollY={scaledScrollY}
        passStyleFlag={true}
      >
        {renderedComponent}
      </Position>
    );
  }
);

export default function HostedPage() {
  const [scaledScrollY, setScaledScrollY] = useState(0);
  const [scale, setScale] = useState(null);
  const [visibleIndices, setVisibleIndices] = useState(new Set());

  const baseUrl = process.env.REACT_APP_ASSET_BASE_URL;
  const containerRef = useRef(null);
  const canvasRef = useRef(null);

  // Cached values to avoid selector re-calculations
  const pageContent = useSelector((state) => state?.hosted_page?.page_content);
  const pageSize = useSelector((state) => state?.hosted_page?.page_size);
  const pageScrollHeight = useSelector(
    (state) => state?.hosted_page?.page_scroll_height
  );

  // Pre-calculate element bounds for viewport culling (only once)
  const elementBounds = useMemo(() => {
    if (!pageContent || !Array.isArray(pageContent)) return [];

    return pageContent.map((elementMetaData, index) => {
      const y = elementMetaData.animation?.keyframes?.y?.[0] || 0;
      const height = elementMetaData.animation?.keyframes?.height?.[0] || 100;
      const entryScroll =
        elementMetaData.animation?.keyframes?.y?.scrollY?.[0] || 0;
      const exitScroll = elementMetaData.exit_scroll;

      return {
        index,
        top: y,
        bottom: y + height,
        entryScroll,
        exitScroll,
        elementMetaData,
      };
    });
  }, [pageContent]);

  // Throttled viewport culling - only update every 16ms max
  const updateVisibleElements = useCallback(() => {
    if (!elementBounds.length || !scale) return;

    const viewportHeight = window.innerHeight / scale;
    const buffer = 500; // Smaller buffer for better performance
    const viewTop = scaledScrollY - buffer;
    const viewBottom = scaledScrollY + viewportHeight + buffer;

    const newVisibleIndices = new Set();

    // Simple loop instead of complex array methods
    for (let i = 0; i < elementBounds.length; i++) {
      const bounds = elementBounds[i];

      // Scroll-based visibility check
      const scrollVisible =
        scaledScrollY >= bounds.entryScroll &&
        (bounds.exitScroll === null ||
          bounds.exitScroll === undefined ||
          scaledScrollY <= bounds.exitScroll);

      // Viewport visibility check
      const viewportVisible =
        bounds.bottom >= viewTop && bounds.top <= viewBottom;

      if (scrollVisible || viewportVisible) {
        newVisibleIndices.add(i);
      }
    }

    // Only update if changed
    if (
      newVisibleIndices.size !== visibleIndices.size ||
      ![...newVisibleIndices].every((i) => visibleIndices.has(i))
    ) {
      setVisibleIndices(newVisibleIndices);
    }
  }, [elementBounds, scaledScrollY, scale, visibleIndices]);

  // Throttled scroll handler
  const lastScrollUpdate = useRef(0);
  useEffect(() => {
    if (!containerRef.current || !scale) return;

    function handleScroll() {
      const now = performance.now();
      if (now - lastScrollUpdate.current < 16) return; // 60fps throttle

      lastScrollUpdate.current = now;

      const scrollTop = containerRef.current?.scrollTop || 0;
      const newScaledScrollY = scrollTop / scale;

      setScaledScrollY(newScaledScrollY);
    }

    const ref = containerRef.current;
    ref.addEventListener("scroll", handleScroll, { passive: true });

    return () => ref.removeEventListener("scroll", handleScroll);
  }, [scale]);

  // Update visible elements when scroll changes (throttled)
  const lastVisibilityUpdate = useRef(0);
  useEffect(() => {
    const now = performance.now();
    if (now - lastVisibilityUpdate.current < 32) return; // 30fps for visibility updates

    lastVisibilityUpdate.current = now;
    updateVisibleElements();
  }, [scaledScrollY, updateVisibleElements]);

  // Scale calculation
  const handleResize = useCallback(() => {
    if (!pageSize) return;

    const scaleWidth = window.innerWidth / pageSize.width;
    const scaleHeight = window.innerHeight / pageSize.height;
    const calculatedScale = Math.min(scaleWidth, scaleHeight);
    setScale(calculatedScale);
  }, [pageSize?.width, pageSize?.height]);

  useEffect(() => {
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [handleResize]);

  // Pre-calculated styles (avoid recalculation on each render)
  const rootViewPortStyle = useMemo(
    () => ({
      width: "100vw",
      height: "100vh",
      overflow: "auto",
      margin: 0,
      padding: 0,
      display: "flex",
      alignItems: "flex-start",
      justifyContent: "center",
      overflowX: "hidden",
      WebkitOverflowScrolling: "touch",
      transform: "translateZ(0)",
    }),
    []
  );

  const scaleWrapperStyle = useMemo(() => {
    if (!pageSize || !scale) return {};
    return {
      width: `${pageSize.width * scale}px`,
      height: `${pageSize.height * scale}px`,
      position: "relative",
    };
  }, [pageSize?.width, pageSize?.height, scale]);

  const canvasStyle = useMemo(() => {
    if (!pageSize || !scale || !pageScrollHeight) return {};
    return {
      transform: `scale(${scale})`,
      transformOrigin: "top left",
      width: `${pageSize.width}px`,
      height: `${pageScrollHeight}px`,
      position: "relative",
      backgroundColor: "transparent",
      willChange: "transform",
      backfaceVisibility: "hidden",
      contain: "layout style paint",
    };
  }, [scale, pageSize?.width, pageScrollHeight]);

  const fixedCanvasStyle = useMemo(
    () => ({
      ...canvasStyle,
      position: "fixed",
      top: "0%",
      left: "0%",
      zIndex: -1000,
    }),
    [canvasStyle]
  );

  if (scale === null) {
    return <div>Loading...</div>;
  }

  // Render only visible elements
  const visibleElements = [];
  for (const index of visibleIndices) {
    const bounds = elementBounds[index];
    if (bounds) {
      visibleElements.push(
        <ElementRenderer
          key={`element-${index}`}
          elementMetaData={bounds.elementMetaData}
          index={index}
          baseUrl={baseUrl}
          scaledScrollY={scaledScrollY}
          scale={scale}
        />
      );
    }
  }

  return (
    <>
      <div
        style={{
          position: "fixed",
          top: "10%",
          left: "10%",
          zIndex: 1000,
          background: "rgba(0,0,0,0.7)",
          color: "white",
          padding: "5px",
        }}
      >
        {scaledScrollY.toFixed(0)} | Visible: {visibleElements.length}/
        {pageContent?.length || 0}
      </div>

      <div
        ref={containerRef}
        id="ROOT_VIEW_PORT"
        className={styles.root_view_port}
        style={rootViewPortStyle}
      >
        <div id="SCALE_WRAPPER" style={scaleWrapperStyle}>
          <div
            ref={canvasRef}
            className={styles.canvas}
            id="CANVAS"
            style={canvasStyle}
          >
            {/* Empty canvas for scroll height */}
          </div>
        </div>
      </div>

      <div style={fixedCanvasStyle}>{visibleElements}</div>
    </>
  );
}
