import BaseElement from "./canvas_base_element.js";

export default class ShapeElement extends BaseElement {
  constructor(metadata, scale = 1, canvas_width = 100, canvas_height = 100) {
    super(metadata, scale, canvas_width, canvas_height);

    this.animationStartTime = Date.now();

    // Shape-specific properties with defaults - mapped to match your data structure
    const objProps = this.metadata.object_property || {};
    this.shapeProps = {
      shapeType: objProps.shape || "rectangle", // Note: "shape" in your data, not "shapeType"
      color: objProps.color || "#3b82f6",
      gradientEnabled: objProps.gradient_enabled || false, // Note: underscore in your data
      gradientColor2: objProps.color2 || "#ef4444", // Note: "color2" in your data
      gradientDirection: this.parseDirection(objProps.direction) || 45, // Convert "45deg" to degrees
      shadow: objProps.shadow || false,
      shadowColor: objProps.shadowColor || "rgba(0, 0, 0, 0.3)",
      shadowBlur: objProps.shadowBlur || 10,
      shadowOffsetX: objProps.shadowOffsetX || 5,
      shadowOffsetY: objProps.shadowOffsetY || 5,
      strokeWidth: objProps.strokeWidth || 0,
      strokeColor: objProps.strokeColor || "#000000",
    };

    // console.log(
    //   `Shape element created: ${this.shapeProps.shapeType}`,
    //   this.shapeProps
    // );
  }

  // Helper function to parse direction string like "45deg" to number
  parseDirection(direction) {
    if (typeof direction === "string") {
      return parseFloat(direction.replace("deg", "")) || 45;
    }
    return typeof direction === "number" ? direction : 45;
  }

  /**
   * Creates a gradient for the shape
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {number} width - Shape width
   * @param {number} height - Shape height
   * @returns {CanvasGradient|string} - Gradient or solid color
   */
  createGradient(ctx, width, height) {
    if (!this.shapeProps.gradientEnabled) {
      return this.shapeProps.color;
    }

    const angle = (this.shapeProps.gradientDirection * Math.PI) / 180;
    const diagonal = Math.sqrt(width * width + height * height);
    const x1 = width / 2 - (Math.cos(angle) * diagonal) / 2;
    const y1 = height / 2 - (Math.sin(angle) * diagonal) / 2;
    const x2 = width / 2 + (Math.cos(angle) * diagonal) / 2;
    const y2 = height / 2 + (Math.sin(angle) * diagonal) / 2;

    const gradient = ctx.createLinearGradient(x1, y1, x2, y2);
    gradient.addColorStop(0, this.shapeProps.color);
    gradient.addColorStop(1, this.shapeProps.gradientColor2);
    return gradient;
  }

  /**
   * Draws different shape types
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {number} width - Shape width
   * @param {number} height - Shape height
   * @param {string|CanvasGradient} fillStyle - Fill color or gradient
   */
  drawShape(ctx, width, height, fillStyle) {
    ctx.fillStyle = fillStyle;
    if (this.shapeProps.strokeWidth > 0) {
      ctx.strokeStyle = this.shapeProps.strokeColor;
      ctx.lineWidth = this.shapeProps.strokeWidth;
    }

    const halfWidth = width / 2;
    const halfHeight = height / 2;

    switch (this.shapeProps.shapeType) {
      case "circle":
        const radius = Math.min(halfWidth, halfHeight);
        ctx.beginPath();
        ctx.arc(0, 0, radius, 0, 2 * Math.PI);
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "square":
        const size = Math.min(width, height);
        const halfSize = size / 2;
        ctx.fillRect(-halfSize, -halfSize, size, size);
        if (this.shapeProps.strokeWidth > 0) {
          ctx.strokeRect(-halfSize, -halfSize, size, size);
        }
        break;

      case "rectangle":
        ctx.fillRect(-halfWidth, -halfHeight, width, height);
        if (this.shapeProps.strokeWidth > 0) {
          ctx.strokeRect(-halfWidth, -halfHeight, width, height);
        }
        break;

      case "triangle":
        ctx.beginPath();
        ctx.moveTo(0, -halfHeight);
        ctx.lineTo(-halfWidth, halfHeight);
        ctx.lineTo(halfWidth, halfHeight);
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "diamond":
        ctx.beginPath();
        ctx.moveTo(0, -halfHeight);
        ctx.lineTo(halfWidth, 0);
        ctx.lineTo(0, halfHeight);
        ctx.lineTo(-halfWidth, 0);
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "hexagon":
        ctx.beginPath();
        for (let i = 0; i < 6; i++) {
          const angle = (i * Math.PI) / 3;
          const x = halfWidth * Math.cos(angle);
          const y = halfHeight * Math.sin(angle);
          if (i === 0) ctx.moveTo(x, y);
          else ctx.lineTo(x, y);
        }
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "pentagon":
        ctx.beginPath();
        for (let i = 0; i < 5; i++) {
          const angle = (i * 2 * Math.PI) / 5 - Math.PI / 2;
          const x = halfWidth * Math.cos(angle);
          const y = halfHeight * Math.sin(angle);
          if (i === 0) ctx.moveTo(x, y);
          else ctx.lineTo(x, y);
        }
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "octagon":
        ctx.beginPath();
        for (let i = 0; i < 8; i++) {
          const angle = (i * Math.PI) / 4;
          const x = halfWidth * Math.cos(angle);
          const y = halfHeight * Math.sin(angle);
          if (i === 0) ctx.moveTo(x, y);
          else ctx.lineTo(x, y);
        }
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "star":
        ctx.beginPath();
        const spikes = 5;
        const outerRadius = Math.min(halfWidth, halfHeight);
        const innerRadius = outerRadius * 0.5;
        let rot = -Math.PI / 2;
        let x = 0;
        let y = 0;
        const step = Math.PI / spikes;

        ctx.moveTo(0, -outerRadius);
        for (let i = 0; i < spikes; i++) {
          x = Math.cos(rot) * outerRadius;
          y = Math.sin(rot) * outerRadius;
          ctx.lineTo(x, y);
          rot += step;

          x = Math.cos(rot) * innerRadius;
          y = Math.sin(rot) * innerRadius;
          ctx.lineTo(x, y);
          rot += step;
        }
        ctx.lineTo(0, -outerRadius);
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "heart":
        const heartWidth = width * 0.8;
        const heartHeight = height * 0.8;
        ctx.beginPath();
        ctx.moveTo(0, heartHeight / 4);
        ctx.bezierCurveTo(
          -heartWidth / 2,
          -heartHeight / 2,
          -heartWidth,
          -heartHeight / 4,
          -heartWidth / 2,
          0
        );
        ctx.bezierCurveTo(
          -heartWidth / 2,
          -heartHeight / 4,
          0,
          -heartHeight / 4,
          0,
          heartHeight / 4
        );
        ctx.bezierCurveTo(
          0,
          -heartHeight / 4,
          heartWidth / 2,
          -heartHeight / 4,
          heartWidth / 2,
          0
        );
        ctx.bezierCurveTo(
          heartWidth,
          -heartHeight / 4,
          heartWidth / 2,
          -heartHeight / 2,
          0,
          heartHeight / 4
        );
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "arrow":
        const arrowWidth = width * 0.6;
        const arrowHeight = height * 0.3;
        ctx.beginPath();
        ctx.moveTo(-halfWidth, -arrowHeight / 2);
        ctx.lineTo(arrowWidth / 2, -arrowHeight / 2);
        ctx.lineTo(arrowWidth / 2, -halfHeight);
        ctx.lineTo(halfWidth, 0);
        ctx.lineTo(arrowWidth / 2, halfHeight);
        ctx.lineTo(arrowWidth / 2, arrowHeight / 2);
        ctx.lineTo(-halfWidth, arrowHeight / 2);
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "trapezoid":
        ctx.beginPath();
        const topWidth = width * 0.6;
        ctx.moveTo(-topWidth / 2, -halfHeight);
        ctx.lineTo(topWidth / 2, -halfHeight);
        ctx.lineTo(halfWidth, halfHeight);
        ctx.lineTo(-halfWidth, halfHeight);
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "parallelogram":
        ctx.beginPath();
        const skew = width * 0.3;
        ctx.moveTo(-halfWidth + skew, -halfHeight);
        ctx.lineTo(halfWidth + skew, -halfHeight);
        ctx.lineTo(halfWidth - skew, halfHeight);
        ctx.lineTo(-halfWidth - skew, halfHeight);
        ctx.closePath();
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      case "ellipse":
        ctx.beginPath();
        ctx.ellipse(0, 0, halfWidth, halfHeight, 0, 0, 2 * Math.PI);
        ctx.fill();
        if (this.shapeProps.strokeWidth > 0) ctx.stroke();
        break;

      default:
        // Default to rectangle
        ctx.fillRect(-halfWidth, -halfHeight, width, height);
        if (this.shapeProps.strokeWidth > 0) {
          ctx.strokeRect(-halfWidth, -halfHeight, width, height);
        }
        break;
    }
  }

  /**
   * Renders the shape element to the canvas
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   */
  draw(ctx) {
    ctx.save();
    const { x, y, width, height, rotation, opacity } = this.currentProps;

    // Set opacity
    ctx.globalAlpha = Math.max(0, Math.min(1, opacity));

    // Move to shape center and apply transformations
    const halfWidth = width / 2;
    const halfHeight = height / 2;
    ctx.translate(x + halfWidth, y + halfHeight);
    ctx.rotate(rotation);

    try {
      // Apply shadow if enabled
      if (this.shapeProps.shadow) {
        ctx.shadowColor = this.shapeProps.shadowColor;
        ctx.shadowBlur = this.shapeProps.shadowBlur;
        ctx.shadowOffsetX = this.shapeProps.shadowOffsetX;
        ctx.shadowOffsetY = this.shapeProps.shadowOffsetY;
      }

      // Create gradient or solid color
      const fillStyle = this.createGradient(ctx, width, height);

      // Draw the shape
      this.drawShape(ctx, width, height, fillStyle);

      // Debug: Draw a border around the shape to see its bounds (uncomment for debugging)
      // ctx.shadowColor = 'transparent';
      // ctx.strokeStyle = "red";
      // ctx.lineWidth = 1;
      // ctx.strokeRect(-halfWidth, -halfHeight, width, height);

      // console.log(`Drew ${this.shapeProps.shapeType} at: x=${Math.round(x)}, y=${Math.round(y)}, w=${Math.round(width)}, h=${Math.round(height)}, opacity=${opacity.toFixed(2)}`);
    } catch (error) {
      console.error("Error drawing shape:", error);
      // Draw a fallback rectangle
      ctx.fillStyle = `rgba(255, 0, 0, ${opacity})`;
      ctx.fillRect(-halfWidth, -halfHeight, width, height);
    }

    ctx.restore();
  }
}
