import React from "react";

const TransformProperties = ({
  selectedElement,
  scaledScrollY,
  getCurrentKeyframeValue,
  updateAnimationKeyframe
}) => {
  if (!selectedElement?.object) {
    return null;
  }

  const keyframes = selectedElement.object.animation?.keyframes || {};
  
  // Get current values at current scroll position
  const currentWidth = getCurrentKeyframeValue(keyframes, "width", 100);
  const currentHeight = getCurrentKeyframeValue(keyframes, "height", 100);
  const currentRotation = getCurrentKeyframeValue(keyframes, "rotation", 0);
  const currentOpacity = getCurrentKeyframeValue(keyframes, "opacity", 1);
  const currentX = getCurrentKeyframeValue(keyframes, "x", 0);
  const currentY = getCurrentKeyframeValue(keyframes, "y", 0);
  return (
    <div style={{ display: "flex", flexDirection: "column", gap: "16px" }}>
      <h4
        style={{
          fontSize: "14px",
          fontWeight: "600",
          color: "#374151",
          marginBottom: "12px",
          marginTop: 0,
          borderBottom: "1px solid #e5e7eb",
          paddingBottom: "8px",
        }}
      >
        Transform Properties
      </h4>
      {/* Position Controls */}
      <div style={{ display: "grid", gap: "12px" }}>
        <div>
          <label
            style={{
              display: "block",
              fontSize: "14px",
              fontWeight: "500",
              marginBottom: "8px",
              color: "#374151",
            }}
          >
            X Position
          </label>
          <div style={{ display: "flex", alignItems: "center", gap: "8px" }}>
            <input
              type="number"
              value={Math.round(currentX)}
              onChange={(e) =>
                updateAnimationKeyframe(
                  "x",
                  parseFloat(e.target.value) || 0
                )
              }
              style={{
                flex: 1,
                padding: "8px",
                border: "1px solid #d1d5db",
                borderRadius: "4px",
                fontSize: "14px",
                textAlign: "center",
              }}
            />
            <span style={{ fontSize: "12px", color: "#6b7280" }}>px</span>
          </div>
        </div>

        <div>
          <label
            style={{
              display: "block",
              fontSize: "14px",
              fontWeight: "500",
              marginBottom: "8px",
              color: "#374151",
            }}
          >
            Y Position
          </label>
          <div style={{ display: "flex", alignItems: "center", gap: "8px" }}>
            <input
              type="number"
              value={Math.round(currentY-scaledScrollY)}
              onChange={(e) =>
                updateAnimationKeyframe(
                  "y",
                  parseFloat(e.target.value)+scaledScrollY || 0
                )
              }
              style={{
                flex: 1,
                padding: "8px",
                border: "1px solid #d1d5db",
                borderRadius: "4px",
                fontSize: "14px",
                textAlign: "center",
              }}
            />
            <span style={{ fontSize: "12px", color: "#6b7280" }}>px</span>
          </div>
        </div>
      </div>

      {/* Size Controls */}
      <div style={{ display: "grid", gap: "12px" }}>
        <div>
          <label
            style={{
              display: "block",
              fontSize: "14px",
              fontWeight: "500",
              marginBottom: "8px",
              color: "#374151",
            }}
          >
            Width
          </label>
          <div style={{ display: "flex", alignItems: "center", gap: "8px" }}>
            <input
              type="number"
              value={Math.round(currentWidth)}
              onChange={(e) =>
                updateAnimationKeyframe(
                  "width",
                  parseFloat(e.target.value) || 0
                )
              }
              style={{
                flex: 1,
                padding: "8px",
                border: "1px solid #d1d5db",
                borderRadius: "4px",
                fontSize: "14px",
                textAlign: "center",
              }}
              min={1}
            />
            <span style={{ fontSize: "12px", color: "#6b7280" }}>px</span>
          </div>
        </div>

        <div>
          <label
            style={{
              display: "block",
              fontSize: "14px",
              fontWeight: "500",
              marginBottom: "8px",
              color: "#374151",
            }}
          >
            Height
          </label>
          <div style={{ display: "flex", alignItems: "center", gap: "8px" }}>
            <input
              type="number"
              value={Math.round(currentHeight)}
              onChange={(e) =>
                updateAnimationKeyframe(
                  "height",
                  parseFloat(e.target.value) || 0
                )
              }
              style={{
                flex: 1,
                padding: "8px",
                border: "1px solid #d1d5db",
                borderRadius: "4px",
                fontSize: "14px",
                textAlign: "center",
              }}
              min={1}
            />
            <span style={{ fontSize: "12px", color: "#6b7280" }}>px</span>
          </div>
        </div>
      </div>

      {/* Rotation Control */}
      <div>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
            color: "#374151",
          }}
        >
          Rotation
        </label>
        <div style={{ display: "flex", alignItems: "center", gap: "12px" }}>
          <input
            type="range"
            min="0"
            max="360"
            value={Math.round(currentRotation) % 360}
            onChange={(e) =>
              updateAnimationKeyframe(
                "rotation",
                parseFloat(e.target.value) || 0
              )
            }
            style={{
              flex: 1,
              height: "6px",
              background: "#e5e7eb",
              borderRadius: "3px",
              outline: "none",
              cursor: "pointer",
            }}
          />
          <div
            style={{
              display: "flex",
              alignItems: "center",
              gap: "4px",
              minWidth: "80px",
            }}
          >
            <input
              type="number"
              value={Math.round(currentRotation) % 360}
              onChange={(e) =>
                updateAnimationKeyframe(
                  "rotation",
                  parseFloat(e.target.value) || 0
                )
              }
              style={{
                width: "50px",
                padding: "4px",
                border: "1px solid #d1d5db",
                borderRadius: "4px",
                fontSize: "12px",
                textAlign: "center",
              }}
              min={0}
              max={360}
            />
            <span style={{ fontSize: "12px", color: "#6b7280" }}></span>
          </div>
        </div>
      </div>

      {/* Opacity Control */}
      <div>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
            color: "#374151",
          }}
        >
          Opacity
        </label>
        <div style={{ display: "flex", alignItems: "center", gap: "12px" }}>
          <input
            type="range"
            min="0"
            max="1"
            step="0.01"
            value={currentOpacity}
            onChange={(e) =>
              updateAnimationKeyframe(
                "opacity",
                parseFloat(e.target.value) || 0
              )
            }
            style={{
              flex: 1,
              height: "6px",
              background: "#e5e7eb",
              borderRadius: "3px",
              outline: "none",
              cursor: "pointer",
            }}
          />
          <div
            style={{
              display: "flex",
              alignItems: "center",
              gap: "4px",
              minWidth: "60px",
            }}
          >
            <span
              style={{
                fontSize: "12px",
                color: "#374151",
                fontWeight: "500",
                minWidth: "35px",
                textAlign: "center",
              }}
            >
              {Math.round(currentOpacity * 100)}%
            </span>
          </div>
        </div>
      </div>

      {/* Current Scroll Position Info */}
      <div
        style={{
          marginTop: "8px",
          padding: "8px 12px",
          backgroundColor: "#f3f4f6",
          borderRadius: "4px",
          fontSize: "12px",
          color: "#6b7280",
        }}
      >
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <span>Current Scroll:</span>
          <span style={{ fontWeight: "500" }}>{scaledScrollY}px</span>
        </div>
        <div style={{ marginTop: "4px", fontSize: "11px" }}>
          Values are set at current scroll position
        </div>
      </div>
    </div>
  );
};

export default TransformProperties;