import React from "react";
import { Move, Eye, EyeOff, Trash2, Lock, LockOpen } from "lucide-react";

const LayerControls = ({
  selectedElement,
  setSelectedElement,
  moveElementUp,
  moveElementDown,
  deleteElement,
  toggleElementVisibility,
  isElementLocked
}) => {
  return (
    <div>
      {/* Lock/Unlock Status */}
      <div
        style={{
          fontSize: "14px",
          fontWeight: "600",
          color: "#374151",
          marginBottom: "12px",
          marginTop: 0,
          borderBottom: "1px solid #e5e7eb",
          paddingBottom: "8px",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          cursor: "pointer",
          backgroundColor: isElementLocked ? "#fef2f2" : "#f0fdf4",
          padding: "8px",
          borderRadius: "6px",
          border: isElementLocked ? "1px solid #f87171" : "1px solid #86efac",
        }}
        onClick={() => toggleElementVisibility(selectedElement.index)}
      >
        {isElementLocked ? (
          <>
            <Lock size={16} style={{ marginRight: "8px", color: "#dc2626" }} />
            <span style={{ color: "#dc2626" }}>Element Locked</span>
          </>
        ) : (
          <>
            <LockOpen size={16} style={{ marginRight: "8px", color: "#059669" }} />
            <span style={{ color: "#059669" }}>Element Unlocked</span>
          </>
        )}
      </div>

      <h4
        style={{
          fontSize: "14px",
          fontWeight: "600",
          color: "#374151",
          marginBottom: "12px",
          marginTop: 0,
          borderBottom: "1px solid #e5e7eb",
          paddingBottom: "8px",
        }}
      >
        Layer Controls
      </h4>
      
      {/* Basic Move Controls */}
      <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: "8px", marginBottom: "12px" }}>
        <button
          onClick={() => moveElementUp(selectedElement.index)}
          style={{
            padding: "8px 12px",
            backgroundColor: "#f3f4f6",
            border: "1px solid #d1d5db",
            borderRadius: "6px",
            fontSize: "12px",
            fontWeight: "500",
            color: "#374151",
            cursor: "pointer",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            gap: "6px",
            transition: "all 0.2s",
          }}
          onMouseOver={(e) => {
            e.target.style.backgroundColor = "#e5e7eb";
            e.target.style.borderColor = "#9ca3af";
          }}
          onMouseOut={(e) => {
            e.target.style.backgroundColor = "#f3f4f6";
            e.target.style.borderColor = "#d1d5db";
          }}
        >
          <Move size={14} />
          Move Up
        </button>
        
        <button
          onClick={() => moveElementDown(selectedElement.index)}
          style={{
            padding: "8px 12px",
            backgroundColor: "#f3f4f6",
            border: "1px solid #d1d5db",
            borderRadius: "6px",
            fontSize: "12px",
            fontWeight: "500",
            color: "#374151",
            cursor: "pointer",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            gap: "6px",
            transition: "all 0.2s",
          }}
          onMouseOver={(e) => {
            e.target.style.backgroundColor = "#e5e7eb";
            e.target.style.borderColor = "#9ca3af";
          }}
          onMouseOut={(e) => {
            e.target.style.backgroundColor = "#f3f4f6";
            e.target.style.borderColor = "#d1d5db";
          }}
        >
          <Move size={14} style={{ transform: "rotate(180deg)" }} />
          Move Down
        </button>
      </div>
      
      {/* Front/Back Controls */}
      <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: "8px", marginBottom: "12px" }}>
        <button
          onClick={() => moveElementUp(selectedElement.index, 10)}
          style={{
            padding: "8px 12px",
            backgroundColor: "#eff6ff",
            border: "1px solid #93c5fd",
            borderRadius: "6px",
            fontSize: "12px",
            fontWeight: "500",
            color: "#1e40af",
            cursor: "pointer",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            gap: "6px",
            transition: "all 0.2s",
          }}
          onMouseOver={(e) => {
            e.target.style.backgroundColor = "#dbeafe";
            e.target.style.borderColor = "#60a5fa";
          }}
          onMouseOut={(e) => {
            e.target.style.backgroundColor = "#eff6ff";
            e.target.style.borderColor = "#93c5fd";
          }}
        >
          <Eye size={14} />
          To Front
        </button>
        
        <button
          onClick={() => moveElementDown(selectedElement.index, 10)}
          style={{
            padding: "8px 12px",
            backgroundColor: "#fef3c7",
            border: "1px solid #fbbf24",
            borderRadius: "6px",
            fontSize: "12px",
            fontWeight: "500",
            color: "#92400e",
            cursor: "pointer",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            gap: "6px",
            transition: "all 0.2s",
          }}
          onMouseOver={(e) => {
            e.target.style.backgroundColor = "#fef0c8";
            e.target.style.borderColor = "#f59e0b";
          }}
          onMouseOut={(e) => {
            e.target.style.backgroundColor = "#fef3c7";
            e.target.style.borderColor = "#fbbf24";
          }}
        >
          <EyeOff size={14} />
          To Back
        </button>
      </div>
      
      {/* Delete Button */}
      <button
        onClick={() => {
          deleteElement(selectedElement.index);
          setSelectedElement({ index: null, object: null });
        }}
        style={{
          width: "100%",
          padding: "10px 12px",
          backgroundColor: "#fef2f2",
          border: "1px solid #f87171",
          borderRadius: "6px",
          fontSize: "12px",
          fontWeight: "500",
          color: "#dc2626",
          cursor: "pointer",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          gap: "6px",
          transition: "all 0.2s",
        }}
        onMouseOver={(e) => {
          e.target.style.backgroundColor = "#fee2e2";
          e.target.style.borderColor = "#ef4444";
        }}
        onMouseOut={(e) => {
          e.target.style.backgroundColor = "#fef2f2";
          e.target.style.borderColor = "#f87171";
        }}
      >
        <Trash2 size={14} />
        Delete Element
      </button>
    </div>
  );
};

export default LayerControls;