import React from "react";

const ShapeProperties = ({
  selectedElement,
  localProperties,
  updateElementProperty
}) => {
  if (!selectedElement?.object || selectedElement.object.object_type !== "shape") {
    return null;
  }

  const props = localProperties;
  const elementIndex = selectedElement.index;

  return (
    <div style={{ display: "flex", flexDirection: "column", gap: "16px" }}>
      <h4
        style={{
          fontSize: "14px",
          fontWeight: "600",
          color: "#374151",
          marginBottom: "12px",
          marginTop: 0,
          borderBottom: "1px solid #e5e7eb",
          paddingBottom: "8px",
        }}
      >
        Shape Properties
      </h4>

      {/* Shape Type */}
      <div>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Shape
        </label>
        <select
          value={props.shape || "circle"}
          onChange={(e) =>
            updateElementProperty(
              elementIndex,
              "object_property.shape",
              e.target.value
            )
          }
          style={{
            width: "100%",
            padding: "8px",
            border: "1px solid #d1d5db",
            borderRadius: "4px",
            fontSize: "14px",
          }}
        >
          <option value="circle">Circle</option>
          <option value="rectangle">Rectangle</option>
          <option value="triangle">Triangle</option>
          <option value="polygon">Polygon</option>
        </select>
      </div>

      {/* Color */}
      <div>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Color
        </label>
        <div style={{ display: "flex", gap: "8px" }}>
          <input
            type="color"
            value={props.color || "#FCEFDA"}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.color",
                e.target.value
              )
            }
            style={{
              width: "48px",
              height: "40px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              cursor: "pointer",
            }}
          />
          <input
            type="text"
            value={props.color || "#FCEFDA"}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.color",
                e.target.value
              )
            }
            style={{
              flex: "1",
              padding: "8px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              fontSize: "14px",
            }}
            placeholder="#FCEFDA"
          />
        </div>
      </div>

      {/* Gradient Toggle */}
      <div>
        <label style={{ display: "flex", alignItems: "center", gap: "8px" }}>
          <input
            type="checkbox"
            checked={props.gradient_enabled || false}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.gradient_enabled",
                e.target.checked
              )
            }
            style={{ borderRadius: "4px" }}
          />
          <span style={{ fontSize: "14px", fontWeight: "500" }}>
            Enable Gradient
          </span>
        </label>
      </div>

      {/* Gradient Properties */}
      {props.gradient_enabled && (
        <div
          style={{ display: "flex", flexDirection: "column", gap: "12px" }}
        >
          <div>
            <label
              style={{
                display: "block",
                fontSize: "14px",
                fontWeight: "500",
                marginBottom: "8px",
              }}
            >
              Gradient Color
            </label>
            <div style={{ display: "flex", gap: "8px" }}>
              <input
                type="color"
                value={props.color2 || "#ef4444"}
                onChange={(e) =>
                  updateElementProperty(
                    elementIndex,
                    "object_property.color2",
                    e.target.value
                  )
                }
                style={{
                  width: "48px",
                  height: "40px",
                  border: "1px solid #d1d5db",
                  borderRadius: "4px",
                  cursor: "pointer",
                }}
              />
              <input
                type="text"
                value={props.color2 || "#ef4444"}
                onChange={(e) =>
                  updateElementProperty(
                    elementIndex,
                    "object_property.color2",
                    e.target.value
                  )
                }
                style={{
                  flex: "1",
                  padding: "8px",
                  border: "1px solid #d1d5db",
                  borderRadius: "4px",
                  fontSize: "14px",
                }}
                placeholder="#ef4444"
              />
            </div>
          </div>
          <div>
            <label
              style={{
                display: "block",
                fontSize: "14px",
                fontWeight: "500",
                marginBottom: "8px",
              }}
            >
              Gradient Direction
            </label>
            <input
              type="text"
              value={props.direction || "45deg"}
              onChange={(e) =>
                updateElementProperty(
                  elementIndex,
                  "object_property.direction",
                  e.target.value
                )
              }
              style={{
                width: "100%",
                padding: "8px",
                border: "1px solid #d1d5db",
                borderRadius: "4px",
                fontSize: "14px",
              }}
              placeholder="45deg"
            />
          </div>
        </div>
      )}

      {/* Shadow Toggle */}
      <div>
        <label style={{ display: "flex", alignItems: "center", gap: "8px" }}>
          <input
            type="checkbox"
            checked={props.shadow || false}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.shadow",
                e.target.checked
              )
            }
            style={{ borderRadius: "4px" }}
          />
          <span style={{ fontSize: "14px", fontWeight: "500" }}>
            Enable Shadow
          </span>
        </label>
      </div>

      {/* Shadow Properties */}
      {props.shadow && (
        <div
          style={{ display: "flex", flexDirection: "column", gap: "12px" }}
        >
          <div>
            <label
              style={{
                display: "block",
                fontSize: "14px",
                fontWeight: "500",
                marginBottom: "8px",
              }}
            >
              Shadow Color
            </label>
            <div style={{ display: "flex", gap: "8px" }}>
              <input
                type="color"
                value={props.shadowColor || "#f4ede2ff"}
                onChange={(e) =>
                  updateElementProperty(
                    elementIndex,
                    "object_property.shadowColor",
                    e.target.value
                  )
                }
                style={{
                  width: "48px",
                  height: "40px",
                  border: "1px solid #d1d5db",
                  borderRadius: "4px",
                  cursor: "pointer",
                }}
              />
              <input
                type="text"
                value={props.shadowColor || "#f4ede2ff"}
                onChange={(e) =>
                  updateElementProperty(
                    elementIndex,
                    "object_property.shadowColor",
                    e.target.value
                  )
                }
                style={{
                  flex: "1",
                  padding: "8px",
                  border: "1px solid #d1d5db",
                  borderRadius: "4px",
                  fontSize: "14px",
                }}
                placeholder="#f4ede2ff"
              />
            </div>
          </div>
          <div
            style={{
              display: "grid",
              gridTemplateColumns: "1fr 1fr 1fr",
              gap: "8px",
            }}
          >
            <div>
              <label
                style={{
                  display: "block",
                  fontSize: "12px",
                  fontWeight: "500",
                  marginBottom: "4px",
                }}
              >
                Blur
              </label>
              <input
                type="number"
                min="0"
                max="50"
                value={props.shadowBlur || 0}
                onChange={(e) =>
                  updateElementProperty(
                    elementIndex,
                    "object_property.shadowBlur",
                    parseInt(e.target.value)
                  )
                }
                style={{
                  width: "100%",
                  padding: "6px",
                  border: "1px solid #d1d5db",
                  borderRadius: "4px",
                  fontSize: "12px",
                }}
              />
            </div>
            <div>
              <label
                style={{
                  display: "block",
                  fontSize: "12px",
                  fontWeight: "500",
                  marginBottom: "4px",
                }}
              >
                Offset X
              </label>
              <input
                type="number"
                min="-50"
                max="50"
                value={props.shadowOffsetX || 0}
                onChange={(e) =>
                  updateElementProperty(
                    elementIndex,
                    "object_property.shadowOffsetX",
                    parseInt(e.target.value)
                  )
                }
                style={{
                  width: "100%",
                  padding: "6px",
                  border: "1px solid #d1d5db",
                  borderRadius: "4px",
                  fontSize: "12px",
                }}
              />
            </div>
            <div>
              <label
                style={{
                  display: "block",
                  fontSize: "12px",
                  fontWeight: "500",
                  marginBottom: "4px",
                }}
              >
                Offset Y
              </label>
              <input
                type="number"
                min="-50"
                max="50"
                value={props.shadowOffsetY || 0}
                onChange={(e) =>
                  updateElementProperty(
                    elementIndex,
                    "object_property.shadowOffsetY",
                    parseInt(e.target.value)
                  )
                }
                style={{
                  width: "100%",
                  padding: "6px",
                  border: "1px solid #d1d5db",
                  borderRadius: "4px",
                  fontSize: "12px",
                }}
              />
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ShapeProperties;