import React, { useState, useRef, useCallback } from "react";
import {
  Type,
  Square,
  Circle,
  Image,
  Download,
  Trash2,
  RotateCw,
  Copy,
  Upload,
  Clipboard,
  Star,
  Heart,
  Triangle,
} from "lucide-react";
import styles from "./hosted_page.module.css";

const CanvaClone = () => {
  const [elements, setElements] = useState([]);
  const [selectedElement, setSelectedElement] = useState(null);
  const [dragElement, setDragElement] = useState(null);
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });
  const [resizeHandle, setResizeHandle] = useState(null);
  const [resizeStart, setResizeStart] = useState({
    x: 0,
    y: 0,
    width: 0,
    height: 0,
  });
  const [canvasSize, setCanvasSize] = useState({ width: 800, height: 600 });
  const [zoom, setZoom] = useState(1);
  const [panOffset, setPanOffset] = useState({ x: 0, y: 0 });
  const [isPanning, setIsPanning] = useState(false);
  const [panStart, setPanStart] = useState({ x: 0, y: 0 });
  const [activeTab, setActiveTab] = useState("elements");
  const [uploadedImages, setUploadedImages] = useState([]);
  const fileInputRef = useRef(null);
  const canvasRef = useRef(null);
  const elementIdCounter = useRef(0);

  const createNewElement = (type, x = 100, y = 100) => {
    const id = `element_${elementIdCounter.current++}`;
    const baseElement = {
      id,
      x,
      y,
      rotation: 0,
    };

    let newElement;
    switch (type) {
      case "text":
        newElement = {
          ...baseElement,
          type: "text",
          content: "Click to edit",
          fontSize: 24,
          color: "#000000",
          fontWeight: "normal",
          width: 200,
          height: 30,
        };
        break;
      case "rectangle":
        newElement = {
          ...baseElement,
          type: "rectangle",
          width: 150,
          height: 100,
          fill: "#3b82f6",
          stroke: "#1e40af",
          strokeWidth: 2,
        };
        break;
      case "circle":
        newElement = {
          ...baseElement,
          type: "circle",
          width: 120,
          height: 120,
          fill: "#ef4444",
          stroke: "#dc2626",
          strokeWidth: 2,
        };
        break;
      case "triangle":
        newElement = {
          ...baseElement,
          type: "triangle",
          width: 120,
          height: 120,
          fill: "#10b981",
          stroke: "#059669",
          strokeWidth: 2,
        };
        break;
      case "star":
        newElement = {
          ...baseElement,
          type: "star",
          width: 120,
          height: 120,
          fill: "#f59e0b",
          stroke: "#d97706",
          strokeWidth: 2,
        };
        break;
      case "heart":
        newElement = {
          ...baseElement,
          type: "heart",
          width: 120,
          height: 120,
          fill: "#ec4899",
          stroke: "#db2777",
          strokeWidth: 2,
        };
        break;
      case "image":
        newElement = {
          ...baseElement,
          type: "image",
          width: 200,
          height: 200,
          src: x, // x parameter contains image src for images
          opacity: 1,
        };
        break;
      default:
        return;
    }

    setElements((prev) => [...prev, newElement]);
    setSelectedElement(id);
  };

  const updateElement = (id, updates) => {
    setElements((prev) =>
      prev.map((el) => (el.id === id ? { ...el, ...updates } : el))
    );
  };

  const deleteElement = (id) => {
    setElements((prev) => prev.filter((el) => el.id !== id));
    setSelectedElement(null);
  };

  const duplicateElement = (id) => {
    const element = elements.find((el) => el.id === id);
    if (element) {
      const newElement = {
        ...element,
        id: `element_${elementIdCounter.current++}`,
        x: element.x + 20,
        y: element.y + 20,
      };
      setElements((prev) => [...prev, newElement]);
    }
  };

  const handleTextEdit = (id, content) => {
    updateElement(id, { content });
  };

  const handleMouseDown = (e, elementId) => {
    e.stopPropagation();
    const element = elements.find((el) => el.id === elementId);
    if (element) {
      const canvasContent = canvasRef.current.querySelector(
        `.${styles.canvasContent}`
      );
      const canvasRect = canvasContent.getBoundingClientRect();
      setDragElement(elementId);
      setDragOffset({
        x: (e.clientX - canvasRect.left) / zoom - element.x,
        y: (e.clientY - canvasRect.top) / zoom - element.y,
      });
      setSelectedElement(elementId);
    }
  };

  const handleCanvasMouseDown = (e) => {
    if (
      e.target === e.currentTarget ||
      e.target.classList.contains(styles.canvasContent)
    ) {
      setSelectedElement(null);

      // Start panning if space key is held or middle mouse button
      if (e.button === 1 || e.ctrlKey || e.metaKey) {
        e.preventDefault();
        setIsPanning(true);
        setPanStart({
          x: e.clientX,
          y: e.clientY,
          offsetX: panOffset.x,
          offsetY: panOffset.y,
        });
      }
    }
  };

  const handleMouseMove = useCallback(
    (e) => {
      if (isPanning) {
        const deltaX = e.clientX - panStart.x;
        const deltaY = e.clientY - panStart.y;
        setPanOffset({
          x: panStart.offsetX + deltaX,
          y: panStart.offsetY + deltaY,
        });
      } else if (dragElement && !resizeHandle) {
        const canvasContent = canvasRef.current.querySelector(
          `.${styles.canvasContent}`
        );
        const canvasRect = canvasContent.getBoundingClientRect();
        const newX = (e.clientX - canvasRect.left) / zoom - dragOffset.x;
        const newY = (e.clientY - canvasRect.top) / zoom - dragOffset.y;

        updateElement(dragElement, { x: newX, y: newY });
      } else if (resizeHandle && dragElement) {
        const canvasContent = canvasRef.current.querySelector(
          `.${styles.canvasContent}`
        );
        const canvasRect = canvasContent.getBoundingClientRect();
        const currentX = (e.clientX - canvasRect.left) / zoom;
        const currentY = (e.clientY - canvasRect.top) / zoom;

        const deltaX = currentX - resizeStart.x;
        const deltaY = currentY - resizeStart.y;

        let newWidth = resizeStart.width;
        let newHeight = resizeStart.height;
        let newX = resizeStart.elementX;
        let newY = resizeStart.elementY;

        switch (resizeHandle) {
          case "nw":
            newWidth = Math.max(20, resizeStart.width - deltaX);
            newHeight = Math.max(20, resizeStart.height - deltaY);
            newX = resizeStart.elementX + (resizeStart.width - newWidth);
            newY = resizeStart.elementY + (resizeStart.height - newHeight);
            break;
          case "ne":
            newWidth = Math.max(20, resizeStart.width + deltaX);
            newHeight = Math.max(20, resizeStart.height - deltaY);
            newY = resizeStart.elementY + (resizeStart.height - newHeight);
            break;
          case "sw":
            newWidth = Math.max(20, resizeStart.width - deltaX);
            newHeight = Math.max(20, resizeStart.height + deltaY);
            newX = resizeStart.elementX + (resizeStart.width - newWidth);
            break;
          case "se":
            newWidth = Math.max(20, resizeStart.width + deltaX);
            newHeight = Math.max(20, resizeStart.height + deltaY);
            break;
          case "n":
            newHeight = Math.max(20, resizeStart.height - deltaY);
            newY = resizeStart.elementY + (resizeStart.height - newHeight);
            break;
          case "s":
            newHeight = Math.max(20, resizeStart.height + deltaY);
            break;
          case "w":
            newWidth = Math.max(20, resizeStart.width - deltaX);
            newX = resizeStart.elementX + (resizeStart.width - newWidth);
            break;
          case "e":
            newWidth = Math.max(20, resizeStart.width + deltaX);
            break;
          default:
            break;
        }

        updateElement(dragElement, {
          x: newX,
          y: newY,
          width: newWidth,
          height: newHeight,
        });
      }
    },
    [
      dragElement,
      dragOffset,
      resizeHandle,
      resizeStart,
      zoom,
      isPanning,
      panStart,
    ]
  );

  const handleMouseUp = useCallback(() => {
    setDragElement(null);
    setResizeHandle(null);
    setIsPanning(false);
  }, []);

  React.useEffect(() => {
    if (dragElement || resizeHandle || isPanning) {
      document.addEventListener("mousemove", handleMouseMove);
      document.addEventListener("mouseup", handleMouseUp);
      return () => {
        document.removeEventListener("mousemove", handleMouseMove);
        document.removeEventListener("mouseup", handleMouseUp);
      };
    }
  }, [dragElement, resizeHandle, isPanning, handleMouseMove, handleMouseUp]);

  const handleResizeStart = (e, handle, elementId) => {
    e.stopPropagation();
    const element = elements.find((el) => el.id === elementId);
    const canvasContent = canvasRef.current.querySelector(
      `.${styles.canvasContent}`
    );
    const canvasRect = canvasContent.getBoundingClientRect();

    setResizeHandle(handle);
    setDragElement(elementId);
    setResizeStart({
      x: (e.clientX - canvasRect.left) / zoom,
      y: (e.clientY - canvasRect.top) / zoom,
      width: element.width,
      height: element.height,
      elementX: element.x,
      elementY: element.y,
    });
  };

  const ResizeHandles = ({ element }) => {
    const handles = [
      { position: "nw", cursor: "nw-resize", top: -4, left: -4 },
      {
        position: "n",
        cursor: "n-resize",
        top: -4,
        left: "50%",
        transform: "translateX(-50%)",
      },
      { position: "ne", cursor: "ne-resize", top: -4, right: -4 },
      {
        position: "e",
        cursor: "e-resize",
        top: "50%",
        right: -4,
        transform: "translateY(-50%)",
      },
      { position: "se", cursor: "se-resize", bottom: -4, right: -4 },
      {
        position: "s",
        cursor: "s-resize",
        bottom: -4,
        left: "50%",
        transform: "translateX(-50%)",
      },
      { position: "sw", cursor: "sw-resize", bottom: -4, left: -4 },
      {
        position: "w",
        cursor: "w-resize",
        top: "50%",
        left: -4,
        transform: "translateY(-50%)",
      },
    ];

    return (
      <>
        {handles.map((handle) => (
          <div
            key={handle.position}
            className={styles.resizeHandle}
            style={{
              top: handle.top,
              left: handle.left,
              right: handle.right,
              bottom: handle.bottom,
              transform: handle.transform,
              cursor: handle.cursor,
              zIndex: 10,
            }}
            onMouseDown={(e) =>
              handleResizeStart(e, handle.position, element.id)
            }
          />
        ))}
      </>
    );
  };

  const handleFileUpload = (e) => {
    const files = Array.from(e.target.files);
    files.forEach((file) => {
      if (file.type.startsWith("image/")) {
        const reader = new FileReader();
        reader.onload = (event) => {
          const newImage = {
            id: `image_${Date.now()}_${Math.random()}`,
            src: event.target.result,
            name: file.name,
          };
          setUploadedImages((prev) => [...prev, newImage]);
        };
        reader.readAsDataURL(file);
      }
    });
    e.target.value = "";
  };

  const handlePaste = useCallback((e) => {
    const items = e.clipboardData?.items;
    if (items) {
      for (let item of items) {
        if (item.type.startsWith("image/")) {
          const file = item.getAsFile();
          const reader = new FileReader();
          reader.onload = (event) => {
            createNewElement("image", event.target.result, 100, 100);
          };
          reader.readAsDataURL(file);
        }
      }
    }
  }, []);

  React.useEffect(() => {
    document.addEventListener("paste", handlePaste);
    return () => document.removeEventListener("paste", handlePaste);
  }, [handlePaste]);

  // Sample images for gallery
  const sampleImages = [
    {
      id: "sample1",
      src: "https://images.unsplash.com/photo-1506905925346-21bda4d32df4?w=200&h=200&fit=crop",
      name: "Mountain",
    },
    {
      id: "sample2",
      src: "https://images.unsplash.com/photo-1441974231531-c6227db76b6e?w=200&h=200&fit=crop",
      name: "Forest",
    },
    {
      id: "sample3",
      src: "https://images.unsplash.com/photo-1469474968028-56623f02e42e?w=200&h=200&fit=crop",
      name: "Lake",
    },
    {
      id: "sample4",
      src: "https://images.unsplash.com/photo-1506905925346-21bda4d32df4?w=200&h=200&fit=crop",
      name: "Sky",
    },
  ];

  const handleWheel = useCallback(
    (e) => {
      if (e.ctrlKey || e.metaKey) {
        e.preventDefault();
        const rect = canvasRef.current.getBoundingClientRect();
        const mouseX = e.clientX - rect.left;
        const mouseY = e.clientY - rect.top;

        const delta = e.deltaY > 0 ? 0.9 : 1.1;
        const newZoom = Math.min(Math.max(zoom * delta, 0.1), 5);

        // Adjust pan offset to zoom toward mouse position
        const zoomChange = newZoom / zoom;
        setPanOffset((prev) => ({
          x: mouseX - (mouseX - prev.x) * zoomChange,
          y: mouseY - (mouseY - prev.y) * zoomChange,
        }));

        setZoom(newZoom);
      }
    },
    [zoom]
  );

  const resetView = () => {
    setZoom(1);
    setPanOffset({ x: 0, y: 0 });
  };

  const zoomToFit = () => {
    const containerRect = canvasRef.current.getBoundingClientRect();
    const scaleX = (containerRect.width - 100) / canvasSize.width;
    const scaleY = (containerRect.height - 100) / canvasSize.height;
    const newZoom = Math.min(scaleX, scaleY, 1);

    setZoom(newZoom);
    setPanOffset({
      x: (containerRect.width - canvasSize.width * newZoom) / 2,
      y: (containerRect.height - canvasSize.height * newZoom) / 2,
    });
  };

  const handleKeyDown = useCallback(
    (e) => {
      if (selectedElement && (e.key === "Delete" || e.key === "Backspace")) {
        deleteElement(selectedElement);
      }
    },
    [selectedElement]
  );

  React.useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);
    return () => document.removeEventListener("keydown", handleKeyDown);
  }, [handleKeyDown]);

  // Add wheel event listener for zoom
  React.useEffect(() => {
    const canvas = canvasRef.current;
    if (canvas) {
      canvas.addEventListener("wheel", handleWheel, { passive: false });
      return () => canvas.removeEventListener("wheel", handleWheel);
    }
  }, [handleWheel]);

  const downloadCanvas = () => {
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");
    canvas.width = canvasSize.width;
    canvas.height = canvasSize.height;

    // White background
    ctx.fillStyle = "#ffffff";
    ctx.fillRect(0, 0, canvas.width, canvas.height);

    // Render elements
    elements.forEach((element) => {
      ctx.save();
      ctx.translate(
        element.x + element.width / 2,
        element.y + element.height / 2
      );
      ctx.rotate((element.rotation * Math.PI) / 180);
      ctx.translate(-element.width / 2, -element.height / 2);

      if (element.type === "rectangle") {
        ctx.fillStyle = element.fill;
        ctx.fillRect(0, 0, element.width, element.height);
        if (element.strokeWidth > 0) {
          ctx.strokeStyle = element.stroke;
          ctx.lineWidth = element.strokeWidth;
          ctx.strokeRect(0, 0, element.width, element.height);
        }
      } else if (element.type === "circle") {
        ctx.beginPath();
        ctx.arc(
          element.width / 2,
          element.height / 2,
          element.width / 2,
          0,
          2 * Math.PI
        );
        ctx.fillStyle = element.fill;
        ctx.fill();
        if (element.strokeWidth > 0) {
          ctx.strokeStyle = element.stroke;
          ctx.lineWidth = element.strokeWidth;
          ctx.stroke();
        }
      } else if (element.type === "text") {
        ctx.fillStyle = element.color;
        ctx.font = `${element.fontWeight} ${element.fontSize}px Arial`;
        ctx.textAlign = "left";
        ctx.textBaseline = "top";
        ctx.fillText(element.content, 0, 0);
      }

      ctx.restore();
    });

    // Download
    const link = document.createElement("a");
    link.download = "design.png";
    link.href = canvas.toDataURL();
    link.click();
  };

  const selectedEl = elements.find((el) => el.id === selectedElement);

  return (
    <div className={styles.container}>
      {/* Sidebar */}
      <div className={styles.sidebar}>
        <div className={styles.sidebarHeader}>
          <h1 className={styles.title}>Design Studio</h1>
        </div>

        <div className={styles.librarySection}>
          <h3 className={styles.sectionTitle}>Library</h3>
          <div className={styles.tabContainer}>
            <button
              onClick={() => setActiveTab("elements")}
              className={`${styles.tab} ${
                activeTab === "elements" ? styles.tabActive : ""
              }`}
            >
              Elements
            </button>
            <button
              onClick={() => setActiveTab("shapes")}
              className={`${styles.tab} ${
                activeTab === "shapes" ? styles.tabActive : ""
              }`}
            >
              Shapes
            </button>
            <button
              onClick={() => setActiveTab("images")}
              className={`${styles.tab} ${
                activeTab === "images" ? styles.tabActive : ""
              }`}
            >
              Images
            </button>
          </div>

          {activeTab === "elements" && (
            <div className={styles.elementsGrid}>
              <button
                onClick={() => createNewElement("text")}
                className={`${styles.elementButton} ${styles.elementButtonBlue}`}
              >
                <Type size={20} />
              </button>
              <button
                onClick={() => createNewElement("rectangle")}
                className={`${styles.elementButton} ${styles.elementButtonGreen}`}
              >
                <Square size={20} />
              </button>
              <button
                onClick={() => createNewElement("circle")}
                className={`${styles.elementButton} ${styles.elementButtonRed}`}
              >
                <Circle size={20} />
              </button>
              <button
                onClick={downloadCanvas}
                className={`${styles.elementButton} ${styles.elementButtonPurple}`}
              >
                <Download size={20} />
              </button>
            </div>
          )}

          {activeTab === "shapes" && (
            <div className={styles.shapesGrid}>
              <button
                onClick={() => createNewElement("rectangle")}
                className={`${styles.shapeButton} ${styles.shapeButtonBlue}`}
                title="Rectangle"
              >
                <Square size={16} />
              </button>
              <button
                onClick={() => createNewElement("circle")}
                className={`${styles.shapeButton} ${styles.shapeButtonRed}`}
                title="Circle"
              >
                <Circle size={16} />
              </button>
              <button
                onClick={() => createNewElement("triangle")}
                className={`${styles.shapeButton} ${styles.shapeButtonGreen}`}
                title="Triangle"
              >
                <Triangle size={16} />
              </button>
              <button
                onClick={() => createNewElement("star")}
                className={`${styles.shapeButton} ${styles.shapeButtonYellow}`}
                title="Star"
              >
                <Star size={16} />
              </button>
              <button
                onClick={() => createNewElement("heart")}
                className={`${styles.shapeButton} ${styles.shapeButtonPink}`}
                title="Heart"
              >
                <Heart size={16} />
              </button>
            </div>
          )}

          {activeTab === "images" && (
            <div className={styles.imagesSection}>
              <div className={styles.uploadContainer}>
                <button
                  onClick={() => fileInputRef.current?.click()}
                  className={`${styles.uploadButton} ${styles.uploadButtonBlue}`}
                >
                  <Upload size={14} />
                  <span>Upload</span>
                </button>
                <div
                  className={`${styles.uploadButton} ${styles.uploadButtonGreen}`}
                >
                  <Clipboard size={14} />
                  <span>Paste Image</span>
                </div>
              </div>

              <input
                ref={fileInputRef}
                type="file"
                accept="image/*"
                multiple
                onChange={handleFileUpload}
                className={styles.hiddenInput}
              />

              <div className={styles.imagesSectionLabel}>Sample Images</div>
              <div className={styles.imagesGrid}>
                {sampleImages.map((img) => (
                  <div
                    key={img.id}
                    className={styles.imageItem}
                    onClick={() => createNewElement("image", img.src, 100, 100)}
                  >
                    <img
                      src={img.src}
                      alt={img.name}
                      className={styles.imagePreview}
                    />
                    <div className={styles.imageLabel}>{img.name}</div>
                  </div>
                ))}
              </div>

              {uploadedImages.length > 0 && (
                <>
                  <div className={styles.imagesSectionLabel}>Your Images</div>
                  <div className={styles.imagesGrid}>
                    {uploadedImages.map((img) => (
                      <div
                        key={img.id}
                        className={styles.imageItem}
                        onClick={() =>
                          createNewElement("image", img.src, 100, 100)
                        }
                      >
                        <img
                          src={img.src}
                          alt={img.name}
                          className={styles.imagePreview}
                        />
                        <div className={styles.imageLabel}>{img.name}</div>
                      </div>
                    ))}
                  </div>
                </>
              )}
            </div>
          )}
        </div>

        {/* Properties Panel */}
        {selectedEl && (
          <div className={styles.propertiesPanel}>
            <h3 className={styles.sectionTitle}>Properties</h3>

            <div className={styles.propertiesContainer}>
              {/* Position */}
              <div className={styles.propertyGroup}>
                <label className={styles.propertyLabel}>Position</label>
                <div className={styles.inputGroup}>
                  <input
                    type="number"
                    value={Math.round(selectedEl.x)}
                    onChange={(e) =>
                      updateElement(selectedEl.id, {
                        x: parseInt(e.target.value) || 0,
                      })
                    }
                    className={styles.propertyInput}
                    placeholder="X"
                  />
                  <input
                    type="number"
                    value={Math.round(selectedEl.y)}
                    onChange={(e) =>
                      updateElement(selectedEl.id, {
                        y: parseInt(e.target.value) || 0,
                      })
                    }
                    className={styles.propertyInput}
                    placeholder="Y"
                  />
                </div>
              </div>

              {/* Size */}
              <div className={styles.propertyGroup}>
                <label className={styles.propertyLabel}>Size</label>
                <div className={styles.inputGroup}>
                  <input
                    type="number"
                    value={Math.round(selectedEl.width)}
                    onChange={(e) =>
                      updateElement(selectedEl.id, {
                        width: parseInt(e.target.value) || 1,
                      })
                    }
                    className={styles.propertyInput}
                    placeholder="W"
                  />
                  <input
                    type="number"
                    value={Math.round(selectedEl.height)}
                    onChange={(e) =>
                      updateElement(selectedEl.id, {
                        height: parseInt(e.target.value) || 1,
                      })
                    }
                    className={styles.propertyInput}
                    placeholder="H"
                  />
                </div>
              </div>

              {/* Rotation */}
              <div className={styles.propertyGroup}>
                <label className={styles.propertyLabel}>Rotation</label>
                <input
                  type="range"
                  min="0"
                  max="360"
                  value={selectedEl.rotation}
                  onChange={(e) =>
                    updateElement(selectedEl.id, {
                      rotation: parseInt(e.target.value),
                    })
                  }
                  className={styles.rangeInput}
                />
                <div className={styles.rangeValue}>{selectedEl.rotation}</div>
              </div>

              {/* Text Properties */}
              {selectedEl.type === "text" && (
                <>
                  <div className={styles.propertyGroup}>
                    <label className={styles.propertyLabel}>Font Size</label>
                    <input
                      type="number"
                      value={selectedEl.fontSize}
                      onChange={(e) =>
                        updateElement(selectedEl.id, {
                          fontSize: parseInt(e.target.value) || 12,
                        })
                      }
                      className={styles.fullWidthInput}
                    />
                  </div>
                  <div className={styles.propertyGroup}>
                    <label className={styles.propertyLabel}>Color</label>
                    <input
                      type="color"
                      value={selectedEl.color}
                      onChange={(e) =>
                        updateElement(selectedEl.id, { color: e.target.value })
                      }
                      className={styles.colorInput}
                    />
                  </div>
                  <div className={styles.propertyGroup}>
                    <label className={styles.propertyLabel}>Font Weight</label>
                    <select
                      value={selectedEl.fontWeight}
                      onChange={(e) =>
                        updateElement(selectedEl.id, {
                          fontWeight: e.target.value,
                        })
                      }
                      className={styles.fullWidthInput}
                    >
                      <option value="normal">Normal</option>
                      <option value="bold">Bold</option>
                    </select>
                  </div>
                </>
              )}

              {/* Shape Properties */}
              {(selectedEl.type === "rectangle" ||
                selectedEl.type === "circle" ||
                selectedEl.type === "triangle" ||
                selectedEl.type === "star" ||
                selectedEl.type === "heart") && (
                <>
                  <div className={styles.propertyGroup}>
                    <label className={styles.propertyLabel}>Fill Color</label>
                    <input
                      type="color"
                      value={selectedEl.fill}
                      onChange={(e) =>
                        updateElement(selectedEl.id, { fill: e.target.value })
                      }
                      className={styles.colorInput}
                    />
                  </div>
                  <div className={styles.propertyGroup}>
                    <label className={styles.propertyLabel}>Stroke Color</label>
                    <input
                      type="color"
                      value={selectedEl.stroke}
                      onChange={(e) =>
                        updateElement(selectedEl.id, { stroke: e.target.value })
                      }
                      className={styles.colorInput}
                    />
                  </div>
                  <div className={styles.propertyGroup}>
                    <label className={styles.propertyLabel}>Stroke Width</label>
                    <input
                      type="number"
                      min="0"
                      value={selectedEl.strokeWidth}
                      onChange={(e) =>
                        updateElement(selectedEl.id, {
                          strokeWidth: parseInt(e.target.value) || 0,
                        })
                      }
                      className={styles.fullWidthInput}
                    />
                  </div>
                </>
              )}

              {/* Image Properties */}
              {selectedEl.type === "image" && (
                <div className={styles.propertyGroup}>
                  <label className={styles.propertyLabel}>Opacity</label>
                  <input
                    type="range"
                    min="0"
                    max="1"
                    step="0.1"
                    value={selectedEl.opacity || 1}
                    onChange={(e) =>
                      updateElement(selectedEl.id, {
                        opacity: parseFloat(e.target.value),
                      })
                    }
                    className={styles.rangeInput}
                  />
                  <div className={styles.rangeValue}>
                    {Math.round((selectedEl.opacity || 1) * 100)}%
                  </div>
                </div>
              )}

              {/* Actions */}
              <div className={styles.actionsContainer}>
                <div className={styles.actionButtons}>
                  <button
                    onClick={() => duplicateElement(selectedEl.id)}
                    className={`${styles.actionButton} ${styles.actionButtonBlue}`}
                  >
                    <Copy size={12} />
                    Copy
                  </button>
                  <button
                    onClick={() => deleteElement(selectedEl.id)}
                    className={`${styles.actionButton} ${styles.actionButtonRed}`}
                  >
                    <Trash2 size={12} />
                    Delete
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Main Canvas Area */}
      <div className={styles.mainArea}>
        {/* Top Bar */}
        <div className={styles.topBar}>
          <div className={styles.canvasSizeControls}>
            <span className={styles.topBarLabel}>Canvas Size:</span>
            <input
              type="number"
              value={canvasSize.width}
              onChange={(e) =>
                setCanvasSize((prev) => ({
                  ...prev,
                  width: parseInt(e.target.value) || 800,
                }))
              }
              className={styles.sizeInput}
              placeholder="Width"
            />
            <span className={styles.sizeLabel}></span>
            <input
              type="number"
              value={canvasSize.height}
              onChange={(e) =>
                setCanvasSize((prev) => ({
                  ...prev,
                  height: parseInt(e.target.value) || 600,
                }))
              }
              className={styles.sizeInput}
              placeholder="Height"
            />
          </div>

          <div className={styles.topBarRight}>
            {/* Zoom Controls */}
            <div className={styles.zoomControls}>
              <button
                onClick={() => setZoom((prev) => Math.max(prev * 0.8, 0.1))}
                className={styles.zoomButton}
                title="Zoom out"
              >
                
              </button>
              <span className={styles.zoomDisplay}>
                {Math.round(zoom * 100)}%
              </span>
              <button
                onClick={() => setZoom((prev) => Math.min(prev * 1.2, 5))}
                className={styles.zoomButton}
                title="Zoom in"
              >
                +
              </button>
              <button
                onClick={zoomToFit}
                className={styles.fitButton}
                title="Fit to view"
              >
                Fit
              </button>
              <button
                onClick={resetView}
                className={styles.resetButton}
                title="Reset zoom and position"
              >
                100%
              </button>
            </div>

            <div className={styles.elementCount}>
              {elements.length} element{elements.length !== 1 ? "s" : ""}
            </div>
            {selectedElement && (
              <div className={styles.deleteHint}>
                Press Delete or click  to remove selected element
              </div>
            )}
          </div>
        </div>

        {/* Canvas */}
        <div className={styles.canvasContainer}>
          <div
            ref={canvasRef}
            className={styles.canvasWrapper}
            style={{ cursor: isPanning ? "grabbing" : "default" }}
            onMouseDown={handleCanvasMouseDown}
          >
            <div
              className={styles.canvasContent}
              style={{
                width: canvasSize.width,
                height: canvasSize.height,
                transform: `translate(${panOffset.x}px, ${panOffset.y}px) scale(${zoom})`,
                transformOrigin: "0 0",
              }}
            >
              {elements.map((element) => (
                <div
                  key={element.id}
                  className={`${styles.element} ${
                    selectedElement === element.id ? styles.elementSelected : ""
                  }`}
                  style={{
                    left: element.x,
                    top: element.y,
                    width: element.width,
                    height: element.height,
                    transform: `rotate(${element.rotation}deg)`,
                    cursor: dragElement === element.id ? "grabbing" : "grab",
                  }}
                  onMouseDown={(e) => handleMouseDown(e, element.id)}
                >
                  {element.type === "text" && (
                    <div
                      contentEditable
                      suppressContentEditableWarning
                      className={styles.textElement}
                      style={{
                        fontSize: element.fontSize,
                        color: element.color,
                        fontWeight: element.fontWeight,
                        lineHeight: "1.2",
                        cursor: "text",
                      }}
                      onMouseDown={(e) => {
                        if (selectedElement === element.id) {
                          e.stopPropagation();
                        } else {
                          handleMouseDown(e, element.id);
                        }
                      }}
                      onBlur={(e) =>
                        handleTextEdit(element.id, e.target.textContent)
                      }
                      onKeyDown={(e) => {
                        if (e.key === "Enter" && !e.shiftKey) {
                          e.preventDefault();
                          e.target.blur();
                        }
                        e.stopPropagation();
                      }}
                    >
                      {element.content}
                    </div>
                  )}

                  {element.type === "rectangle" && (
                    <div
                      className={styles.rectangleElement}
                      style={{
                        backgroundColor: element.fill,
                        border: `${element.strokeWidth}px solid ${element.stroke}`,
                      }}
                    />
                  )}

                  {element.type === "circle" && (
                    <div
                      className={styles.circleElement}
                      style={{
                        backgroundColor: element.fill,
                        border: `${element.strokeWidth}px solid ${element.stroke}`,
                      }}
                    />
                  )}

                  {element.type === "triangle" && (
                    <div
                      className={styles.shapeContainer}
                      style={{
                        color: element.fill,
                      }}
                    >
                      <Triangle
                        size={Math.min(element.width, element.height)}
                        fill={element.fill}
                        stroke={element.stroke}
                        strokeWidth={element.strokeWidth}
                      />
                    </div>
                  )}

                  {element.type === "star" && (
                    <div
                      className={styles.shapeContainer}
                      style={{
                        color: element.fill,
                      }}
                    >
                      <Star
                        size={Math.min(element.width, element.height)}
                        fill={element.fill}
                        stroke={element.stroke}
                        strokeWidth={element.strokeWidth}
                      />
                    </div>
                  )}

                  {element.type === "heart" && (
                    <div
                      className={styles.shapeContainer}
                      style={{
                        color: element.fill,
                      }}
                    >
                      <Heart
                        size={Math.min(element.width, element.height)}
                        fill={element.fill}
                        stroke={element.stroke}
                        strokeWidth={element.strokeWidth}
                      />
                    </div>
                  )}

                  {element.type === "image" && (
                    <img
                      src={element.src}
                      alt="Design element"
                      className={styles.imageElement}
                      style={{
                        opacity: element.opacity || 1,
                      }}
                      draggable={false}
                    />
                  )}

                  {/* Resize handles for selected element */}
                  {selectedElement === element.id && (
                    <ResizeHandles element={element} />
                  )}

                  {/* Delete button for selected element */}
                  {selectedElement === element.id && (
                    <button
                      className={styles.deleteButton}
                      style={{
                        top: -8 / zoom,
                        right: -2 / zoom,
                        transform: `scale(${1 / zoom})`,
                        transformOrigin: "center",
                      }}
                      onClick={(e) => {
                        e.stopPropagation();
                        deleteElement(element.id);
                      }}
                      title="Delete element"
                    >
                      
                    </button>
                  )}
                </div>
              ))}
            </div>

            {/* Zoom instruction overlay */}
            <div className={styles.zoomInstructions}>
              Hold Ctrl/Cmd + scroll to zoom  Ctrl/Cmd + drag to pan
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CanvaClone;
