import positionStyles from './position.module.css';
import { useState, useRef, useEffect, useCallback, useMemo } from 'react';
import ANIMATION_PRESET from '../../animations/preset.js';
import React from 'react';

// Custom hook to replicate useTransform behavior
const useTransform = (scrollY, inputRange, outputRange, defaultValue) => {
  return useMemo(() => {
    // Early validation
    if (!inputRange?.length || !outputRange?.length || 
        inputRange.length !== outputRange.length || inputRange.length < 2) {
      return defaultValue;
    }

    // Handle edge cases
    if (scrollY <= inputRange[0]) return outputRange[0];
    if (scrollY >= inputRange[inputRange.length - 1]) return outputRange[outputRange.length - 1];

    // Binary search for the correct segment (O(log n) vs O(n))
    let left = 0;
    let right = inputRange.length - 1;
    
    while (left < right - 1) {
      const mid = Math.floor((left + right) / 2);
      if (scrollY < inputRange[mid]) {
        right = mid;
      } else {
        left = mid;
      }
    }

    const segmentIndex = left;
    const inputMin = inputRange[segmentIndex];
    const inputMax = inputRange[segmentIndex + 1];
    const outputMin = outputRange[segmentIndex];
    const outputMax = outputRange[segmentIndex + 1];

    // Calculate interpolation progress
    const progress = (scrollY - inputMin) / (inputMax - inputMin);

    // Type-specific interpolation
    if (typeof outputMin === 'number') {
      return outputMin + (outputMax - outputMin) * progress;
    }
    
    if (typeof outputMin === 'string' && outputMin.includes('px')) {
      const startNum = parseFloat(outputMin);
      const endNum = parseFloat(outputMax);
      return `${startNum + (endNum - startNum) * progress}px`;
    }
    
    // Fallback for non-interpolatable values
    return progress < 0.5 ? outputMin : outputMax;
  }, [scrollY, inputRange, outputRange, defaultValue]);
};

function Position({
  x = '0px',
  y = '0px',
  height = '100px',
  width = '100px',
  rotation = 0,
  opacity = 1,
  layer = 0,
  animation = {},
  parentScrollObject = 'none',
  scale = 1,
  scaledScrollY = 0, // Accept external scaled scroll value
  style = {},
  children,
  passStyleFlag = false,
  isEditing = true,
  onClick,
}) {
  const [show, setShow] = useState(true);
  const elementRef = useRef(null);

  // Use the external scaled scroll value instead of calculating internally
  const scrollY = scaledScrollY;

  const hasAnimation = animation && typeof animation === 'object';

  // Create all transforms (they will default to static values if no animation)
  const animate_x = useTransform(
    scrollY,
    animation?.keyframes?.x?.scrollY ?? [0, 1],
    animation?.keyframes?.x?.value ?? [x, x],
    x
  );

  const animate_y = useTransform(
    scrollY,
    animation?.keyframes?.y?.scrollY ?? [0, 1],
    animation?.keyframes?.y?.value ?? [y, y],
    y
  ) - scrollY;

  const animate_height = useTransform(
    scrollY,
    animation?.keyframes?.height?.scrollY ?? [0, 1],
    animation?.keyframes?.height?.value ?? [height, height],
    height
  );

  const animate_width = useTransform(
    scrollY,
    animation?.keyframes?.width?.scrollY ?? [0, 1],
    animation?.keyframes?.width?.value ?? [width, width],
    width
  );

  const animate_rotation = useTransform(
    scrollY,
    animation?.keyframes?.rotation?.scrollY ?? [0, 1],
    animation?.keyframes?.rotation?.value ?? [rotation, rotation],
    rotation
  );

  const animate_opacity = useTransform(
    scrollY,
    animation?.keyframes?.opacity?.scrollY ?? [0, 1],
    animation?.keyframes?.opacity?.value ?? [opacity, opacity],
    opacity
  );

  // Handle preset animations
  const css_animation = useMemo(() => {
    if (animation?.type !== 'preset') return '';
    
    const animation_type = animation?.properties?.type;
    const animation_preset = ANIMATION_PRESET?.[animation_type];

    if (!animation_preset) return '';

    const duration = animation?.properties?.duration || '';
    const timingFunction = animation_preset['timing-function'] || 'linear';
    const iterationCount = animation_preset['iteration-count'] || 1;

    return `${animation_preset.name} ${duration} ${timingFunction} ${iterationCount}`;
  }, [animation]);

  // Create the style object similar to original - memoized with animated values as dependencies
  const elementStyle = useMemo(() => ({
    position: "absolute",
    left: animate_x,
    top: animate_y,
    width: animate_width,
    height: animate_height,
    transform: `rotate(${animate_rotation}deg) translateZ(0)`,
    zIndex: layer,
    willChange: 'transform, opacity',
    backfaceVisibility: 'hidden',
    ...style, // Merge external styles
  }), [animate_x, animate_y, animate_width, animate_height, animate_rotation, layer, style]);

  const wrapperStyle = useMemo(() => ({
    animation: css_animation,
    width: "100%",
    height: "100%",
    transform: 'translateZ(0)',
    opacity: animate_opacity,
  }), [css_animation, animate_opacity]);

  // Memoize the className string
  const elementClassName = useMemo(() => 
    isEditing ? positionStyles.hoverBorder : '', 
    [isEditing]
  );

  // Memoize children cloning to prevent unnecessary re-renders
  const clonedChildren = useMemo(() => {
    return React.Children.map(children, child =>
      React.isValidElement(child)
        ? React.cloneElement(child, {
            style: { 
              ...(child.props.style || {}),
              ...elementStyle,
              child: wrapperStyle,
            },
            scrollY: scrollY
          })
        : child
    );
  }, [children, elementStyle, wrapperStyle, scrollY]);

  return show ? (
    <div
      ref={elementRef}
      style={elementStyle}
      className={elementClassName}
      onClick={onClick}
    >
      <div
        className={`${positionStyles.wrapper} `}
        style={wrapperStyle}
      >
        {clonedChildren}
      </div>
    </div>
  ) : null;
}

// Memoize the entire component to prevent re-renders when props haven't changed
export default React.memo(Position, (prevProps, nextProps) => {
  // Custom comparison function - only re-render if these specific props change
  const keysToCompare = [
    'x', 'y', 'height', 'width', 'rotation', 'opacity', 
    'scaledScrollY', 'layer', 'isEditing', 'show'
  ];
  
  for (const key of keysToCompare) {
    if (prevProps[key] !== nextProps[key]) {
      return false; // Props changed, re-render
    }
  }
  
  // Deep comparison for animation and style objects
  if (JSON.stringify(prevProps.animation) !== JSON.stringify(nextProps.animation)) {
    return false;
  }
  
  if (JSON.stringify(prevProps.style) !== JSON.stringify(nextProps.style)) {
    return false;
  }
  
  // Compare children
  if (prevProps.children !== nextProps.children) {
    return false;
  }
  
  return true; // Props haven't changed, don't re-render
});