import BaseElement from "./canvas_base_element.js";

export default class TextElement extends BaseElement {
  constructor(metadata, scale = 1, canvas_width = 100, canvas_height = 100) {
    super(metadata, scale, canvas_width, canvas_height);

    // Text-specific properties with defaults - mapped to match data structure
    const objProps = this.metadata.object_property || {};
    const styleProps = objProps.style || {};

    this.textProps = {
      // Text content from object_property root
      text: objProps.text || "Sample Text",

      // Font properties from style object
      fontFamily: this.parseFontFamily(styleProps.fontFamily) || "Arial, sans-serif",
      fontSize: parseFloat(styleProps.fontSize) || 24,
      fontWeight: styleProps.fontWeight || "normal",
      fontStyle: styleProps.fontStyle || "normal",

      // Text alignment and layout from style
      textAlign: styleProps.textAlign || "left",
      textBaseline: this.mapTextBaseline(styleProps.textBaseline, styleProps.verticalAlign) || "top",
      lineHeight: styleProps.lineHeight || 1.2,
      letterSpacing: this.parsePixelValue(styleProps.letterSpacing) || 0,
      wordSpacing: this.parsePixelValue(styleProps.wordSpacing) || 0,

      // Text styling from style
      color: styleProps.color || "#000000",
      backgroundColor: styleProps.backgroundColor || "transparent",
      padding: this.parsePixelValue(styleProps.padding) || 10,
      textDecoration: styleProps.textDecoration || "none",
      textTransform: styleProps.textTransform || "none",

      // Text layout behavior from style
      whiteSpace: styleProps.whiteSpace || "normal",
      wordWrap: styleProps.wordWrap || "break-word",
      maxLines: styleProps.maxLines || null,
      ellipsis: styleProps.ellipsis || false,

      // Gradient properties from style
      gradientEnabled: styleProps.gradientEnabled || false,
      gradientColor2: styleProps.gradientColor2 || "#ff0000",
      gradientDirection: this.parseDirection(styleProps.gradientDirection) || 0,

      // Shadow properties from style
      shadow: styleProps.shadow || styleProps.textShadow || false,
      shadowColor: styleProps.shadowColor || this.parseTextShadowColor(styleProps.textShadow) || "rgba(0, 0, 0, 0.5)",
      shadowBlur: styleProps.shadowBlur || this.parseTextShadowBlur(styleProps.textShadow) || 4,
      shadowOffsetX: styleProps.shadowOffsetX || this.parseTextShadowOffsetX(styleProps.textShadow) || 2,
      shadowOffsetY: styleProps.shadowOffsetY || this.parseTextShadowOffsetY(styleProps.textShadow) || 2,

      // Stroke properties from style
      strokeWidth: styleProps.strokeWidth || 0,
      strokeColor: styleProps.strokeColor || "#000000",

      // Border properties from style
      borderWidth: styleProps.borderWidth || 0,
      borderColor: styleProps.borderColor || "#000000",
      borderRadius: styleProps.borderRadius || 0,
    };

    // Apply text transform
    this.textProps.text = this.applyTextTransform(this.textProps.text);

    // console.log(
    //   `Text element created: "${this.textProps.text.substring(0, 30)}..."`,
    //   this.textProps
    // );
  }

  // Helper function to parse direction string like "45deg" to number
  parseDirection(direction) {
    if (typeof direction === "string") {
      return parseFloat(direction.replace("deg", "")) || 0;
    }
    return typeof direction === "number" ? direction : 0;
  }
  // Helper function to parse pixel values like "0.5px" to number
  parsePixelValue(value) {
    if (!value) return 0;
    if (typeof value === "number") return value;
    if (typeof value === "string") {
      return parseFloat(value.replace("px", "")) || 0;
    }
    return 0;
  }

  // Helper function to map CSS vertical-align and textBaseline to canvas textBaseline
  mapTextBaseline(textBaseline, verticalAlign) {
    if (verticalAlign === "middle") return "middle";
    return textBaseline || "top";
  }

  // Helper function to parse font family and remove extra quotes/semicolons
  parseFontFamily(fontFamily) {
    if (!fontFamily) return null;
    return fontFamily.replace(/[;"]/g, '').trim();
  }

  // Helper functions to parse CSS textShadow property
  parseTextShadowColor(textShadow) {
    if (!textShadow) return null;
    const colorMatch = textShadow.match(/(#[0-9a-fA-F]{3,6}|rgb\([^)]+\)|rgba\([^)]+\)|[a-zA-Z]+)/);
    return colorMatch ? colorMatch[0] : null;
  }

  parseTextShadowOffsetX(textShadow) {
    if (!textShadow) return 0;
    const parts = textShadow.match(/(-?\d+(?:\.\d+)?)px/g);
    return parts && parts[0] ? parseFloat(parts[0]) : 0;
  }

  parseTextShadowOffsetY(textShadow) {
    if (!textShadow) return 0;
    const parts = textShadow.match(/(-?\d+(?:\.\d+)?)px/g);
    return parts && parts[1] ? parseFloat(parts[1]) : 0;
  }

  parseTextShadowBlur(textShadow) {
    if (!textShadow) return 0;
    const parts = textShadow.match(/(-?\d+(?:\.\d+)?)px/g);
    return parts && parts[2] ? parseFloat(parts[2]) : 0;
  }



  // Apply text transform
  applyTextTransform(text) {
    switch (this.textProps.textTransform) {
      case "uppercase":
        return text.toUpperCase();
      case "lowercase":
        return text.toLowerCase();
      case "capitalize":
        return text.replace(/\b\w/g, (char) => char.toUpperCase());
      default:
        return text;
    }
  }

  /**
   * Creates a gradient for the text
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {number} width - Text width
   * @param {number} height - Text height
   * @returns {CanvasGradient|string} - Gradient or solid color
   */
  createTextGradient(ctx, width, height) {
    if (!this.textProps.gradientEnabled) {
      return this.textProps.color;
    }

    const angle = (this.textProps.gradientDirection * Math.PI) / 180;
    const diagonal = Math.sqrt(width * width + height * height);
    const x1 = width / 2 - (Math.cos(angle) * diagonal) / 2;
    const y1 = height / 2 - (Math.sin(angle) * diagonal) / 2;
    const x2 = width / 2 + (Math.cos(angle) * diagonal) / 2;
    const y2 = height / 2 + (Math.sin(angle) * diagonal) / 2;

    const gradient = ctx.createLinearGradient(x1, y1, x2, y2);
    gradient.addColorStop(0, this.textProps.color);
    gradient.addColorStop(1, this.textProps.gradientColor2);
    return gradient;
  }

  /**
   * Breaks text into lines that fit within the specified width
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {string} text - Text to wrap
   * @param {number} maxWidth - Maximum width for each line
   * @returns {Array<string>} - Array of text lines
   */
  wrapText(ctx, text, maxWidth) {
    if (this.textProps.whiteSpace === "nowrap") {
      return [text];
    }

    // Handle line breaks in text
    const lines = text.split('\n');
    const wrappedLines = [];
    
    for (const line of lines) {
      if (line.trim() === '') {
        wrappedLines.push('');
        continue;
      }
      wrappedLines.push(...this.wrapSingleLine(ctx, line, maxWidth));
    }
    
    return this.applyMaxLines(wrappedLines, ctx, maxWidth);
  }

  wrapSingleLine(ctx, text, maxWidth) {
    const words = text.split(" ");
    const lines = [];
    let currentLine = "";

    for (const word of words) {
      const testLine = currentLine ? `${currentLine} ${word}` : word;
      const metrics = ctx.measureText(testLine);

      if (metrics.width > maxWidth && currentLine) {
        lines.push(currentLine);
        currentLine = word;
      } else {
        currentLine = testLine;
      }
    }

    if (currentLine) {
      lines.push(currentLine);
    }

    return lines;
  }

  applyMaxLines(lines, ctx, maxWidth) {
    // Apply max lines limit
    if (this.textProps.maxLines && lines.length > this.textProps.maxLines) {
      const limitedLines = lines.slice(0, this.textProps.maxLines);
      if (this.textProps.ellipsis && limitedLines.length > 0) {
        const lastLineIndex = limitedLines.length - 1;
        const lastLine = limitedLines[lastLineIndex];
        const ellipsisText = lastLine + "...";
        const ellipsisMetrics = ctx.measureText(ellipsisText);

        if (ellipsisMetrics.width <= maxWidth) {
          limitedLines[lastLineIndex] = ellipsisText;
        } else {
          // Trim the last line to fit ellipsis
          let trimmedLine = lastLine;
          while (trimmedLine.length > 0) {
            trimmedLine = trimmedLine.slice(0, -1);
            const testEllipsis = trimmedLine + "...";
            if (ctx.measureText(testEllipsis).width <= maxWidth) {
              limitedLines[lastLineIndex] = testEllipsis;
              break;
            }
          }
        }
      }
      return limitedLines;
    }

    return lines;
  }

  /**
   * Sets up the canvas context with text styles
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   */
  applyTextStyles(ctx) {
    const fontString = `${this.textProps.fontStyle} ${this.textProps.fontWeight} ${this.textProps.fontSize}px ${this.textProps.fontFamily}`;
    ctx.font = fontString;
    ctx.textAlign = this.textProps.textAlign;
    ctx.textBaseline = this.textProps.textBaseline;
    // console.log('Applied font:', fontString);
    // console.log('Text align:', this.textProps.textAlign);
    // console.log('Text baseline:', this.textProps.textBaseline);
    // console.log('Shadow enabled:', this.textProps.shadow);

    // Apply shadow if enabled
    if (this.textProps.shadow && this.textProps.shadow !== "false") {
      ctx.shadowColor = this.textProps.shadowColor;
      ctx.shadowBlur = this.textProps.shadowBlur;
      ctx.shadowOffsetX = this.textProps.shadowOffsetX;
      ctx.shadowOffsetY = this.textProps.shadowOffsetY;
    } else {
      ctx.shadowColor = "transparent";
    }

    // Apply stroke if enabled
    if (this.textProps.strokeWidth > 0) {
      ctx.lineWidth = this.textProps.strokeWidth;
      ctx.strokeStyle = this.textProps.strokeColor;
    }
  }

  /**
   * Draws the background for the text
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {number} x - X position
   * @param {number} y - Y position
   * @param {number} width - Background width
   * @param {number} height - Background height
   */
  drawBackground(ctx, x, y, width, height) {
    if (this.textProps.backgroundColor === "transparent") return;

    ctx.save();
    ctx.shadowColor = "transparent"; // Disable shadow for background

    // Draw background
    ctx.fillStyle = this.textProps.backgroundColor;
    if (this.textProps.borderRadius > 0) {
      // Rounded rectangle
      ctx.beginPath();
      ctx.roundRect(x, y, width, height, this.textProps.borderRadius);
      ctx.fill();
    } else {
      ctx.fillRect(x, y, width, height);
    }

    // Draw border if enabled
    if (this.textProps.borderWidth > 0) {
      ctx.strokeStyle = this.textProps.borderColor;
      ctx.lineWidth = this.textProps.borderWidth;
      if (this.textProps.borderRadius > 0) {
        ctx.beginPath();
        ctx.roundRect(x, y, width, height, this.textProps.borderRadius);
        ctx.stroke();
      } else {
        ctx.strokeRect(x, y, width, height);
      }
    }

    ctx.restore();
  }

  /**
   * Draws text with custom letter spacing
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
  +   * @param {string} text - Text to draw
   * @param {number} x - X position
   * @param {number} y - Y position
   * @param {number} letterSpacing - Letter spacing
   * @param {number} wordSpacing - Word spacing
   */
  drawTextWithSpacings(ctx, text, x, y, letterSpacing, wordSpacing) {
    if (letterSpacing === 0 && wordSpacing === 0) {
      ctx.fillText(text, x, y);
      if (this.textProps.strokeWidth > 0) {
        ctx.strokeText(text, x, y);
      }
      return;
    }

    const words = text.split(' ');
    let currentX = x;

    // Adjust starting position for different alignments
    if (this.textProps.textAlign === "center") {
      const totalWidth = this.calculateTextWidthWithSpacings(ctx, text, letterSpacing, wordSpacing);
      currentX = x - totalWidth / 2;
    } else if (this.textProps.textAlign === "right") {
      const totalWidth = this.calculateTextWidthWithSpacings(ctx, text, letterSpacing, wordSpacing);
      currentX = x - totalWidth;
    }

    for (let wordIndex = 0; wordIndex < words.length; wordIndex++) {
      const word = words[wordIndex];
      // Handle empty words (multiple spaces)
      if (word === '') {
        currentX += ctx.measureText(' ').width + wordSpacing;
        continue;
      }
     
      // Draw each character in the word with letter spacing
      for (let charIndex = 0; charIndex < word.length; charIndex++) {
        const char = word[charIndex];
        ctx.fillText(char, currentX, y);
        
        if (this.textProps.strokeWidth > 0) {
          ctx.strokeText(char, currentX, y);
        }
        
        const charWidth = ctx.measureText(char).width;
        currentX += charWidth + letterSpacing;
      }
      
      // Add word spacing after each word (except the last one)
      if (wordIndex < words.length - 1) {
        currentX += wordSpacing;
      }
    }
  }

  /**
   * Draws text with custom letter spacing (legacy method)
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {string} text - Text to draw
   * @param {number} x - X position
   * @param {number} y - Y position
   * @param {number} spacing - Letter spacing
   */
  drawTextWithSpacing(ctx, text, x, y, spacing) {
    let currentX = x;

    // Adjust starting position for different alignments
    if (this.textProps.textAlign === "center") {
      const totalWidth = this.calculateTextWidthWithSpacing(ctx, text, spacing);
      currentX = x - totalWidth / 2;
    } else if (this.textProps.textAlign === "right") {
      const totalWidth = this.calculateTextWidthWithSpacing(ctx, text, spacing);
      currentX = x - totalWidth;
    }

    for (let i = 0; i < text.length; i++) {
      const char = text[i];
      ctx.fillText(char, currentX, y);

      if (this.textProps.strokeWidth > 0) {
        ctx.strokeText(char, currentX, y);
      }

      const charWidth = ctx.measureText(char).width;
      currentX += charWidth + spacing;
    }
  }

  /**
   * Calculates text width including letter spacing
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {string} text - Text to measure
   * @param {number} letterSpacing - Letter spacing
   * @param {number} wordSpacing - Word spacing
   * @returns {number} - Total width
   */
  calculateTextWidthWithSpacings(ctx, text, letterSpacing, wordSpacing) {
    const words = text.split(' ');
    let totalWidth = 0;
    
    for (let wordIndex = 0; wordIndex < words.length; wordIndex++) {
      const word = words[wordIndex];
      totalWidth += this.calculateTextWidthWithSpacing(ctx, word, letterSpacing);
      if (wordIndex < words.length - 1) {
        totalWidth += wordSpacing;
      }
    }
    
    return totalWidth;
  }

  /**
   * Calculates text width including letter spacing (legacy method)
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {string} text - Text to measure
   * @param {number} spacing - Letter spacing
   * @returns {number} - Total width
   */
  calculateTextWidthWithSpacing(ctx, text, spacing) {
    let totalWidth = 0;
    for (let i = 0; i < text.length; i++) {
      totalWidth += ctx.measureText(text[i]).width;
      if (i < text.length - 1) totalWidth += spacing;
    }
    return totalWidth;
  }

  /**
   * Draws text decoration (underline, strikethrough, etc.)
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {string} text - Text content
   * @param {number} x - X position
   * @param {number} y - Y position
   * @param {number} maxWidth - Maximum width
   */
  drawTextDecoration(ctx, text, x, y, maxWidth) {
    const textMetrics = ctx.measureText(text);
    const textWidth = Math.min(textMetrics.width, maxWidth);

    let startX = x;
    if (this.textProps.textAlign === "center") {
      startX = x - textWidth / 2;
    } else if (this.textProps.textAlign === "right") {
      startX = x - textWidth;
    }

    ctx.save();
    ctx.shadowColor = "transparent"; // Disable shadow for decoration
    ctx.strokeStyle = this.textProps.color;
    ctx.lineWidth = Math.max(1, this.textProps.fontSize * 0.05);

    ctx.beginPath();
    switch (this.textProps.textDecoration) {
      case "underline":
        const underlineY = y + this.textProps.fontSize * 0.1;
        ctx.moveTo(startX, underlineY);
        ctx.lineTo(startX + textWidth, underlineY);
        break;
      case "strikethrough":
      case "line-through":
        const strikeY = y - this.textProps.fontSize * 0.25;
        ctx.moveTo(startX, strikeY);
        ctx.lineTo(startX + textWidth, strikeY);
        break;
      case "overline":
        const overlineY = y - this.textProps.fontSize * 0.8;
        ctx.moveTo(startX, overlineY);
        ctx.lineTo(startX + textWidth, overlineY);
        break;
    }
    ctx.stroke();
    ctx.restore();
  }

  /**
   * Draws the text with proper alignment and styling
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   */
  drawText(ctx) {
    const { x, y, width, height } = this.currentProps;
    const padding = this.textProps.padding;
    const textWidth = width - padding * 2;
    const textHeight = height - padding * 2;

    // Draw background
    this.drawBackground(ctx, x, y, width, height);

    // Apply text styles
    this.applyTextStyles(ctx);
    // console.log('Drawing text:', this.textProps.text);
    // console.log('Text bounds:', { x, y, width, height });
    // console.log('Text color:', this.textProps.color);

    // Wrap text to fit width
    const lines = this.wrapText(ctx, this.textProps.text, textWidth);

    // Calculate line height in pixels
    const lineHeightPx = this.textProps.fontSize * this.textProps.lineHeight;
    const totalTextHeight = lines.length * lineHeightPx;

    // Calculate starting Y position based on text alignment
    let startY = y + padding;
    if (this.textProps.textBaseline === "middle") {
      startY = y + height / 2 - totalTextHeight / 2 + lineHeightPx / 2;
    } else if (this.textProps.textBaseline === "bottom") {
      startY = y + height - padding - totalTextHeight + lineHeightPx;
    }

    // Create gradient or solid color
    const fillStyle = this.createTextGradient(ctx, textWidth, totalTextHeight);
    ctx.fillStyle = fillStyle;

    // Draw each line
    lines.forEach((line, index) => {
      let lineX = x + padding;
      
      // console.log(`Drawing line ${index}: "${line}"`);
      // Adjust X position based on text alignment
      if (this.textProps.textAlign === "center") {
        lineX = x + width / 2;
      } else if (this.textProps.textAlign === "right") {
        lineX = x + width - padding;
      }

      const lineY = startY + index * lineHeightPx;

      // Apply letter and word spacing if needed
      const hasLetterSpacing = this.textProps.letterSpacing !== 0;
      const hasWordSpacing = this.textProps.wordSpacing !== 0;
      
      if (hasLetterSpacing || hasWordSpacing) {
        this.drawTextWithSpacings(
          ctx,
          line,
          lineX,
          lineY,
          this.textProps.letterSpacing,
          this.textProps.wordSpacing
        );
       } else if (hasLetterSpacing) {
        this.drawTextWithSpacing(
          ctx,
          line,
          lineX,
          lineY,
          this.textProps.letterSpacing
        );
      } else {
        // Draw text fill
        ctx.fillText(line, lineX, lineY);

        // Draw text stroke if enabled
        if (this.textProps.strokeWidth > 0) {
          ctx.strokeText(line, lineX, lineY);
        }
      }

      // Draw text decoration
      if (this.textProps.textDecoration !== "none") {
        this.drawTextDecoration(ctx, line, lineX, lineY, textWidth);
      }
    });
  }

  /**
   * Renders the text element to the canvas
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   */
  draw(ctx) {
    ctx.save();
    const { x, y, width, height, rotation, opacity } = this.currentProps;

    // Set opacity
    ctx.globalAlpha = Math.max(0, Math.min(1, opacity));
    // console.log(`Drawing text element with opacity: ${opacity}`);
    // console.log('Current props:', this.currentProps);
    // Apply rotation if needed
    if (rotation !== 0) {
      const centerX = x + width / 2;
      const centerY = y + height / 2;
      ctx.translate(centerX, centerY);
      ctx.rotate(rotation);
      ctx.translate(-centerX, -centerY);
    }

    try {
      // Draw the text
      this.drawText(ctx);

      // Debug: Draw a border around the text bounds (uncomment for debugging)
      // ctx.save();
      // ctx.shadowColor = 'transparent';
      // ctx.strokeStyle = "blue";
      // ctx.lineWidth = 1;
      // ctx.strokeRect(x, y, width, height);
      // ctx.restore();

      // console.log(`Drew text "${this.textProps.text.substring(0, 20)}..." at: x=${Math.round(x)}, y=${Math.round(y)}, w=${Math.round(width)}, h=${Math.round(height)}, opacity=${opacity.toFixed(2)}`);
    } catch (error) {
      console.error("Error drawing text:", error);
      // Draw a fallback rectangle
      ctx.fillStyle = `rgba(255, 0, 0, ${opacity})`;
      ctx.fillRect(x, y, width, height);
    }

    ctx.restore();
  }
}
