import React from "react";
import TextTypography from "./text_typography";
import TextEffects from "./text_effects";
import TextLayout from "./text_layout";

const TextProperties = ({
  selectedElement,
  localProperties,
  updateElementProperty
}) => {
  if (!selectedElement?.object || selectedElement.object.object_type !== "text") {
    return null;
  }

  const props = localProperties;
  const style = props.style || {};
  const elementIndex = selectedElement.index;

  return (
    <div style={{ display: "flex", flexDirection: "column", gap: "16px" }}>
      <h4
        style={{
          fontSize: "14px",
          fontWeight: "600",
          color: "#374151",
          marginBottom: "12px",
          marginTop: 0,
          borderBottom: "1px solid #e5e7eb",
          paddingBottom: "8px",
        }}
      >
        Text Properties
      </h4>

      {/* Text Content */}
      <div>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Text Content
        </label>
        <textarea
          value={props.text || ""}
          onChange={(e) =>
            updateElementProperty(
              elementIndex,
              "object_property.text",
              e.target.value
            )
          }
          style={{
            width: "100%",
            padding: "8px",
            border: "1px solid #d1d5db",
            borderRadius: "4px",
            height: "96px",
            resize: "none",
            fontSize: "14px",
          }}
          placeholder="Enter your text..."
        />
      </div>

      {/* Typography Section */}
      <TextTypography
        style={style}
        elementIndex={elementIndex}
        updateElementProperty={updateElementProperty}
      />

      {/* Layout & Alignment */}
      <TextLayout
        style={style}
        elementIndex={elementIndex}
        updateElementProperty={updateElementProperty}
      />

      {/* Text Effects */}
      <TextEffects
        style={style}
        elementIndex={elementIndex}
        updateElementProperty={updateElementProperty}
      />
    </div>
  );
};

export default TextProperties;