import { useSelector } from "react-redux";
import { useState, useRef, useEffect, useCallback, useMemo } from "react";
import React from "react";
import ImageElement from "./canvas_components/image_element";
import ShapeElement from "./canvas_components/shape_element";
import TextElement from "./canvas_components/text_element";
import styles from "./canvas_hosted_page.module.css";
const baseUrl = process.env.REACT_APP_ASSET_BASE_URL;
// iOS Safari compatibility fixes - IIFE runs immediately when module loads
(() => {
  try {
    // Prevent ethereum-related errors on iOS
    if (typeof window !== "undefined") {
      // Create ethereum object before any scripts try to access it
      if (typeof window.ethereum === "undefined") {
        // Create a proper ethereum-like object
        const ethereumStub = {
          selectedAddress: undefined, // This is what gets assigned to
          isMetaMask: false,
          request: () => Promise.reject(new Error("Not available on iOS")),
          // Add common properties that might be accessed
          isConnected: () => false,
          chainId: null,
          networkVersion: null,
        };

        // Define the property to be writable for selectedAddress assignments
        Object.defineProperty(window, "ethereum", {
          value: ethereumStub,
          writable: true, // Allow reassignment
          configurable: true, // Allow reconfiguration
        });

        console.log("iOS Safari: Created ethereum compatibility stub");
      }
      // Suppress common iOS Safari errors
      // Global error suppression for iOS Safari
      const originalError = window.onerror;
      window.onerror = function (message, source, lineno, colno, error) {
        if (
          typeof message === "string" &&
          (message.includes("ethereum") ||
            message.includes("translateDisabled") ||
            message === "Script error.")
        ) {
          console.warn("iOS Safari error suppressed:", message);
          return true; // Prevent default error handling
        }
        return originalError ? originalError.apply(this, arguments) : false;
      };

      // Also handle unhandled promise rejections
      window.addEventListener("unhandledrejection", (event) => {
        if (
          event.reason &&
          event.reason.message &&
          event.reason.message.includes("ethereum")
        ) {
          event.preventDefault();
          console.warn(
            "iOS Safari ethereum promise rejection suppressed:",
            event.reason
          );
        }
      });
    }
  } catch (e) {
    console.warn("iOS compatibility setup failed:", e);
  }
})(); // IIFE - runs immediately when module loads

// Dummy function for useEffect calls
const setupIOSCompatibility = () => {};

// Error Boundary for iOS Safari
class IOSErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(error) {
    return { hasError: true };
  }

  componentDidCatch(error, errorInfo) {
    console.warn("iOS Safari error caught:", error, errorInfo);
  }

  render() {
    if (this.state.hasError) {
      return <div>Something went wrong. Please refresh the page.</div>;
    }

    return this.props.children;
  }
}

const CanvasAnimatedElements = () => {
  const canvasRef = useRef(null);
  const scrollCanvasRef = useRef(null);
  const contextRef = useRef(null);
  const elementInstancesRef = useRef([]); // Changed from imageInstancesRef to elementInstancesRef
  const animationRef = useRef(null);

  const [scale, setScale] = useState(1);
  const [showLottie, setShowLottie] = useState(true);
  const [scrollPosition, setScrollPosition] = useState(0);
  const [clickPosition, setClickPosition] = useState(null);
  // Redux selectors with fallbacks
  const pageContent = useSelector(
    (state) => state?.hosted_page?.page_content || []
  );
  const pageSize = useSelector(
    (state) => state?.hosted_page?.page_size || null
  );
  const pageScrollHeight = useSelector(
    (state) => state?.hosted_page?.page_scroll_height || null
  );
  const pageBackground = useSelector(
    (state) => state?.hosted_page?.page_background || null
  );
  const pageTitle = useSelector(
    (state) => state?.hosted_page?.title_text || null
  );
  // console.log(pageSize, pageScrollHeight, pageBackground, pageTitle);
  // Default page dimensions if Redux values aren't available
  const defaultPageSize = { width: 1920, height: 1080 };
  const currentPageSize = pageSize || defaultPageSize;
  const currentScrollHeight = pageScrollHeight || 2500;

  // Filter and sort all drawable elements from pageContent
  const drawableElements = useMemo(() => {
    if (!pageContent || !Array.isArray(pageContent)) return [];

    return pageContent
      .filter(
        (element) =>
          element?.object_type === "image" ||
          element?.object_type === "shape" ||
          element?.object_type === "text"
      )
      .sort((a, b) => (a.layer || 0) - (b.layer || 0)); // Sort by layer for proper rendering order
  }, [pageContent]);

  // Memoized canvas dimensions
  const canvasDimensions = useMemo(() => {
    return {
      width: currentPageSize.width,
      height: currentScrollHeight,
      scaledWidth: currentPageSize.width * scale,
      scaledHeight: currentPageSize.height * scale,
    };
  }, [currentPageSize, currentScrollHeight, scale]);

  // Collect all scroll positions for markers
  const scrollMarkers = useMemo(() => {
    const markers = new Set();
    drawableElements.forEach((element) => {
      const keyframes = element.animation?.keyframes;
      if (keyframes) {
        Object.values(keyframes).forEach((keyframe) => {
          if (keyframe.scrollY) {
            keyframe.scrollY.forEach((scroll) => markers.add(scroll));
          }
        });
      }
    });
    return Array.from(markers).sort((a, b) => a - b);
  }, [drawableElements]);

  // Handle canvas click events
  const handleScrollContainerClick = (event) => {
    // iOS Safari click handling fix
    try {
      if (
        typeof event.clientX !== "number" ||
        typeof event.clientY !== "number"
      ) {
        console.warn("Invalid click event on iOS Safari");
        return;
      }
    } catch (e) {
      console.warn("iOS Safari click error:", e);
      return;
    }

    // console.table(event)
    // console.log("scale:",scale)
    const canvas = canvasRef.current;
    const event_x = event.clientX;
    const event_y = event.clientY;
    // console.log(event_x,event_y,event.clientX,event.clientY)
    const scrollContainer = scrollCanvasRef.current;
    if (!scrollContainer || !canvas) return;
    // Hide Lottie animation on any click
    if (showLottie) {
      setShowLottie(false);
      console.log("Lottie animation hidden due to click");
    }
    const rect = scrollContainer.getBoundingClientRect();
    const currentScrollTop = scrollContainer.scrollTop;

    // Convert click coordinates to canvas coordinates
    // Account for canvas being centered in the viewport
    const viewportCenterX = window.innerWidth / 2;
    const canvasLeftEdge =
      viewportCenterX - (currentPageSize.width * scale) / 2;
    // console.log(rect, event_x, canvasLeftEdge, scrollContainer);
    const clickX = event_x - canvasLeftEdge; //scale ;
    const clickY = event_y; //scale;

    // console.log('Click debug:', {
    //   clientX: event_x,
    //   clientY: event_y-currentScrollTop,
    //   currentScrollTop:currentScrollTop,
    //   rectTop: rect.top,
    //   canvasLeftEdge,
    //   clickX,
    //   clickY,
    //   scale,
    //   currentScrollTop
    // });
    // Check which element was clicked (reverse order for top-to-bottom hit testing)

    const clickedElements = elementInstancesRef.current
      .slice()
      .reverse()
      .filter(
        (element) =>
          element &&
          element.hitTest &&
          element.hitTest(clickX, clickY, currentScrollTop)
      );

    // console.log("Elements found at click:", clickedElements.length);
    // console.log(elementInstancesRef.current);
    if (clickedElements.length > 0) {
      const topElement = clickedElements[0];
      console.log(
        "Clicking element:",
        topElement.metadata.object_type,
        topElement.metadata?.object_property?.file || ""
      );
      topElement.onClick && topElement.onClick();
      console.log("topElement", topElement);
      if (topElement.metadata.on_click_prop) {
        console.log("lol");
        if (topElement.metadata.on_click_prop.action == "LINK") {
          window.open(topElement.metadata.on_click_prop.URL, "_blank");
        }
      }
    }

    setClickPosition({ x: clickX, y: clickY });
  };
  // Handle scroll events to hide Lottie animation
  const handleScroll = useCallback(() => {
    if (showLottie) {
      setShowLottie(false);
      console.log("Lottie animation hidden due to scroll");
    }
  }, [showLottie]);

  // Calculate scale based on window size
  const handleResize = useCallback(() => {
    const scaleWidth = window.innerWidth / currentPageSize.width;
    const scaleHeight = window.innerHeight / currentPageSize.height;
    setScale(Math.min(scaleWidth, scaleHeight, 1));
  }, [currentPageSize]);

  // Animation function - Updated to handle multiple element types
  const animate = useCallback(() => {
    try {
      const ctx = contextRef.current;
      const scrollContainer = scrollCanvasRef.current;

      if (ctx && scrollContainer && elementInstancesRef.current.length > 0) {
        // Clear the entire canvas
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

        // Update scroll position state
        const currentScrollTop = scrollContainer.scrollTop;
        // setScrollPosition(currentScrollTop);

        // Update and draw all element instances (images, shapes, and text)
        elementInstancesRef.current.forEach((elementInstance) => {
          if (elementInstance) {
            elementInstance.update(ctx, currentScrollTop);
          }
        });

        // console.log('Animation frame - scrollY:', currentScrollTop, 'Elements:', elementInstancesRef.current.length);
      }
    } catch (error) {
      console.warn("Animation frame error (iOS Safari):", error);
    }
    // Continue animation loop
    if (typeof requestAnimationFrame === "function") {
      animationRef.current = requestAnimationFrame(animate);
    }
  }, []);

  // Initialize element instances when pageContent changes
  useEffect(() => {
    if (drawableElements.length > 0) {
      // console.log('Creating element instances for', drawableElements.length, 'elements');

      // Clear existing instances
      elementInstancesRef.current = [];

      // Create new instances for each drawable element
      drawableElements.forEach((element, index) => {
        let elementInstance;

        if (element.object_type === "image") {
          elementInstance = new ImageElement(
            element,
            scale,
            currentPageSize.width * scale,
            currentPageSize.height * scale
          );
          // console.log(`Created ImageElement ${index}:`, element.object_property.file);
        } else if (element.object_type === "shape") {
          elementInstance = new ShapeElement(
            element,
            scale,
            currentPageSize.width * scale,
            currentPageSize.height * scale
          );
          // console.log(`Created ShapeElement ${index}:`, element.object_property.shape || 'rectangle');
        } else if (element.object_type === "text") {
          elementInstance = new TextElement(
            element,
            scale,
            currentPageSize.width * scale,
            currentPageSize.height * scale
          );
          // console.log(
          //   `Created TextElement ${index}:`,
          //   element.object_property || "rectangle"
          // );
        }
        if (elementInstance) {
          elementInstancesRef.current.push(elementInstance);
        }
      });
    }
  }, [drawableElements, scale]);

  // Initialize canvas and context
  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas || !currentPageSize) return;

    // console.log('Initializing canvas with dimensions:', currentPageSize.width * scale, 'x', currentPageSize.height * scale);

    // Set canvas size to match viewport
    canvas.width = currentPageSize.width * scale;
    canvas.height = currentPageSize.height * scale;
    canvas.style.width = `${currentPageSize.width * scale}px`;
    canvas.style.height = `${currentPageSize.height * scale}px`;
    // iOS Safari canvas context fix
    let ctx;
    try {
      ctx = canvas.getContext("2d", {
        alpha: true,
        willReadFrequently: false,
      });
    } catch (e) {
      ctx = canvas.getContext("2d");
    }
    contextRef.current = ctx;

    // Draw initial test rectangle
    ctx.fillStyle = "rgba(0, 255, 0, 0.3)";
    ctx.fillRect(10, 10, 100, 50);
    ctx.fillStyle = "black";
    ctx.font = "12px Arial";
    ctx.fillText("Canvas Active", 15, 30);
    // console.log('Canvas initialized with context:', !!ctx);
  }, [currentPageSize, scale]);

  // Start animation loop separately to avoid dependency issues
  useEffect(() => {
    if (contextRef.current && !animationRef.current) {
      // console.log('Starting animation loop');
      animate();
    }

    return () => {
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
        animationRef.current = null;
      }
    };
  }, [animate]);

  // Handle resize events
  useEffect(() => {
    // Setup iOS compatibility on component mount
    setupIOSCompatibility();
  }, []);

  // Handle resize events
  useEffect(() => {
    handleResize();

    console.log("Component mounted - showLottie:", showLottie);

    // Add scroll event listener to the scroll container
    const scrollContainer = scrollCanvasRef.current;
    if (scrollContainer) {
      scrollContainer.addEventListener("scroll", handleScroll);
    }

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
      if (scrollContainer) {
        scrollContainer.removeEventListener("scroll", handleScroll);
      }
    };
  }, [handleResize]);

  // Update canvas size when scale changes
  useEffect(() => {
    const canvas = canvasRef.current;
    if (canvas && scale && currentPageSize) {
      // Set canvas ACTUAL size (what gets drawn on) - keep at original size
      canvas.width = currentPageSize.width;
      canvas.height = currentPageSize.height;

      // Set canvas DISPLAY size (how it appears on screen) - scale this
      canvas.style.width = `${currentPageSize.width * scale}px`;
      canvas.style.height = `${currentPageSize.height * scale}px`;

      const ctx = canvas.getContext("2d");
      contextRef.current = ctx;

      // console.log('Canvas size updated - Internal:', canvas.width, 'x', canvas.height, 'Display:', canvas.style.width, 'x', canvas.style.height);
    }
  }, [scale, currentPageSize]);

  // Early return check - AFTER all hooks
  if (!scale || !canvasDimensions) {
    return <div>Loading canvas dimensions...</div>;
  }

  const { scaledWidth, scaledHeight } = canvasDimensions;
  // console.log("Rendering with scale:", scale, { scaledWidth, scaledHeight });

  return (
    <IOSErrorBoundary>
      <>
        {/* Background layer - covers whole screen */}
        <div
          style={{
            position: "fixed",
            top: "0",
            left: "0",
            width: "100vw",
            height: "100vh",
            backgroundColor: pageBackground.color || "#000000ff",
            zIndex: -10,
          }}
        />
        {/* Scrollable viewport */}
        <div
          ref={scrollCanvasRef}
          style={{
            width: "100vw",
            height: "100vh",
            overflow: "auto",
            display: "flex",
            alignItems: "flex-start",
            justifyContent: "center",
            overflowX: "hidden",
            backgroundColor: "transparent",
            
          }}
          onClick={handleScrollContainerClick}
        >
          <div
            style={{
              width: `${scaledWidth}px`,
              height: `${currentScrollHeight}px`,
              position: "relative",
              backgroundColor: "transparent",
              // border: "2px solid #ccc",
              zIndex:100
            }}
          ></div>
        </div>
        {/* Lottie Animation Overlay */}
        {showLottie && (
          <div
            style={{
              position: "fixed",
              top: "0",
              left: "0",
              width: "100vw",
              height: "100vh",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              // zIndex: 9999, // Increased z-index to ensure it's on top
              pointerEvents: "auto", // Changed to auto to allow interaction with overlay
              backgroundColor: "rgba(0, 0, 0, 0.3)",
            }}
            onClick={() => setShowLottie(false)} // Allow clicking overlay to dismiss
          >
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
                animation: "swipeUpPulse 3s ease-in-out infinite",
              }}
            >
              <img
                src={`${baseUrl}/global_asset/svg/swipe-up-72-svgrepo-com.svg`}
                alt="Swipe up"
                style={{
                  width: "100px",
                  height: "100px",
                  filter: "invert(1)",
                  animation: "swipeUpBounce 3s ease-in-out infinite",
                }}
              />
              <div
                style={{
                  color: "white",
                  fontSize: "40px",
                  marginTop: "16px",
                  fontFamily: "Arial, sans-serif",
                  textAlign: "center",
                }}
              >
                Swipe up to continue
              </div>
            </div>
          </div>
        )}
        {/* Fixed canvas overlay - CENTERED IN VIEWPORT */}
        <canvas
          ref={canvasRef}
          style={{
            position: "fixed",
            top: "0%",
            left: "50%",
            transform: "translate(-50%)",
            zIndex: -1,
            pointerEvents: "auto",
            cursor: "pointer",
            // border: "2px solid red",
            backgroundColor: "rgba(0, 0, 0, 0.1)", // Semi-transparent to see what's underneath
            width: `${currentPageSize.width * scale}px`,
            height: `${currentPageSize.height * scale}px`,
          }}
        />
        {/* Bottom text when there's space */}
        {window.innerHeight > currentPageSize.height * scale && (
          <div
            style={{
              position: "fixed",
              bottom: "0",
              left: "0",
              width: "100vw",
              height: `${
                window.innerHeight - currentPageSize.height * scale
              }px`,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              backgroundColor: "transparent",
              zIndex: -1,
              fontSize: `${Math.min(
                48,
                (window.innerHeight - currentPageSize.height * scale) / 3
              )}px`,
              fontWeight: "bold",
              color: "#333",
              fontFamily: "Arial, sans-serif",
            }}
          >
            {pageTitle}
          </div>
        )}
      </>
    </IOSErrorBoundary>
  );
};

export default CanvasAnimatedElements;
