import React, { useState, useCallback } from "react";
import { Play, LogOut } from "lucide-react";

const ExitAnimationPresets = ({ 
  selectedElement,
  addExitScroll,
  scaledScrollY,
  animationDurations
}) => {
  const [hoveredAnimation, setHoveredAnimation] = useState(null);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });

  // Exit animations configuration
  const EXIT_ANIMATIONS = [
    { 
      name: 'out', 
      displayName: 'Direct Out',
      description: 'the element to remove directly without any animation.',
      properties: {
        'animation-name': 'fadeOut',
        'timing-function': 'ease-out',
        'iteration-count': '1',
        'fill-mode': 'forwards'
      }
    },
  ];

  // Handle mouse events for animation hover
  const handleAnimationMouseMove = useCallback((e, key) => {
    setMousePosition({ x: e.clientX, y: e.clientY });
    setHoveredAnimation(key);
  }, []);

  const handleAnimationMouseLeave = useCallback(() => {
    setHoveredAnimation(null);
  }, []);

  return (
    <div>
      <h3
        style={{
          fontWeight: "500",
          color: "#111827",
          marginBottom: "16px",
          marginTop: 0,
        }}
      >
        Exit Animation Presets
      </h3>
      
      <div
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(180px, 1fr))",
          gap: "16px",
          marginBottom: "24px",
        }}
      >
        {EXIT_ANIMATIONS.map((animation) => {
          const currentDuration = animationDurations || 1;

          return (
            <div
              key={animation.name}
              style={{
                position: "relative",
                backgroundColor: "rgba(255, 255, 255, 0.8)",
                border: "1px solid #e5e7eb",
                borderRadius: "8px",
                padding: "16px",
                transition: "all 0.2s ease",
                textAlign: "center",
              }}
              onMouseOver={(e) => {
                e.currentTarget.style.borderColor = "#f87171";
                e.currentTarget.style.backgroundColor = "#fef2f2";
                e.currentTarget.style.transform = "translateY(-1px)";
                e.currentTarget.style.boxShadow = "0 4px 12px rgba(0, 0, 0, 0.1)";
              }}
              onMouseOut={(e) => {
                e.currentTarget.style.borderColor = "#e5e7eb";
                e.currentTarget.style.backgroundColor = "rgba(255, 255, 255, 0.8)";
                e.currentTarget.style.transform = "translateY(0)";
                e.currentTarget.style.boxShadow = "none";
              }}
            >
              {/* Animation Preview */}
              <div
                onMouseMove={(e) => handleAnimationMouseMove(e, animation.name)}
                onMouseLeave={handleAnimationMouseLeave}
                style={{
                  cursor: "pointer",
                  marginBottom: "12px",
                }}
              >
                <div
                  style={{
                    width: "40px",
                    height: "40px",
                    background: "linear-gradient(45deg, #ef4444, #dc2626)",
                    borderRadius: "6px",
                    margin: "0 auto 8px auto",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    color: "white",
                    fontSize: "16px",
                    fontWeight: "bold",
                    boxShadow: "0 2px 8px rgba(0, 0, 0, 0.2)",
                  }}
                >
                  <LogOut size={16} />
                </div>
                
                <h4 
                  style={{
                    fontSize: "12px",
                    fontWeight: "600",
                    color: "#374151",
                    marginBottom: "4px",
                    marginTop: 0,
                    textTransform: "capitalize",
                    lineHeight: "1.2",
                  }}
                >
                  {animation.displayName}
                </h4>
              </div>

              {/* Apply Button */}
              <button
                onClick={() => {
                  if (selectedElement?.index !== undefined) {
                    addExitScroll(selectedElement.index, scaledScrollY);
                  }
                }}
                disabled={!selectedElement?.object}
                style={{
                  width: "100%",
                  marginTop: "8px",
                  padding: "6px 8px",
                  backgroundColor: selectedElement?.object ? "#fef2f2" : "#f3f4f6",
                  border: selectedElement?.object ? "1px solid #f87171" : "1px solid #d1d5db",
                  borderRadius: "4px",
                  fontSize: "11px",
                  fontWeight: "500",
                  color: selectedElement?.object ? "#dc2626" : "#6b7280",
                  cursor: selectedElement?.object ? "pointer" : "not-allowed",
                  transition: "all 0.2s",
                  opacity: selectedElement?.object ? 1 : 0.6,
                }}
                onMouseOver={(e) => {
                  if (selectedElement?.object) {
                    e.target.style.backgroundColor = "#fee2e2";
                    e.target.style.borderColor = "#ef4444";
                  }
                }}
                onMouseOut={(e) => {
                  if (selectedElement?.object) {
                    e.target.style.backgroundColor = "#fef2f2";
                    e.target.style.borderColor = "#f87171";
                  }
                }}
              >
                Apply Exit Animation
              </button>
            </div>
          );
        })}
      </div>

      {/* Info Messages */}
      {selectedElement?.object && (
        <div
          style={{
            marginTop: "16px",
            padding: "12px",
            backgroundColor: "#fef2f2",
            borderRadius: "6px",
            border: "1px solid #f87171",
          }}
        >
          <p
            style={{
              fontSize: "12px",
              color: "#7f1d1d",
              marginBottom: "6px",
              fontWeight: "500",
              margin: "0 0 6px 0",
            }}
          >
            Exit Animation Tip
          </p>
          <p
            style={{
              fontSize: "11px",
              color: "#991b1b",
              margin: 0,
              lineHeight: "1.4",
            }}
          >
            Click any exit animation preset to apply it to the selected element. The exit animation will play when the element is removed or hidden at scroll position {scaledScrollY}px.
          </p>
        </div>
      )}

      {!selectedElement?.object && (
        <div
          style={{
            marginTop: "16px",
            padding: "12px",
            backgroundColor: "#fef3c7",
            borderRadius: "6px",
            border: "1px solid #f59e0b",
          }}
        >
          <p
            style={{
              fontSize: "12px",
              color: "#92400e",
              marginBottom: "6px",
              fontWeight: "500",
              margin: "0 0 6px 0",
            }}
          >
            Select an Element
          </p>
          <p
            style={{
              fontSize: "11px",
              color: "#b45309",
              margin: 0,
              lineHeight: "1.4",
            }}
          >
            Select an element from the canvas first, then choose an exit animation preset to apply to it.
          </p>
        </div>
      )}

      {/* Tooltip */}
      {hoveredAnimation && EXIT_ANIMATIONS.find(anim => anim.name === hoveredAnimation) && (
        <div
          style={{
            position: "fixed",
            left: mousePosition.x + 10,
            top: mousePosition.y - 10,
            backgroundColor: "rgba(0, 0, 0, 0.9)",
            color: "white",
            padding: "8px 12px",
            borderRadius: "6px",
            fontSize: "12px",
            boxShadow: "0 4px 15px rgba(0, 0, 0, 0.3)",
            backdropFilter: "blur(5px)",
            border: "1px solid rgba(255, 255, 255, 0.2)",
            pointerEvents: "none",
            zIndex: 50,
            opacity: 0.95,
            whiteSpace: "pre-line",
            maxWidth: "200px",
          }}
        >
          <div style={{fontWeight: "600", marginBottom: "4px"}}>
            {EXIT_ANIMATIONS.find(anim => anim.name === hoveredAnimation)?.displayName}
          </div>
          <div style={{fontSize: "11px", color: "#d1d5db"}}>
            {EXIT_ANIMATIONS.find(anim => anim.name === hoveredAnimation)?.description}
          </div>
        </div>
      )}
    </div>
  );
};

export default ExitAnimationPresets;