import { PresetAnimationManager } from "./preset_animation.js";

export default class BaseElement {
  constructor(metadata, scale = 1, canvas_width = 100, canvas_height = 100) {
    this.scale = scale;
    this.metadata = metadata;
    this.canvas_height = canvas_height;
    this.canvas_width = canvas_width;
    this.loaded = false;
    this.loadError = false;

    // Initialize with default values for immediate visibility
    const anim = this.metadata.animation?.keyframes;
    this.currentProps = {
      x: anim?.x?.value?.[0] || 100,
      y: anim?.y?.value?.[0] || 100,
      width: anim?.width?.value?.[0] || 200,
      height: anim?.height?.value?.[0] || 200,
      rotation: anim?.rotation?.value?.[0] || 0,
      opacity: 1, // Start with full opacity for visibility
    };

    // Initialize preset animation manager
    this.presetAnimation = new PresetAnimationManager(metadata);
  }

  /**
   * Interpolates a value based on scroll position and keyframe data
   * @param {number} scrollY - Current scroll position
   * @param {number[]} inputRange - Array of scroll positions
   * @param {any[]} outputRange - Array of corresponding values
   * @param {any} defaultValue - Fallback value
   * @returns {any} Interpolated value
   */
  interpolateValue(scrollY, inputRange, outputRange, defaultValue) {
    // Early validation
    if (
      !inputRange?.length ||
      !outputRange?.length ||
      inputRange.length !== outputRange.length ||
      inputRange.length < 2
    ) {
      return defaultValue;
    }

    // Handle edge cases
    if (scrollY <= inputRange[0]) return outputRange[0];
    if (scrollY >= inputRange[inputRange.length - 1])
      return outputRange[outputRange.length - 1];

    // Binary search for the correct segment (O(log n) vs O(n))
    let left = 0;
    let right = inputRange.length - 1;

    while (left < right - 1) {
      const mid = Math.floor((left + right) / 2);
      if (scrollY < inputRange[mid]) {
        right = mid;
      } else {
        left = mid;
      }
    }

    const segmentIndex = left;
    const inputMin = inputRange[segmentIndex];
    const inputMax = inputRange[segmentIndex + 1];
    const outputMin = outputRange[segmentIndex];
    const outputMax = outputRange[segmentIndex + 1];

    // Calculate interpolation progress
    const progress = (scrollY - inputMin) / (inputMax - inputMin);

    // Type-specific interpolation
    if (typeof outputMin === "number") {
      return outputMin + (outputMax - outputMin) * progress;
    }

    if (typeof outputMin === "string" && outputMin.includes("px")) {
      const startNum = parseFloat(outputMin);
      const endNum = parseFloat(outputMax);
      return `${startNum + (endNum - startNum) * progress}px`;
    }

    // Fallback for non-interpolatable values
    return (progress < 0.5 ? outputMin : outputMax) * this.scale;
  }

  /**
   * Updates animation properties based on scroll progress
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   * @param {number} scaledScrollY - Current scroll position
   */
  update(ctx, scaledScrollY) {
    const keyframes = this.metadata.animation?.keyframes;
    if (!keyframes) {
      // Even without keyframes, we should draw the element
      this.draw(ctx);
      return;
    }
    if (keyframes?.y?.scrollY[0]>scaledScrollY){
      return
    }
    // Update properties based on scroll position
    if (!this.presetAnimation.affectsProperty("x")) {
      this.currentProps.x = this.interpolateValue(
        scaledScrollY,
        keyframes?.x?.scrollY ?? [0, 0],
        keyframes?.x?.value ?? [this.currentProps.x],
        this.currentProps.x
      );
    }

    if (!this.presetAnimation.affectsProperty("y")) {
      this.currentProps.y =
        this.interpolateValue(
          scaledScrollY,
          keyframes?.y?.scrollY ?? [0, 0],
          keyframes?.y?.value ?? [this.currentProps.y],
          this.currentProps.y
        ) - scaledScrollY; // Subtract scroll for relative positioning
    }

    if (!this.presetAnimation.affectsProperty("width")) {
      this.currentProps.width = this.interpolateValue(
        scaledScrollY,
        keyframes?.width?.scrollY ?? [0, 1],
        keyframes?.width?.value ?? [this.currentProps.width],
        this.currentProps.width
      );
    }

    if (!this.presetAnimation.affectsProperty("height")) {
      this.currentProps.height = this.interpolateValue(
        scaledScrollY,
        keyframes?.height?.scrollY ?? [0, 1],
        keyframes?.height?.value ?? [this.currentProps.height],
        this.currentProps.height
      );
    }

    if (!this.presetAnimation.affectsProperty("rotation")) {
      this.currentProps.rotation = this.interpolateValue(
        scaledScrollY,
        keyframes?.rotation?.scrollY ?? [0, 1],
        keyframes?.rotation?.value ?? [this.currentProps.rotation],
        this.currentProps.rotation
      );
    }

    if (!this.presetAnimation.affectsProperty("opacity")) {
      this.currentProps.opacity = this.interpolateValue(
        scaledScrollY,
        keyframes?.opacity?.scrollY ?? [0, 1],
        keyframes?.opacity?.value ?? [this.currentProps.opacity],
        this.currentProps.opacity
      );
    }

    // Apply preset animations after keyframe animations
    this.currentProps = this.presetAnimation.apply(this.currentProps);

    // Always call draw after updating properties
    this.draw(ctx);
  }

  /**
   * Tests if a point is within the element's bounds
   * @param {number} x - Click x coordinate
   * @param {number} y - Click y coordinate
   * @param {number} scrollY - Current scroll position
   * @returns {boolean} - True if point is within element bounds
   */
  hitTest(x, y, scrollY = 0) {
    // console.log("INN",x, y, scrollY)
    const canvas_scaled_x = x;
    const canvas_scaled_y = y;
    // console.log(this.metadata)
    x = x / this.scale;
    y = y / this.scale;

    // Get the element's current position at this scroll level
    const tempProps = { ...this.currentProps };
//
    // Recalculate position based on current scroll (same logic as update method)
    const keyframes = this.metadata.animation?.keyframes;
    if (keyframes && !this.presetAnimation.affectsProperty("y")) {
      tempProps.y =
        this.interpolateValue(
          scrollY,
          keyframes?.y?.scrollY ?? [0, 0],
          keyframes?.y?.value ?? [this.currentProps.y],
          this.currentProps.y
        ) - scrollY; // Subtract scroll for relative positioning
    }

    const { x: elemX, y: elemY, width, height, opacity } = tempProps;

    const scaled_width = width * this.scale;
    const scaled_height = height * this.scale;

    const isInViewport =
      scrollY >= keyframes?.y?.scrollY[0] &&
      canvas_scaled_x + scaled_width >= 0 &&
      canvas_scaled_x <= this.canvas_width &&
      canvas_scaled_y + scaled_height >= 0 &&
      canvas_scaled_y <= this.canvas_height;
//     console.table([
//   {
//     Condition: "scrollY >= keyframes?.y?.scrollY[0]",
//     Left: scrollY,
//     Right: keyframes?.y?.scrollY?.[0],
//     Result: scrollY >= keyframes?.y?.scrollY?.[0]
//   },
//   {
//     Condition: "canvas_scaled_x + scaled_width >= 0",
//     Left: canvas_scaled_x + scaled_width,
//     Right: 0,
//     Result: canvas_scaled_x + scaled_width >= 0
//   },
//   {
//     Condition: "canvas_scaled_x <= this.canvas_width",
//     Left: canvas_scaled_x,
//     Right: this.canvas_width,
//     Result: canvas_scaled_x <= this.canvas_width
//   },
//   {
//     Condition: "canvas_scaled_y + scaled_height >= 0",
//     Left: canvas_scaled_y + scaled_height,
//     Right: 0,
//     Result: canvas_scaled_y + scaled_height >= 0
//   },
//   {
//     Condition: "canvas_scaled_y <= this.canvas_height",
//     Left: canvas_scaled_y,
//     Right: this.canvas_height,
//     Result: canvas_scaled_y <= this.canvas_height
//   },
//   {
//     Condition: "FINAL isInViewport",
//     Left: "All above",
//     Right: "true",
//     Result: isInViewport
//   }
// ]);
    // Element must be in viewport to be clickable
    if (!isInViewport) {
      return false;
    }

    const isInBounds =
      x >= elemX &&
      y >= elemY &&
      x <= elemX + width &&
      y <= elemY + height &&
      opacity > 0;
    // console.log(isInViewport,isInBounds)
    return isInBounds && isInViewport;
  }

  /**
   * Abstract method to be implemented by subclasses
   * Renders the element to the canvas
   * @param {CanvasRenderingContext2D} ctx - Canvas 2D context
   */
  draw(ctx) {
    throw new Error("draw() method must be implemented by subclass");
  }
}
