import React, { useState } from 'react';

const ShapeGenerator = ({
  selectedShape,
  color = '#3b82f6',
  gradientEnabled = false,
  gradientColor2 = '#ef4444',
  gradientDirection = '45deg',
  shadow = false,
  shadowColor = 'rgba(0, 0, 0, 0.3)',
  shadowBlur = 10,
  shadowOffsetX = 5,
  shadowOffsetY = 5
}) => {

  const width = '100%'
  const height = '100%'
  // // console.log("selectedShape", selectedShape)
  
  const shapes = {
    circle: {
      name: 'Circle',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${w}px`, // Keep square for perfect circle
        borderRadius: '50%',
        background: c
      })
    },
    square: {
      name: 'Square',
      style: (w, h, c) => ({
        width: `${Math.min(w, h)}px`,
        height: `${Math.min(w, h)}px`,
        background: c
      })
    },
    rectangle: {
      name: 'Rectangle',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c
      })
    },
    triangle: {
      name: 'Triangle',
      style: (w, h, c) => ({
        width: 0,
        height: 0,
        borderLeft: `${w/2}px solid transparent`,
        borderRight: `${w/2}px solid transparent`,
        borderBottom: `${h}px solid ${gradientEnabled ? '#3b82f6' : color}`,
        background: 'transparent'
      })
    },
    diamond: {
      name: 'Diamond',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        transform: 'rotate(45deg)'
      })
    },
    hexagon: {
      name: 'Hexagon',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        clipPath: 'polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%)'
      })
    },
    pentagon: {
      name: 'Pentagon',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        clipPath: 'polygon(50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%)'
      })
    },
    octagon: {
      name: 'Octagon',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        clipPath: 'polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%)'
      })
    },
    star: {
      name: 'Star',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        clipPath: 'polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%)'
      })
    },
    heart: {
      name: 'Heart',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        clipPath: 'path("M12,21.35l-1.45-1.32C5.4,15.36,2,12.28,2,8.5 C2,5.42,4.42,3,7.5,3c1.74,0,3.41,0.81,4.5,2.09C13.09,3.81,14.76,3,16.5,3 C19.58,3,22,5.42,22,8.5c0,3.78-3.4,6.86-8.55,11.54L12,21.35z")',
        transform: 'scale(0.8)'
      })
    },
    arrow: {
      name: 'Arrow',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        clipPath: 'polygon(0% 20%, 60% 20%, 60% 0%, 100% 50%, 60% 100%, 60% 80%, 0% 80%)'
      })
    },
    trapezoid: {
      name: 'Trapezoid',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        clipPath: 'polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%)'
      })
    },
    parallelogram: {
      name: 'Parallelogram',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        transform: 'skew(-20deg)'
      })
    },
    ellipse: {
      name: 'Ellipse',
      style: (w, h, c) => ({
        width: `${w}px`,
        height: `${h}px`,
        background: c,
        borderRadius: '50%'
      })
    }
  };

  const getBackgroundColor = () => {
    if (gradientEnabled) {
      return `linear-gradient(${gradientDirection}, ${color}, ${gradientColor2})`;
    }
    return color;
  };

  const getShadowStyle = () => {
    if (!shadow) return {};
    
    // For shapes with clipPath, use filter drop-shadow
    const hasClipPath = ['hexagon', 'pentagon', 'octagon', 'star', 'heart', 'arrow', 'trapezoid'].includes(selectedShape);
    
    if (hasClipPath) {
      return {
        filter: `drop-shadow(${shadowOffsetX}px ${shadowOffsetY}px ${shadowBlur}px ${shadowColor})`
      };
    }
    
    // For regular shapes, use box-shadow
    return {
      boxShadow: `${shadowOffsetX}px ${shadowOffsetY}px ${shadowBlur}px ${shadowColor}`
    };
  };

  const getShapeStyle = () => {
    const baseStyle = shapes[selectedShape].style(width, height, getBackgroundColor());
    const shadowStyle = getShadowStyle();
    
    // Special handling for triangle since it uses borders
    if (selectedShape === 'triangle' && gradientEnabled) {
      return {
        ...baseStyle,
        ...shadowStyle,
        borderBottomColor: color,
        background: `linear-gradient(${gradientDirection}, ${color}, ${gradientColor2})`,
        borderBottom: `${height}px solid transparent`,
        backgroundClip: 'padding-box'
      };
    }
    
    return {
      ...baseStyle,
      ...shadowStyle
    };
  };

  return (
    <div
      style={{
        ...getShapeStyle(),
        width: width,
        height: height
      }}
      className="transition-all duration-300 ease-in-out"
    />
  );
};

export default ShapeGenerator;