import React from "react";
import { useState } from "react";
const TextTypography = ({ style, elementIndex, updateElementProperty }) => {
  // Popular font options with categories
  const fontOptions = [
    // Web Safe Fonts
    { value: "Arial, sans-serif", label: "Arial", category: "Sans-serif" },
    { value: "Helvetica, Arial, sans-serif", label: "Helvetica", category: "Sans-serif" },
    { value: "Times New Roman, Times, serif", label: "Times New Roman", category: "Serif" },
    { value: "Georgia, Times, serif", label: "Georgia", category: "Serif" },
    { value: "Courier New, Courier, monospace", label: "Courier New", category: "Monospace" },
    { value: "Verdana, Arial, sans-serif", label: "Verdana", category: "Sans-serif" },
    
    // Google Fonts - Popular choices
    { value: '"Roboto", sans-serif', label: "Roboto", category: "Google Fonts" },
    { value: '"Open Sans", sans-serif', label: "Open Sans", category: "Google Fonts" },
    { value: '"Lato", sans-serif', label: "Lato", category: "Google Fonts" },
    { value: '"Montserrat", sans-serif', label: "Montserrat", category: "Google Fonts" },
    { value: '"Poppins", sans-serif', label: "Poppins", category: "Google Fonts" },
    { value: '"Inter", sans-serif', label: "Inter", category: "Google Fonts" },
    { value: '"Playfair Display", serif', label: "Playfair Display", category: "Google Fonts" },
    { value: '"Merriweather", serif', label: "Merriweather", category: "Google Fonts" },
    { value: '"Source Code Pro", monospace', label: "Source Code Pro", category: "Google Fonts" },
    { value: '"Dancing Script", cursive', label: "Dancing Script", category: "Google Fonts" },
    { value: '"Pacifico", cursive', label: "Pacifico", category: "Google Fonts" },
    { value: '"Raleway", sans-serif', label: "Raleway", category: "Google Fonts" },
    { value: '"Nunito", sans-serif', label: "Nunito", category: "Google Fonts" },
    { value: '"Oswald", sans-serif', label: "Oswald", category: "Google Fonts" },
    { value: '"Ubuntu", sans-serif', label: "Ubuntu", category: "Google Fonts" },
    { value: '"Crimson Text", serif', label: "Crimson Text", category: "Google Fonts" },
    { value: '"Fira Sans", sans-serif', label: "Fira Sans", category: "Google Fonts" },
    { value: '"Work Sans", sans-serif', label: "Work Sans", category: "Google Fonts" },
    { value: '"Quicksand", sans-serif', label: "Quicksand", category: "Google Fonts" },
    { value: '"Ranchers", sans-serif', label: "Ranchers", category: "Google Fonts" },
    
    // Decorative/Display fonts
    { value: '"Lobster", cursive', label: "Lobster", category: "Decorative" },
    { value: '"Righteous", cursive', label: "Righteous", category: "Decorative" },
    { value: '"Bangers", cursive', label: "Bangers", category: "Decorative" },
    { value: '"Creepster", cursive', label: "Creepster", category: "Decorative" },
    { value: '"Kalam", cursive', label: "Kalam", category: "Decorative" },
  ];

  // Group fonts by category
  const groupedFonts = fontOptions.reduce((acc, font) => {
    if (!acc[font.category]) {
      acc[font.category] = [];
    }
    acc[font.category].push(font);
    return acc;
  }, {});

  const currentFont = style.fontFamily || "Arial, sans-serif";
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);

  // Clean current font value to match dropdown options
  const cleanCurrentFont = (font) => {
    // Remove extra quotes and semicolons that might be in the stored value
    const cleaned = font.replace(/[;"]/g, '').trim();
    
    // Try to find exact match first
    const exactMatch = fontOptions.find(option => option.value === cleaned);
    if (exactMatch) return exactMatch.value;
    
    // Try to find partial match
    const partialMatch = fontOptions.find(option => 
      cleaned.includes(option.label) || option.value.includes(cleaned)
    );
    if (partialMatch) return partialMatch.value;
    
    return cleaned;
  };
  // Get the current selected font for display
  const selectedFontValue = cleanCurrentFont(currentFont);
  const selectedFont = fontOptions.find(font => font.value === selectedFontValue);

  const handleFontChange = (e) => {
    const selectedFont = e.target.value;
    updateElementProperty(
      elementIndex,
      "object_property.style.fontFamily",
      selectedFont
    );
    setIsDropdownOpen(false);
  };

  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
  };

  return (
    <div>
      <h4
        style={{
          fontSize: "14px",
          fontWeight: "600",
          color: "#374151",
          marginBottom: "12px",
          marginTop: 0,
        }}
      >
        Typography
      </h4>

      {/* Font Family */}
      <div style={{ marginBottom: "12px" }}>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Font Family
        </label>
        
        {/* Custom Styled Dropdown */}
        <div style={{ position: "relative" }}>
          {/* Dropdown Button */}
          <button
            type="button"
            onClick={toggleDropdown}
            style={{
              width: "100%",
              padding: "8px 12px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              fontSize: "14px",
              backgroundColor: "white",
              textAlign: "left",
              cursor: "pointer",
              fontFamily: selectedFontValue,
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center"
            }}
          >
            <span>{selectedFont ? selectedFont.label : "Select Font"}</span>
            <span style={{ transform: isDropdownOpen ? "rotate(180deg)" : "rotate(0deg)", transition: "transform 0.2s" }}>
              
            </span>
          </button>

          {/* Dropdown Options */}
          {isDropdownOpen && (
            <div style={{
              position: "absolute",
              top: "100%",
              left: 0,
              right: 0,
              backgroundColor: "white",
              border: "1px solid #d1d5db",
              borderTop: "none",
              borderRadius: "0 0 4px 4px",
              maxHeight: "300px",
              overflowY: "auto",
              zIndex: 1000,
              boxShadow: "0 4px 6px -1px rgba(0, 0, 0, 0.1)"
            }}>
              {Object.entries(groupedFonts).map(([category, fonts]) => (
                <div key={category}>
                  {/* Category Header */}
                  <div style={{
                    padding: "6px 12px",
                    backgroundColor: "#f3f4f6",
                    fontSize: "12px",
                    fontWeight: "600",
                    color: "#6b7280",
                    borderBottom: "1px solid #e5e7eb"
                  }}>
                    {category}
                  </div>
                  
                  {/* Font Options */}
                  {fonts.map((font) => (
                    <div
                      key={font.value}
                      onClick={() => handleFontChange({ target: { value: font.value } })}
                      style={{
                        padding: "8px 12px",
                        cursor: "pointer",
                        fontSize: "14px",
                        fontFamily: font.value,
                        backgroundColor: font.value === selectedFontValue ? "#eff6ff" : "transparent",
                        borderLeft: font.value === selectedFontValue ? "3px solid #3b82f6" : "3px solid transparent",
                        ":hover": {
                          backgroundColor: "#f9fafb"
                        }
                      }}
                      onMouseEnter={(e) => e.target.style.backgroundColor = "#f9fafb"}
                      onMouseLeave={(e) => e.target.style.backgroundColor = font.value === selectedFontValue ? "#eff6ff" : "transparent"}
                    >
                      {font.label} {font.value === selectedFontValue && ""}
                    </div>
                  ))}
                </div>
              ))}
            </div>
          )}
        </div>
        


        {/* Custom Font Input */}
        <details style={{ marginTop: "8px" }}>
          <summary style={{ 
            fontSize: "12px", 
            color: "#6b7280", 
            cursor: "pointer",
            userSelect: "none"
          }}>
            Use Custom Font
          </summary>
          <input
            type="text"
            value={currentFont}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.style.fontFamily",
                e.target.value
              )
            }
            style={{
              width: "100%",
              padding: "6px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              fontSize: "12px",
              marginTop: "6px",
              fontFamily: "monospace",
            }}
            placeholder="e.g., Custom Font, fallback"
          />
          <div style={{
            fontSize: "11px",
            color: "#9ca3af",
            marginTop: "4px"
          }}>
            Enter the exact font family name with fallbacks
          </div>
        </details>
        
        {/* Click outside to close dropdown */}
        {isDropdownOpen && (
          <div style={{ position: "fixed", top: 0, left: 0, right: 0, bottom: 0, zIndex: 999 }}
               onClick={() => setIsDropdownOpen(false)} />
        )}
      </div>

      {/* Font Size and Weight */}
      <div
        style={{
          display: "grid",
          gridTemplateColumns: "1fr 1fr",
          gap: "12px",
          marginBottom: "12px",
        }}
      >
        <div>
          <label
            style={{
              display: "block",
              fontSize: "14px",
              fontWeight: "500",
              marginBottom: "8px",
            }}
          >
            Font Size (px)
          </label>
          <input
            type="number"
            value={
              style.fontSize
                ? parseFloat(style.fontSize.toString().replace("px", ""))
                : 40
            }
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.style.fontSize",
                e.target.value
              )
            }
            style={{
              width: "100%",
              padding: "8px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              fontSize: "14px",
            }}
            placeholder="24"
            min="8"
            max="200"
          />
        </div>
        <div>
          <label
            style={{
              display: "block",
              fontSize: "14px",
              fontWeight: "500",
              marginBottom: "8px",
            }}
          >
            Font Weight
          </label>
          <select
            value={style.fontWeight || "normal"}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.style.fontWeight",
                e.target.value
              )
            }
            style={{
              width: "100%",
              padding: "8px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              fontSize: "14px",
            }}
          >
            <option value="100">100 - Thin</option>
            <option value="300">300 - Light</option>
            <option value="normal">400 - Normal</option>
            <option value="500">500 - Medium</option>
            <option value="600">600 - Semi Bold</option>
            <option value="bold">700 - Bold</option>
            <option value="800">800 - Extra Bold</option>
            <option value="900">900 - Black</option>
          </select>
        </div>
      </div>

      {/* Font Style */}
      <div style={{ marginBottom: "12px" }}>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Font Style
        </label>
        <select
          value={style.fontStyle || "normal"}
          onChange={(e) =>
            updateElementProperty(
              elementIndex,
              "object_property.style.fontStyle",
              e.target.value
            )
          }
          style={{
            width: "100%",
            padding: "8px",
            border: "1px solid #d1d5db",
            borderRadius: "4px",
            fontSize: "14px",
          }}
        >
          <option value="normal">Normal</option>
          <option value="italic">Italic</option>
          <option value="oblique">Oblique</option>
        </select>
      </div>

      {/* Text Color */}
      <div style={{ marginBottom: "12px" }}>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Text Color
        </label>
        <div style={{ display: "flex", gap: "8px" }}>
          <input
            type="color"
            value={style.color || "#000000"}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.style.color",
                e.target.value
              )
            }
            style={{
              width: "48px",
              height: "40px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              cursor: "pointer",
            }}
          />
          <input
            type="text"
            value={style.color || "#000000"}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.style.color",
                e.target.value
              )
            }
            style={{
              flex: "1",
              padding: "8px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              fontSize: "14px",
            }}
            placeholder="#000000"
          />
        </div>
      </div>

      {/* Background Color */}
      <div style={{ marginBottom: "12px" }}>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Background Color
        </label>
        <div style={{ display: "flex", gap: "8px" }}>
          <input
            type="color"
            value={
              style.backgroundColor === "transparent" 
                ? "#ffffff" 
                : (style.backgroundColor || "#ffffff")
            }
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.style.backgroundColor",
                e.target.value
              )
            }
            style={{
              width: "48px",
              height: "40px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              cursor: "pointer",
            }}
          />
          <input
            type="text"
            value={style.backgroundColor || "transparent"}
            onChange={(e) =>
              updateElementProperty(
                elementIndex,
                "object_property.style.backgroundColor",
                e.target.value
              )
            }
            style={{
              flex: "1",
              padding: "8px",
              border: "1px solid #d1d5db",
              borderRadius: "4px",
              fontSize: "14px",
            }}
            placeholder="transparent"
          />
        </div>
      </div>

      {/* Text Decoration */}
      <div style={{ marginBottom: "12px" }}>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Text Decoration
        </label>
        <select
          value={style.textDecoration || "none"}
          onChange={(e) =>
            updateElementProperty(
              elementIndex,
              "object_property.style.textDecoration",
              e.target.value
            )
          }
          style={{
            width: "100%",
            padding: "8px",
            border: "1px solid #d1d5db",
            borderRadius: "4px",
            fontSize: "14px",
          }}
        >
          <option value="none">None</option>
          <option value="underline">Underline</option>
          <option value="overline">Overline</option>
          <option value="line-through">Line Through</option>
        </select>
      </div>

      {/* Text Transform */}
      <div style={{ marginBottom: "12px" }}>
        <label
          style={{
            display: "block",
            fontSize: "14px",
            fontWeight: "500",
            marginBottom: "8px",
          }}
        >
          Text Transform
        </label>
        <select
          value={style.textTransform || "none"}
          onChange={(e) =>
            updateElementProperty(
              elementIndex,
              "object_property.style.textTransform",
              e.target.value
            )
          }
          style={{
            width: "100%",
            padding: "8px",
            border: "1px solid #d1d5db",
            borderRadius: "4px",
            fontSize: "14px",
          }}
        >
          <option value="none">None</option>
          <option value="capitalize">Capitalize</option>
          <option value="uppercase">Uppercase</option>
          <option value="lowercase">Lowercase</option>
        </select>
      </div>
    </div>
  );
};

export default TextTypography;