import React, { useState } from 'react';

// Mock animation presets for demonstration
import ANIMATION_PRESET from '../../animations/preset.js'; // Import animations if needed
function AnimationDashboard() {
  const [hoveredCard, setHoveredCard] = useState(null);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });

  const getAnimationStyles = (animation) => {
    return {
      animationName: animation.name,
      animationTimingFunction: animation["timing-function"],
      animationIterationCount: animation["iteration-count"],
      animationDuration: animation.duration || '1s',
      animationFillMode: animation["fill-mode"] || 'none'
    };
  };

  const handleMouseMove = (e, key) => {
    setMousePosition({ x: e.clientX, y: e.clientY });
    setHoveredCard(key);
  };

  const handleMouseLeave = () => {
    setHoveredCard(null);
  };

  const formatAnimationValue = (animation) => {
    return `Duration: ${animation.duration || '1s'}
Timing: ${animation['timing-function']}
Iterations: ${animation['iteration-count']}
Fill Mode: ${animation['fill-mode'] || 'none'}`;
  };

  const containerStyle = {
    minHeight: '100vh',
    background: 'linear-gradient(135deg, #1e293b 0%, #7c3aed 50%, #1e293b 100%)',
    padding: '2rem'
  };

  const headerStyle = {
    fontSize: '2.5rem',
    fontWeight: 'bold',
    color: 'white',
    textAlign: 'center',
    marginBottom: '3rem',
    margin: '0 auto',
    maxWidth: '1200px'
  };

  const gridStyle = {
    display: 'grid',
    gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
    gap: '2rem',
    maxWidth: '1200px',
    margin: '0 auto'
  };

  const cardStyle = {
    position: 'relative',
    backgroundColor: 'rgba(255, 255, 255, 0.1)',
    backdropFilter: 'blur(10px)',
    borderRadius: '12px',
    padding: '1.5rem',
    border: '1px solid rgba(255, 255, 255, 0.2)',
    cursor: 'pointer',
    transition: 'all 0.3s ease',
    textAlign: 'center'
  };

  const cardHoverStyle = {
    ...cardStyle,
    borderColor: 'rgba(255, 255, 255, 0.4)',
    transform: 'translateY(-2px)',
    boxShadow: '0 10px 25px rgba(0, 0, 0, 0.3)'
  };

  const animationBoxStyle = {
    width: '80px',
    height: '80px',
    background: 'linear-gradient(45deg, #60a5fa, #a855f7)',
    borderRadius: '8px',
    margin: '0 auto 1rem auto',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    color: 'white',
    fontSize: '1.5rem',
    fontWeight: 'bold',
    boxShadow: '0 4px 15px rgba(0, 0, 0, 0.3)'
  };

  const titleStyle = {
    fontSize: '1.25rem',
    fontWeight: '600',
    color: 'white',
    marginBottom: '0.5rem',
    textTransform: 'capitalize'
  };

  const detailsStyle = {
    fontSize: '0.875rem',
    color: '#d1d5db',
    lineHeight: '1.5'
  };

  const overlayStyle = {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.6)',
    backdropFilter: 'blur(5px)',
    borderRadius: '12px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    opacity: 0,
    transition: 'opacity 0.3s ease',
    color: 'white',
    fontSize: '0.875rem',
    textAlign: 'center',
    whiteSpace: 'pre-line',
    padding: '1rem'
  };

  const overlayVisibleStyle = {
    ...overlayStyle,
    opacity: 1
  };

  const tooltipStyle = {
    position: 'fixed',
    left: mousePosition.x + 10,
    top: mousePosition.y - 10,
    backgroundColor: 'rgba(0, 0, 0, 0.8)',
    color: 'white',
    padding: '0.5rem 0.75rem',
    borderRadius: '6px',
    fontSize: '0.875rem',
    boxShadow: '0 4px 15px rgba(0, 0, 0, 0.3)',
    backdropFilter: 'blur(5px)',
    border: '1px solid rgba(255, 255, 255, 0.2)',
    pointerEvents: 'none',
    zIndex: 50,
    opacity: 0.9,
    whiteSpace: 'pre-line'
  };

  return (
    <>


      <div style={containerStyle}>
        <div style={headerStyle}>
          Animation Dashboard
        </div>
        
        <div style={gridStyle}>
          {Object.keys(ANIMATION_PRESET).map((key) => {
            const animation = ANIMATION_PRESET[key];
            const animationStyles = getAnimationStyles(animation);

            return (
              <div
                key={key}
                className="card-hover"
                style={cardStyle}
                onMouseMove={(e) => handleMouseMove(e, key)}
                onMouseLeave={handleMouseLeave}
              >
                <div
                  style={{...animationBoxStyle, ...animationStyles}}
                >
                  A
                </div>
                
                <h3 style={titleStyle}>
                  {animation.name}
                </h3>
                
                <div style={detailsStyle}>
                  <div>Duration: {animation.duration || '1s'}</div>
                  <div>Timing: {animation['timing-function']}</div>
                </div>
                
                <div
                  className="overlay-hover"
                  style={overlayStyle}
                >
                  {formatAnimationValue(animation)}
                </div>
              </div>
            );
          })}
        </div>

        {hoveredCard && (
          <div style={tooltipStyle}>
            <div style={{fontWeight: '600', marginBottom: '4px'}}>
              {ANIMATION_PRESET[hoveredCard].name}
            </div>
            <div style={{fontSize: '0.75rem', color: '#d1d5db'}}>
              {formatAnimationValue(ANIMATION_PRESET[hoveredCard])}
            </div>
          </div>
        )}
      </div>
    </>
  );
}

export default AnimationDashboard;